import React,{Component,Fragment } from 'react';
import { Form, Icon, Input, Button,Row,Col,Statistic } from 'antd';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import Footer from 'components/footer.js';
import { actionCreators } from './store';

const Countdown = Statistic.Countdown;

const formItemLayout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 },
};
const formTailLayout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 8, offset: 8 },
};

class Main extends Component {
  constructor(props) {
    super(props);
    this.state={
      isLoadingCode:false,
      deadline:null
    }
  }
  render() {
    const { getFieldDecorator,
      // getFieldValue 
    } = this.props.form;
    return (
      <Fragment>
          <Row style={{paddingTop:'80px'}} type="flex" justify="center" align="middle">
            <Col span={8}>
              <Link to="/login"><Icon type="rollback" />返回登陆页面</Link>
              <div className="l-box-shadow1 l-pd3 l-bg-white l-radius1 l-mt2">
                <h2 className="l-align-center">找回密码</h2>
                <Form onSubmit={this.handleSubmit}>
                  <Form.Item
                    label='当前绑定手机号'
                    {...formItemLayout}
                  >
                    {getFieldDecorator('mobile', {
                      rules: [{
                        required: true,
                        message: '请输入手机号!'
                      }]
                    })(
                      <Input
                        placeholder="手机号"
                        style={{width:'180px',marginRight:'8px'}}
                      />
                    )}
                    {
                      this.state.isLoadingCode ?
                      <span>
                        还剩
                        <Countdown
                          style={{display:'inline-block',verticalAlign:'top'}}
                          value={this.state.deadline}
                          onFinish={()=> this.setState({isLoadingCode:false})}
                          format="ss"
                        />
                        秒
                      </span>
                      :
                      <Button
                        onClick={ this.getSmsCode }
                      >获取验证码</Button>
                    }
                  </Form.Item>
                  <Form.Item
                    label='验证码'
                    {...formItemLayout}
                  >
                    {getFieldDecorator('smsCode', {
                      rules: [{
                        required: true,
                        message: '请输入验证码!'
                      }]
                    })(
                      <Input
                        placeholder="验证码"
                        style={{width:'180px'}}
                      />
                    )}
                  </Form.Item>
                  <Form.Item
                    label='新密码'
                    {...formItemLayout}
                  >
                    {getFieldDecorator('newPassword', {
                      rules: [{
                        required: true,
                        message: '请输入新密码!'
                      }]
                    })(
                      <Input
                        placeholder="新密码"
                        style={{width:'180px'}}
                      />
                    )}
                  </Form.Item>
                  <Form.Item
                    label='确认新密码'
                    {...formItemLayout}
                  >
                    {getFieldDecorator('affirmPassword', {
                      rules: [{
                        required: true,
                        message: '请确认新密码!'
                      },{
                        validator:this.compareToFirstPassword

                      }],
                    })(
                      <Input
                        placeholder="确认新密码"
                        style={{width:'180px'}}
                      />
                    )}
                  </Form.Item>
                  <Form.Item { ...formTailLayout }>
                    <Button
                      type="primary"
                      htmlType="submit"
                      style={{width:'100%'}}
                    >
                      确定
                    </Button>
                  </Form.Item>
                </Form>
              </div>
            </Col>
        </Row>
        <div style={{position:'fixed',bottom:'0',width:'100%'}}>
          <Footer />
        </div>
      </Fragment>
    );
  }
  compareToFirstPassword = (rule, value, callback) => {
    const form = this.props.form;
    if (value && value !== form.getFieldValue('newPassword')) {
      callback('两次输入的密码不一致!');
    } else {
      callback();
    }
  }
  onFinish() {
    this.setState(()=> ({
      isLoadingCode:false
    }));
  }
  getSmsCode = ()=> {
    let { getFieldValue,validateFields } = this.props.form;
    let mobile;
    let { getMessageCode } = this.props;

    validateFields(['mobile'],
      (err) => {
        if (!err) {
          mobile = getFieldValue('mobile');
          getMessageCode(mobile);
          this.setState({
            isLoadingCode:true,
            deadline:Date.now() + 1000*60
          })
        }
      },
    );
  }
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.setState(()=>({
          isLoadingCode:false
        }));
        this.props.onConfirm(values,this.props.history);
      }
    });
  }
}

const mapState = (state)=> ({
  username:state.forgetPassword.username
});
const mapDispatch = (dispatch)=> ({
  getMessageCode(mobile) {
    dispatch(actionCreators.getMessageCode(mobile));
  },
  onConfirm(values,history) {
    dispatch(actionCreators.confirm(values,history));
  }
});

export default connect(mapState,mapDispatch)(Form.create()(Main));
