import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { Table, Button, Popconfirm } from 'antd';
import { actionCreators } from '../store';

class Main extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listData: [],
      listColumns: []
    }
  }

  render() {

    

    const columns = [
      {
        title: '编号',
        dataIndex: 'index',
      },
      {
        title: '教师姓名',
        dataIndex: 'name',
      },
      {
        title: '教师手机号',
        dataIndex: 'phone',
      },
      {
        title: '账号创建时间',
        dataIndex: 'create',
      }, {
        title: '操作',
        dataIndex: 'action',
        width: 300,
        render: (text, record) => (
          <Fragment>
            <Button type="primary" className="l-mr2" onClick={() => this.editFn(record)}>编辑</Button>
            <Popconfirm
              title="确认要删除吗？"
              onConfirm={() => this.deleteFn(record)}
              okText="确认"
              cancelText="取消"
            >
              <Button type="primary" className="l-mr2">删除</Button>
            </Popconfirm>

          </Fragment>
        ),
      },
    ];
    const pagination = {
      showSizeChanger: true,
      defaultPageSize: 30,
      pageSizeOptions: ['10', '20', '30', '50', '100', '150', '200']
    }
    return (<div className="l-mt2 l-mb2">
      <Table
        rowKey={record => record.index}
        columns={columns}
        dataSource={this.props.dataSource}
        pagination={pagination}
      />
    </div>);
  }
  editFn = (record) => {
    this.props.showEditOrAddFn({
      type: 'edit',
      name: record.name,
      phone: record.phone
    })
  }
  deleteFn = (record) => {
    console.log('删除', record)
  }
  componentDidMount() {
  }
}

const mapState = (state) => ({
  dataSource: state.teacher.dataSource
})

const mapDispatch = (dispatch) => ({
  showEditOrAddFn(obj) {
    dispatch(actionCreators.showOrHideEditOrAddFn(true, obj))
  },
})

export default connect(mapState, mapDispatch)(Main);
