import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { Button, Row, Col, Radio } from 'antd';
import { actionCreators } from '../store';

const RadioGroup = Radio.Group
class Main extends Component {
  componentDidMount() {
    this.props.getBooksOfBag(this.props.qrCodeNumber);
  }

  confirmInStorage = (qrCodeNumber, willInBooksData) => {   
    this.props.confirmInStorage(qrCodeNumber, willInBooksData)
    this.props.putInStorageCancel()
  }
  render() {
    const {
      willInBooksData,
      onChangeBookState,
      qrCodeNumber,
      willInBooksDataObj
    } = this.props;
    return (
      <Fragment>
        
        <Row>
          {willInBooksDataObj.babyName && <Col className="l-bold" span={6}>
            宝宝姓名：{willInBooksDataObj.babyName}
          </Col>}
          {willInBooksDataObj.className && <Col className="l-bold" span={6} >
            班级：{willInBooksDataObj.className}
          </Col>}
        </Row>
        <Row className="l-mt2 l-mb2 l-bold">书包资产编号： {qrCodeNumber}</Row>
        {
          willInBooksData.map((item, index) => {
           
            const bookState = item.bookState > 1 ? +item.bookState : 2;
            return <Row key={item.bookBagDetailSysNo}>
              <Col span={6}>
                <h3>书本{++index}:</h3>
              </Col>
              <Col span={18}>
                <Row gutter={24}>
                  <Col span={12}>
                    <span className="l-mr3">{item.bookName}</span> ISBN:{item.iSBN}
                  </Col>
                  <Col span={12}>
                    <RadioGroup
                      onChange={(e) => onChangeBookState(item, e.target.value)}
                      value={bookState}
                    >
                      <Radio value={2}>正常</Radio>
                      <Radio value={3}>损坏</Radio>
                      <Radio value={4}>丢失</Radio>
                    </RadioGroup>
                  </Col>
                </Row>
              </Col>
            </Row>
          })
        }
        <br />
        <Row>
          <Col offset={6} span={12}>
            <Button
              type="primary"
              disabled={willInBooksData.length > 0 ? false : true}
              onClick={() => this.confirmInStorage(qrCodeNumber, willInBooksData)}
            >确定归还书包</Button>
          </Col>
        </Row>
        <br />
      </Fragment>);
  }
}

const mapState = (state) => ({
  visibleCheckModal: state.returnBook.visibleCheckModal,
  qrCodeNumber: state.returnBook.qrCodeNumber,
  willInBooksData: state.returnBook.willInBooksData,
  willInBooksDataObj: state.returnBook.willInBooksDataObj,
})

const mapDispatch = (dispatch) => ({
  putInStorageCancel() {
    dispatch(actionCreators.putInStorageCancel());
  },
  getBooksOfBag(value) {
    dispatch(actionCreators.getBooksOfBag(value));
  },
  onChangeBookState(item, bookState) {
    dispatch(actionCreators.changeBookState(item, bookState));
  },
  confirmInStorage(qrCodeNumber, willInBooksData) {
    dispatch(actionCreators.confirmInStorage(qrCodeNumber, willInBooksData));
  }
})

export default connect(mapState, mapDispatch)(Main);
