import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { Button, Modal, Divider, message, Select, Checkbox } from 'antd';
import { loStorage } from 'libs/storage.js';
import { actionCreators } from '../store';
import { actionCreators as actionCreatorsLogin } from 'pages/login/login/store';
// import BagDetail from './bagDetail';
const Option = Select.Option;

class Main extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: false,
      grades: [],
      classes: [],
      classSysNo: null,
      num: 0,
      dataSource: []
    }
  }

  handleSelectGradeChange = value => {
    const { gradeClassInfos } = this.props.gradeAndClass;
    this.setState({
      classes: gradeClassInfos[value],
      classSysNo: gradeClassInfos[value] ? gradeClassInfos[value][0].sysNo : null
    })
  };
  handleSelectClassChange = value => {
    this.setState({
      classSysNo: value
    })
  }

  render() {
    const { classes, grades, visible, num, dataSource } = this.state;
    return (<Fragment>
      <Button
        onClick={this.buttunFn}
        className="l-mr2 l-mt3" type="primary">批量更换班级</Button>
      <Modal
        visible={visible}
        width="80%"
        title='批量更换班级'
        onCancel={this.cancelFn}
        footer={[
          <Button key="back" onClick={this.cancelFn}>
            取消
            </Button>,
          <Button key="submit" type="primary" onClick={this.okFn}>
            确认更改
            </Button>,
        ]}
      >
        <div>
          <span className="l-bold">宝贝设置新班级：</span>
          年级： <Select
            className="l-mr3"
            style={{ width: 200 }}
            placeholder="请选择年级"
            allowClear
            onChange={this.handleSelectGradeChange}
          >
            {

              grades && grades.map((item) => {
                return <Option key={item.sysNo} value={item.sysNo}>{item.name}</Option>
              })
            }
          </Select>
          班级： <Select
            style={{ width: 200 }}
            placeholder="请选择班级"
            value={this.state.classSysNo}
            onChange={this.handleSelectClassChange}
            allowClear
          >
            {
              classes && classes.map((item) => {
                return <Option key={item.sysNo} value={item.sysNo}>{item.name}</Option>
              })
            }
          </Select>
        </div>
        <Divider />
        <div>已选择会员数量： {num}</div>
        <div>

          {dataSource.map((item, index) => {
            // 具体的小朋友
            return <Checkbox
              className="l-mb2"
              key={item.kiddieSysNo}
              onChange={(e) => this.checkboxFn(e, index)}
              checked={item.checked}
              lable={item.kiddieSysNo}
              value={item.kiddieName}
            >
              {item.kiddieName}
            </Checkbox>
          }
          )}
        </div>
      </Modal>
    </Fragment>);
  }

  buttunFn = async () => {
    const { selectedRows } = this.props;
    
    if (selectedRows && selectedRows.length <= 0) {
      return message.info('请先勾选')
    }
    
    // 等待对数据的处理  
    const dataSource = await this.dealWithSelectData(selectedRows)

    this.setState({
      visible: true,
      dataSource
    })
  }

  dealWithSelectData = (arr) => {
    return new Promise((resolve, reject) => {
      let num = 0;
      let brr = arr.map(item => {
        num++;
        item.checked = true;
        return item
      })
      this.setState({
        num
      })
      resolve(brr)
    })
  }

  checkboxFn = (e, index) => {
    const { checked } = e.target;
    let { dataSource, num } = this.state;
    let predataSource = JSON.parse(JSON.stringify(dataSource));

    predataSource[index]['checked'] = checked;
    num += checked ? 1 : -1;
    this.setState({
      dataSource: predataSource,
      num
    })
  }

  cancelFn = () => {
    this.setState({
      visible: false
    })
  }

  okFn = () => {
    const { dataSource, classSysNo } = this.state;
    
    if (!classSysNo) {
      return message.error('请选择班级')
    }
    let selectArr = [];
    dataSource.forEach(item => {
      
      if (item.checked) {
        selectArr.push(item.kiddieSysNo)
      }
    })
    this.props.changeClassFn(selectArr, classSysNo)
    this.setState({
      visible: false,
      classSysNo: null
    })
  }

  componentDidMount() {
    this.props.getAllClass()
    const obj = loStorage.getItem('allClass');
    if (!obj) return;
    let newGrades = obj.grades;

    const gradesList = obj.grades;

    if (gradesList.length > 0 && gradesList[gradesList.length - 1].sysNo !== '0') {
      newGrades = obj.grades.concat({
        sysNo: '0',
        name: '无'
      })
    }

    this.setState({
      grades: newGrades,
      gradeClassInfos: obj.gradeClassInfos
    })
  }
}

const mapState = (state) => ({
  selectedRowKeys: state.allMember.selectedRowKeys,
  selectedRows: state.allMember.selectedRows,
  gradeAndClass: state.login.gradeAndClass || loStorage.getItem('allClass'),
})

const mapDispatch = (dispatch) => ({
  getAllClass() {
    dispatch(actionCreatorsLogin.getClass());
  },
  changeClassFn(selectArr, classSysNo) {
    dispatch(actionCreators.changeClassFn(selectArr, classSysNo))
  }
})

export default connect(mapState, mapDispatch)(Main);
