import React,{ Component } from 'react';
import { Button } from 'antd';
import { connect } from 'react-redux';
import { actionCreators } from '../store';

class Main extends Component {
  render() {
    return(<Button className="l-mr2" type="primary" style={{marginLeft: '15px'}} 
    onClick={this.addMember}>添加普通会员</Button>
    );
  }
  addMember= () => {
    this.props.addMember(true)
  }
}

const mapState = (state)=> ({
  selectedRowKeys:state.seniorMember.selectedRowKeys
})


const mapDispatch = (dispatch) => ({
  addMember(type) {
    dispatch(actionCreators.showAddMemberDialog(type));
  }
})
export default connect(mapState,mapDispatch )(Main);