import React from 'react';
import {
  Form, Input, Button, DatePicker
} from 'antd';
import { connect } from 'react-redux';
import { loStorage } from 'libs/storage.js';
import { actionCreators } from '../store';
const { RangePicker } = DatePicker;

class MyForm extends React.Component {

  render() {
    const {
      getFieldDecorator,
    } = this.props.form;
    
    return (
      <Form className="l-mb3" layout="inline" onSubmit={this.handleSubmit}>
        <Form.Item label="生成时间">
          {getFieldDecorator('range-picker')(<RangePicker />)}
        </Form.Item>        
        <Form.Item
          label="操作人"
        >
          {
            getFieldDecorator('createUserName')(
              <Input allowClear placeholder="请输入操作人姓名" />
            )
          }
        </Form.Item>       
        <Form.Item>
          <Button
            type="primary"
            htmlType="submit"
          >
            查询
          </Button>
        </Form.Item>
      </Form>
    );
  }
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, fieldsValue) => {   
      
      const rangeValue = fieldsValue['range-picker'] || [];
      const startDate = rangeValue.length > 0 ? rangeValue[0].format('YYYY-MM-DD'): ''
      const endDate = rangeValue.length > 0 ? rangeValue[1].format('YYYY-MM-DD'): ''

      let values = {
        ...fieldsValue,
        startDate,
        endDate        
      };    
      delete values['range-picker']  
      
      if (!err) {
        this.props.onSearch(values);
      }
    });
  }
  componentDidMount() {
    this.props.onSearch()
  }
}

const WrappedMyForm = Form.create({
  name: 'borrowBook-search'
})(MyForm);

const mapState = (state) => ({
  gradeAndClass: state.login.gradeAndClass || loStorage.getItem('allClass')
})

const mapDispatch = (dispatch) => ({
  onSearch(param) {
    dispatch(actionCreators.getTableList(param));
  }
})

export default connect(mapState, mapDispatch)(WrappedMyForm);
