import React, { Component, Fragment } from 'react';
import { Table, Button } from 'antd';
import { connect } from 'react-redux';
import axios from 'axios';
import { loStorage } from 'libs/storage.js';
import { host, sessionId } from 'libs/io.js';
import { withRouter } from 'react-router-dom';

class Main extends Component {

  render() {
    let { tableList } = this.props;

    const columns = [
      {
        title: '编号',
        dataIndex: 'batchCode',
        key: 'batchCode',
        align: 'center',
        render: text => <span>{text ? text : '无'}</span>,
      },
      {
        title: '添加时间',
        dataIndex: 'createDate',
        key: 'createDate',
        align: 'center',
        render: (text, record) => {
          const arr = ['', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六', '星期天']
          const week = record.week;
          const we = week ? arr[week] : '';
          const txt = text ? `${text} ${we}` : '无';
          return <span>{txt}</span>
        }
      },

      {
        title: '添加宝贝人数',
        dataIndex: 'operateNum',
        key: 'operateNum',
        align: 'center',
        render: text => <span>{text ? text : '无'}</span>,
      },
      {
        title: '添加成功人数',
        dataIndex: 'succeedNum',
        key: 'succeedNum',
        align: 'center',
        render: text => <span>{text ? text : '无'}</span>,
      },
      {
        title: '添加失败人数',
        dataIndex: 'errorNum',
        key: 'errorNum',
        align: 'center',
        render: text => <span>{text ? text : '无'}</span>,
      },
      {
        title: '操作人',
        dataIndex: 'managerName',
        key: 'managerName',
        align: 'center',
        render: text => <span>{text ? text : '无'}</span>,
      },
      {
        title: '操作',
        key: 'action',
        render: (text, record) => {
          
          return record.errorNum !== '0' && <Fragment>
            <Button type="primary" onClick={() => this.exportData(record.batchCode)}>下载失败名单</Button>
          </Fragment>
        },
      }
    ];
    const pagination = {
      showSizeChanger: true,
      defaultPageSize: 30,
      pageSizeOptions: ['10', '20', '30', '50', '100', '150', '200']
    }
    return (
      <Fragment>
        <Table pagination={pagination} rowKey="batchCode" columns={columns} dataSource={tableList} bordered={true} />
      </Fragment>
    );
  }
  // 下载
  exportData = (batchCode) => {
    let url = host + '/curator/member/exportBatchIncKiddieList?x-auth-token=' + sessionId + '&isSucceed=0&batchCode=' + batchCode;
    let fileName = loStorage.getItem('userMes')['userInfo']['eduInstitution']['name'];
    axios({
      method: 'get',
      url: url,
      headers: { 'x-auth-token': loStorage.getItem('userMes') && loStorage.getItem('userMes').x_auth_token },
      responseType: 'blob' // 表明返回服务器返回的数据类型
    })
      .then((res) => { // 处理返回的文件流
        if (!res.data) {
          return
        }
        let url = window.URL.createObjectURL(new Blob([res.data]))
        let link = document.createElement('a')
        link.style.display = 'none'
        link.href = url
        link.setAttribute('download', fileName + '-失败名单.xls')
        document.body.appendChild(link)
        link.click()
      })
  }

  componentDidMount() {

  }
}

let mapStateToProps = (state) => ({
  tableList: state.regularMember.tableList,
})

let mapDispatchToProps = (dispatch) => {
  return {
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Main))
