import React, { Component } from 'react';
import { connect } from 'react-redux';
import { listColumns1, listColumns2 } from '../config';
import { Table, Button, Alert, Icon} from 'antd';
import { loStorage } from 'libs/storage.js';
import { actionCreators } from '../store';
import { actionCreators as actionCreators2 } from 'components/store';

let category;

class Main extends Component {

  clearSelected = () => {
    this.props.setSelectArr([],[])
  }

  render() {
    const { userInfo, selectedRowKeys, setSelectArr } = this.props;
    category = userInfo.eduInstitution.category;
    const { listData } = this.props;
    let columns = category === '2' ? listColumns2 : listColumns1;

    if (columns[columns.length - 1]['title'] !== '操作') {
      columns.push({
        title: '操作',
        key: 'action',
        render: (text, record) => {
          return <Button type="primary" onClick={() => this.props.showEditUser(true, record)}>编辑</Button>
        }
      })
    }
    const rowSelection = {
      selectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {        
        setSelectArr(selectedRowKeys, selectedRows)
      }
    };

    const MessageText = () => {
      return (<div>
        <span className="l-mr2 l-bule"><Icon theme="filled" type="info-circle" /></span>
        已选择{selectedRowKeys.length}个宝贝
        <span className="l-ml4 l-bule l-hover" onClick={this.clearSelected}>清空选择</span>
      </div>);
    }

    const pagination = {
      showSizeChanger: true,
      defaultPageSize: 30,
      pageSizeOptions: ['10', '20', '30', '50', '100', '150', '200']
    }

    return (
      <div className="l-mt2 l-mb2">
        {
        selectedRowKeys.length > 0 &&
        <Alert className="l-mb2" message={MessageText()} type="info" />
      }
        <Table
          rowKey='kiddieSysNo'
          columns={columns}
          dataSource={listData}
          rowSelection={category === '2' ? null: rowSelection}
          pagination={pagination}
          bordered={true}
        />
      </div>);
  }
  componentDidMount() {
    
  } 
}

const mapState = (state) => ({
  userInfo: state.login.userInfo || (loStorage.getItem('userMes') && loStorage.getItem('userMes').userInfo) || null,
  listData: state.allMember.listData,
  selectedRowKeys: state.allMember.selectedRowKeys,
})

const mapDispatch = (dispatch) => ({
  showEditUser(val, data) {
    dispatch(actionCreators2.showEditUser(val, data));
  },
  setSelectArr(kiddleIdArr, objArr) {    
    dispatch(actionCreators.setSelectArr(kiddleIdArr, objArr))
  },
})

export default connect(mapState, mapDispatch)(Main);
