import * as actionTypes from './actionTypes';


const defaultState = {
  viewCirculationRecordsData: {},
  visibileCirculationRecords: false,
  showEditUserData: {
    ifShowEditUser: false,
    data: {}
  },
  visibileErrorModal: false,
  viewBooksData: {},
  isErrorModal: false,
  otherData: {}
}

export default (state = defaultState, action) => {
  switch (action.type) {
    case actionTypes.VISIBLE_CIRCULATION_RECORDS:
      return {
        ...state,
        visibileCirculationRecords: true,
        viewCirculationRecordsData: action.data
      }
    case actionTypes.HIDDEN_CIRCULATION_RECORDS:
      return {
        ...state,
        visibileCirculationRecords: false
      }
    case actionTypes.SHOW_EDIT_USER:
      return {
        ...state,
        showEditUserData: {
          ifShowEditUser: action.data.val,
          data: action.data.data
        }
      }
      case actionTypes.VISIBILE_ERROR_MODAL:
        return {
          ...state,
          visibileErrorModal: true,
          viewBooksData: action.data,
          isErrorModal: action.flag
      };
      case actionTypes.CANCEL_ERROR_MODAL:
        return {
          ...state,
          visibileErrorModal: false
        };
      case actionTypes.UPDATE_BOOKS_DATA:
        return {
          ...state,
          viewBooksData: action.data
        };
      case actionTypes.SET_OTHER_DATA:
        return {
          ...state,
          otherData: action.data
        };
    default:
      return state;
  }
}

