import { message } from 'antd';
import { ajaxAxios } from 'libs/io.js';
import * as actionTypes from './actionTypes';

export const getTableData = (param = {}) => {
  return (dispatch) => {
    ajaxAxios({
      url: '/book/pageBookVOByCnd',
      data: {
        currentPage: 1,
        pageSize: 9999,
        ...param
      }
    }).then((res) => {
      if (res.success) {
        dispatch(updateData(res.result.records, param));
      } else {
        res.logMessage && message.error(res.code);
      }
    });
  };
}

export const updateSearchFormData = (data) => {
  return {
    type: actionTypes.STORE_SEARCH_FORM_DATA,
    data
  }
};

const updateData = (data, searchParam) => {
  return {
    type: actionTypes.UPDATE_TABLE_LIST_DATA,
    data,
    searchParam
  }
};
