import {
  ajaxAxios
} from 'libs/io.js';
import { message } from 'antd';
import * as actionTypes from './actionTypes';

let params = {}
export const getTableList = param => {
  params = param
  return (dispatch) => {
    for (const key in param) {
      if (!param[key]) {
        param[key] = undefined
      }
    }
    ajaxAxios({
      url: '/member/pageKiddieVOByCnd',
      method: 'get',
      params: {
        currentPage: 1,
        pageSize: 9999,
        ...params,
        type: 1,
      }
    }).then((res) => {
      if (res.success) {
        dispatch(updateData(res.result.records));
      } else {
        res.logMessage && message.error(res.logMessage);
      };
    });
  }
}

const updateData = (data) => {
  return {
    type: actionTypes.UPDATE_TABLE_LIST_DATA,
    data
  }
};


// 选中的
export const selectColumns = (selectedRowKeys, selectedRows) => {
  return {
    type: actionTypes.SELECT_COLUMNS,
    selectedRowKeys,
    selectedRows
  }
}

// 显示添加会员弹窗
export const showAddMemberDialog = (data) => {
  return {
    type: actionTypes.SHOWADDMEMBERDIALOG,
    data
  }
}

export const visibleBatchUpMemberModal = (data) => {
  return {
    type: actionTypes.SHOWBATCHDIALOG,
    data
  }
}

export const cancelAddMemberDialog = (data) => {
  return {
    type: actionTypes.CANCELADDMEMBERDIALOG,
    data
  }
}

export const getTableListPre = () => {
  return (dispatch) => {
    dispatch(getTableList(params))
    dispatch(getResidue())
  }
}

//  查询馆剩余会员名额
export const getResidue = () => {
  return (dispatch) => {
    ajaxAxios({
      url: '/curator/memberOrder/getMemberResidue',
      method: 'get'
    }).then((res) => {
      const result = res.result
      dispatch(getResidueNum(result));
    });
  }
}

export const getResidueNum = (data) => {
  return {
    type: actionTypes.GETRESIDUE,
    data
  }
}

// 显示升级会员弹窗
export const visibleUpMemberModal = (data, kiddieId) => {
  return {
    type: actionTypes.VISIBLEUPMEMBERMODAL,
    data,
    kiddieId
  }
}

let addMultiParams = {}
// 批量添加会员列表查询
export const onSearchAddMulti = (param) => {
  addMultiParams = param
  return dispatch => {
    ajaxAxios({
      url: '/curator/member/pageBatchIncKiddie',
      method: 'post',
      data: {
        currentPage: 1,
        pageSize: 9999,
        ...addMultiParams,
      }
    }).then((res) => {
      if (res.success) {
        dispatch(UpdataMulti(res.result.records))
      } else {
        res.logMessage && message.error(res.logMessage);
      };
    });
  }
}

export const UpdataMulti = (data) => {
  return {
    type: actionTypes.UPDATA_MULTI,
    data
  }
}
export const onSearchAddMultiPre = () => {
  return (dispatch) => {
    dispatch(onSearchAddMulti(addMultiParams))
  }
}

export const setInform = (flag) => {
  return {
    type: actionTypes.SET_INFORM_VISIBLE,
    data: flag
  }
}





