import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { Table, Icon, Alert, Button } from 'antd';
import { listColumns1 } from '../config';
import { loStorage } from 'libs/storage.js';
import { actionCreators } from '../store';
import { actionCreators as actionCreatorsCommon } from 'components/store';

class Main extends Component {
  clearSelected = () => {
    this.props.setSelectArr([],[])
  }
  // 处理异常
  dealwithListColumns(list) {
    return list.map(item => {

      if (item.title === '阅读包状态') {
        item.render = (text, record) => {
          if (record.borrowState === '2') {
            return (<Fragment> {text || '暂无'}<Icon type="exclamation-circle" className="l-red l-ml1" theme="filled" /></Fragment>)
          } else {
            return text || '暂无'
          }
        }
      }
      return item
    })
  }
  render() {
    const {
      listData,
    } = this.props;
    const { selectedRowKeys, setSelectArr } = this.props;

    this.dealwithListColumns(listColumns1)
    if (listColumns1[listColumns1.length - 1]['title'] !== '操作') {
      listColumns1.push({
        title: '操作',
        key: 'action',
        render: (text, record) => {
          const borrowState = record.borrowState;
          return <Fragment>
            <Button className="l-mr2" type="primary" onClick={() => this.props.onViewErrorBooks(record.sysNo, false)}>查看图书</Button>
            {borrowState === '2' && <Button className="l-mr2" type="primary" onClick={() => this.props.onViewErrorBooks(record.sysNo, true)}>补书</Button>}
            <Button className="l-mr2" type="primary" onClick={() => this.props.circulationRecords(record.sysNo)}>流通记录</Button>
            {borrowState === '0' && <Button type="primary" className="l-mr2" onClick={() => {
              this.props.stopOrUndistributedSingle(record.qrCodeNumber, 4)
            }}>设置为停用</Button>}
            {borrowState === '4' && <Button type="primary" className="l-mr2" onClick={() => {
              this.props.stopOrUndistributedSingle(record.qrCodeNumber, 0)
            }}>设置为未分配</Button>}
          </Fragment>

        },
      })
    }
    const rowSelection = {
      selectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        setSelectArr(selectedRowKeys, selectedRows)
      }
    };
    const MessageText = () => {
      return (<div>
        <span className="l-mr2 l-bule"><Icon theme="filled" type="info-circle" /></span>
        已选择{selectedRowKeys.length}个阅读包
        <span className="l-ml4 l-bule" onClick={this.clearSelected}>清空选择</span>
      </div>);
    }
    const pagination = {
      showSizeChanger: true,
      defaultPageSize: 30,
      pageSizeOptions: ['10', '20', '30', '50', '100', '150', '200']
    }
    return (<div className="l-mt2 l-mb2">
      {
        selectedRowKeys.length > 0 &&
        <Alert className="l-mb2" message={MessageText()} type="info" />
      }
      <Table
        rowKey={record => record.qrCodeNumber}
        rowSelection={rowSelection}
        columns={listColumns1}
        dataSource={listData}
        pagination={pagination}
        bordered={true}
      />
    </div>);
  }
  componentDidMount() {
  }
}

const mapState = (state) => ({
  listData: state.readBags.listData,
  userInfo: state.login.userInfo || (loStorage.getItem('userMes') && loStorage.getItem('userMes').userInfo) || null,
  listColumns: state.readBags.listColumns,
  selectedRowKeys: state.readBags.selectedRowKeys,
  selectedRows: state.readBags.selectedRows,
})

const mapDispatch = (dispatch) => ({
  onViewErrorBooks(bookBagSysNo, flag) {
    dispatch(actionCreatorsCommon.getBooksData(bookBagSysNo, flag));
  },
  stopOrUndistributedSingle(qrCodeNumber, type) {
    dispatch(actionCreators.stopOrUndistributedSingle({ qrCodeNumber, type }))
  },
  setSelectArr(qrCodeNumberArr, objArr) {    
    dispatch(actionCreators.setSelectArr(qrCodeNumberArr, objArr))
  },
  circulationRecords(bookBagSysNo) {
    dispatch(actionCreatorsCommon.circulationRecords(bookBagSysNo))
  }
})

export default connect(mapState, mapDispatch)(Main);
