import React,{ Component,
  // Fragment 
} from 'react';
import { connect } from 'react-redux';
import { Modal } from 'antd';
import { actionCreators } from '../store';
import ChangeForm from './changeForm.js';

class Main extends Component {
  render() {
    const { handleCancel,visibileChangeDefaultPwsModal} = this.props;
    return(
      <Modal
        visible={visibileChangeDefaultPwsModal}
        width="400px"
        title="安全设置"
        onCancel={handleCancel}
        footer={null}
        maskClosable={false}
      >
        <p className="l-red l-fz1 l-align-center">*为了您的信息安全，首次登录请先修改您的登录密码。</p>
        <ChangeForm />
      </Modal>
    );
  }
}

const mapState = (state)=> ({
  visibileChangeDefaultPwsModal:state.login.visibileChangeDefaultPwsModal
})

const mapDispatch = (dispatch)=> ({
  handleCancel() {
    dispatch(actionCreators.hideChangeDefaultPwsModal());
  }
})

export default connect(mapState,mapDispatch)(Main);
