import {
  ajaxAxios
} from 'libs/io.js';
import * as actionTypes from './actionTypes';
// import { loStorage } from 'libs/storage.js';

let defParams = {}; // 默认查询参数

export const showOrHideEditOrAddFn = (flag = false, obj = {}) => {
  // obj type name phone
  let data = {
    visibleEditOrAdd: flag,
    type: obj.type,
    name: obj.name || '',
    phone: obj.phone || ''
  }
  
  return {
    type: actionTypes.EDITORADDSHOW,
    data
  }
}

