// 添加或修改教师
export const addOrEditTeacher = (v) => {  
  return (dispatch) => {
    ajaxAxios({
      url: '',
      method: 'post',
      data: { ...v },
      // params: {
      //   ...paramsObj
      // }
    }).then((res) => {
      // dispatch((res.result));
      dispatch(showOrHideEditOrAddFn(false));
    });
  }
};

const setDataSource = (data) => {
  return {
    type: actionTypes.SETDATASOURCE,
    data
  }
}
