import * as actionTypes from './actionTypes';

const defaultState = {
  listData: [],
  selectArr: [],
  selectObj: [],
  visibleModal: false,
  visibleModal2: false,
  statistical: {},
}

export default (state = defaultState, action) => {
  switch (action.type) {
    case actionTypes.CHANGE_SELECT_QRCODE_ARR:
      return {
        ...state,
        selectArr: action.arr,
        selectObj: action.obj,
      }
    case actionTypes.IF_SHOW_OR_HIDE_MODAL:
      return {
        ...state,
        visibleModal: action.data
      }
    case actionTypes.IF_SHOW_OR_HIDE_MODAL2:
      return {
        ...state,
        visibleModal2: action.data
      }
    case actionTypes.SET_LIST:
      return {
        ...state,
        listData: action.data
      }
    case actionTypes.STATISTICAL:
      return {
        ...state,
        statistical: action.data
      };
    default:
      return state;
  }
}
