import React,{ Component } from 'react';
import { connect } from 'react-redux';
import {
  Form, Icon, Input, Button, Row, Col
} from 'antd';
import { Link } from 'react-router-dom';
import PageFooter from 'components/footer';
import { actionCreators } from './store';
import ChangeDefaultPsw from './components/changeDefaultPsw.js';

class Main extends Component {
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.props.onLogin(values,this.props.history);
      }
    });
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    return (<div
        style={{
          backgroundColor:'#f2f2f2',
          backgroundImage:'url(http://cdn.baonahao.com//login_center/img/login_regis_banner/index_beij1.jpg)',
          backgroundSize:'cover'
        }}
      >
      <div style={{maxWidth:'1000px',margin:'0 auto'}}>
        <Row className="l-100vh" type="flex" justify="center" align="middle">
          <Col span={8} className="l-box-shadow1 l-pd3 l-bg-white l-radius1">
            <h2 className="l-align-center">互动宝宝馆长端</h2>
            <Form onSubmit={this.handleSubmit}>
              <Form.Item>
                {getFieldDecorator('username', {
                  rules: [{ required: true, message: '请输入用户名!' }],
                })(
                  <Input prefix={<Icon type="user" className="l-pale" />} placeholder="用户名" />
                )}
              </Form.Item>
              <Form.Item>
                {getFieldDecorator('password', {
                  rules: [{ required: true, message: '请输入密码!' }],
                })(
                  <Input prefix={<Icon type="lock" className="l-pale" />} type="password" placeholder="密码" />
                )}
              </Form.Item>
              <Form.Item>
                <Link className="l-right" to="/forgetPassword">忘记密码</Link>
                <Button type="primary" htmlType="submit" style={{width:'100%'}}>
                  登录
                </Button>
              </Form.Item>
            </Form>
          </Col>
      </Row>
    </div>
    <ChangeDefaultPsw />
    <PageFooter />
  </div>);
  }
}

const Login = Form.create()(Main);

const mapState=(state)=> ({
})
  

const mapDispatch= (dispatch)=> ({
  onLogin(values,history) {
    dispatch(actionCreators.login(values,history));
  }
})

export default connect(mapState,mapDispatch)(Login);
