import { ajaxAxios,
 } from 'libs/io.js';
// import { ldyAxios } from 'libs/ldyAxios.js';
import { message } from 'antd';
// import qs from 'qs';

// import axios from 'axios';
import * as actionTypes from './actionTypes';
import { loStorage } from 'libs/storage.js';


export const getData = () => {
  return (dispatch) => {
    ajaxAxios({
      url: '/stats/eduFirstPage',
      method: 'get'
    }).then((res) => {
      dispatch(storeData(res.result));
    });
  }
};


export const getCodeImgAjax = (invitationCode) => {
  
  return (dispatch) => {
    
    ajaxAxios({
      method: 'get',
      url: '/edu/getEduShare',
      params: { invitationCode }
    })
      .then(function (response) {
        dispatch(setCodeImgAction(response.result));
      })
      .catch(function (error) {
        console.log(error);
      })

  }
};
// 删除班级
export const delClassrooms = (id) => {
  return (dispatch) => {
    
    ajaxAxios({
      method: 'get',
      url: '/classInfo/delete',
      params: { classId: id }
    })
      .then(function (res) {
        message.success('删除班级成功！')
      })
      .catch(function (error) {
        console.log(error);
      })

  }
}

//获取班级详细信息
export const getClassrooms = () => {
  return (dispatch) => {
    ajaxAxios({
      url: '/edu/getEduInfo',
      method: 'get'
    }).then((res) => {
      dispatch(setClassrooms(res.result));
      loStorage.setItem('indexRoomList',res.result )
    });
  }
}

// 修改图书馆信息
export const changeLibrary = (values) => {
  // console.log(values)
  return (dispatch) => {
    ajaxAxios({
      url: '/edu/editEduAndClassInfo',
      data: {
        eduName: values.library ? values.library.libraryName : '', //修改图书馆名称
        eduAddress: values.library ? values.library.libraryAddress : '', //修改图书馆地址
        edClsInfos: values.classRooms ? values.classRooms : '', // 修改班级
        rmClsInfos: values.delRooms ? values.delRooms : '',// 删除班级
        addClsInfos: values.addClsInfos ? values.addClsInfos : ''// 添加班级
      }
    }).then((res) => {
      if (res.code === 'OK') {
        message.info('操作成功')
      } else {
        message.error(res.logMessage)
      }
     dispatch(getClassrooms())
    });
  }
}


// 获取二维码
export const getCode = () => {
  return (dispatch) => {
    ajaxAxios({
      url: '/edu/getEduShare',
      method: 'get'
    }).then((res) => {
      dispatch(setCode(res.result));
    });
  }
}

// 获取年级列表
export const getGrades = () => {
  return (dispatch) => {
    ajaxAxios({
      url: '/grade/getGrades',
      method: 'get'
    }).then((res) => {
      // console.log(res.result)
      dispatch(setGrades(res.result));
    });
  }
}


// 获取年级列表
const setGrades = (data) => {
  return {
    type: actionTypes.STAGRADES,
    data
  }
}

const storeData = (data) => {
  return {
    type: actionTypes.STATISTICAL,
    data
  }
}

const setCodeImgAction = (data) => {
  return {
    type: actionTypes.GETCODEIMG,
    data
  }
}

// 获取年级信息
const setClassrooms = (data) => {
  return {
    type: actionTypes.SETCLASSROOMS,
    data
  }
}

// 获取二维码
const setCode = (data) => {
  return {
    type: actionTypes.SETCODE,
    data
  }
}

// 改变班级
export const changObjFn = (obj) => {
  return {
    type: actionTypes.CHANG_OBJ_CLASS,
    data: obj
  }
}

// 删除班级
export const delObjFn = (obj) => {
  return {
    type: actionTypes.DEL_OBJ_CLASS,
    data: obj
  }
}

// 弹窗修改班级 时 修改的是哪一项
export const changeEditingKey = (key) => {
  return {
    type: actionTypes.CHANG_EDITING_KEY,
    data: key
  }
}
