import React,{Component,Fragment } from 'react';
import { Form, message, Input, Button,Statistic } from 'antd';
// import { Link } from 'react-router-dom';
import * as G from 'libs/g.js';
import { ajaxAxios } from 'libs/io.js';

const Countdown = Statistic.Countdown;

const formItemLayout = {
  labelCol: { span: 3 },
  wrapperCol: { span: 16 },
};
const formTailLayout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 2, offset: 3 },
};

class Main extends Component {
  constructor(props) {
    super(props);
    this.state={
      isLoadingCode:false,
      deadline:null,
      seconds:1000*60
    }
  }
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
    <Fragment>
        <Form onSubmit={this.handleSubmit}>
          <Form.Item
            label='手机号'
            {...formItemLayout}
          >
            {getFieldDecorator('mobile', {
              rules: [{
                required: true,
                message: '请输入手机号!'
              }]
            })(
              <Input
                placeholder="手机号"
                style={{width:'180px',marginRight:'8px'}}
              />
            )}
            {
              this.state.isLoadingCode ?
              <span>
                还剩
                <Countdown
                  style={{display:'inline-block',verticalAlign:'top'}}
                  value={this.state.deadline}
                  onFinish={()=> this.setState({isLoadingCode:false})}
                  format="ss"
                />
                秒
              </span>
              :
              <Button
                onClick={ this.getSmsCode }
              >获取验证码</Button>
            }
          </Form.Item>
          <Form.Item
            label='验证码'
            {...formItemLayout}
          >
            {getFieldDecorator('smsCode', {
              rules: [{
                required: true,
                message: '请输入验证码!'
              }]
            })(
              <Input
                placeholder="验证码"
                style={{width:'180px'}}
              />
            )}
          </Form.Item>
          <Form.Item
            label='新密码'
            {...formItemLayout}
          >
            {getFieldDecorator('newPassword', {
              rules: [{
                required: true,
                message: '请输入新密码!'
              }]
            })(
              <Input
                placeholder="新密码"
                style={{width:'180px'}}
              />
            )}
          </Form.Item>
          <Form.Item
            label='确认新密码'
            {...formItemLayout}
          >
            {getFieldDecorator('affirmPassword', {
              rules: [{
                required: true,
                message: '请确认新密码!'
              },{
                validator:this.compareToFirstPassword

              }],
            })(
              <Input
                placeholder="确认新密码"
                style={{width:'180px'}}
              />
            )}
          </Form.Item>
          <Form.Item { ...formTailLayout }>
            <Button
              type="primary"
              htmlType="submit"
              style={{width:'100%'}}
            >
              确认修改
            </Button>
          </Form.Item>
        </Form>
    </Fragment>);
  }
  compareToFirstPassword = (rule, value, callback) => {
    const form = this.props.form;
    if (value && value !== form.getFieldValue('newPassword')) {
      callback('两次输入的密码不一致!');
    } else {
      callback();
    }
  }
  getSmsCode = ()=> {
    let { getFieldValue,validateFields } = this.props.form;
    let mobile = getFieldValue('mobile');
    let validateObj = ['mobile'];

    validateFields(validateObj,
      (err) => {
        if (!err) {
          this.setState(()=> ({
            isLoadingCode:true,
            deadline:Date.now() + this.state.seconds
          }));

          G.getSmsCode(mobile).then((success)=> {            
            if(!success) {
              this.setState(()=> ({
                isLoadingCode:false
              }));
            }
          });
        }
      },
    );
  }
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.setState(()=>({
          isLoadingCode:false
        }));
        delete values.mobile;
        this.onConfirm(values);
      }
    });
  }
  onConfirm = (values)=> {
    ajaxAxios({
      url:'/manageUser/modifyPassword',
      data:values,
      method:'post',
    }).then((res)=>{
      res.success ? message.success('修改成功！') : message.success(res.logMessage || '');
    });
  }
}

export default Form.create()(Main);
