import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { Modal, Row, Col, Alert, Divider, Input, Icon, message } from 'antd';
import { actionCreators } from '../store';
import gun from 'imgs/gun.jpg';

class Main extends Component {
  constructor(props) {
    super(props);
    this.bagCodeRef = React.createRef();
  }

  componentWillReceiveProps() {
    setTimeout(() => {
      this.bagCodeRef.current && this.bagCodeRef.current.focus();
    }, 300);
  }

  render() {
    const {
      stopUndistributedVisible,
      cancelProps,
      okProps,
      qrCodeNumber_stop_undistributed,
      qrCodeNumber_stop_undistributed_list,
      changeStopQrCodeNumber
    } = this.props;
    const { isStop } = this.props;
    return (<Fragment>
      <Modal
        visible={stopUndistributedVisible}
        width="80%"
        title={isStop ? '设置为停用' : '设置为未分配'}
        onCancel={cancelProps}
        onOk={() => okProps(qrCodeNumber_stop_undistributed_list, isStop)}
        okText={isStop ? '确认设置为停用' : '确认设置为未分配'}
      >
        <Alert message={isStop ? '书包设置为停用之后将不再参与分配流通' : '书包设置为未分配之后将重新参与分配流通'} type="warning" />
        <Divider />
        <Row className="l-mt5 l-mb5">
          <Col span={24}>
            <Input
              ref={this.bagCodeRef}
              style={{ background: `url(${gun}) no-repeat right top` }}
              placeholder="鼠标聚焦此输入框,并扫描书包二维码"
              value={qrCodeNumber_stop_undistributed}
              onChange={(e) => this.changeInputValue(e.target.value, isStop)}
            />
          </Col>
        </Row>
        <Row>
          <Col span={24}>{isStop ? '已选择设置停用书包个数:' : '已选择设置未分配书包个数:'}{qrCodeNumber_stop_undistributed_list.length}个</Col>
        </Row>
        <Row gutter={16} className="management-list">
          {
            qrCodeNumber_stop_undistributed_list.map((item, index) => {
              return <Col span={6} key={index + item}>
                <div>{item} <Icon style={{ cursor: 'pointer', display: 'inline-block', marginLeft: '5px' }} className="l-mt1 " type="close" onClick={() => changeStopQrCodeNumber({ classify: 'delete', value: index })} /></div>
              </Col>
            })
          }
        </Row>
      </Modal>
    </Fragment>);
  }
  changeInputValue = (value, isStop) => {
    const type = isStop ? 4 : 0; // 0未分配 1已分配 2异常 3已归还 4停用
    let { chaneQrCodeNumberStop, changeStopQrCodeNumber } = this.props;
    chaneQrCodeNumberStop(value)
    clearTimeout(this.timer);
    this.timer = setTimeout(() => {
      changeStopQrCodeNumber({ classify: 'add', value: value, type });
      chaneQrCodeNumberStop('')
    }, 500);
  }
}

const mapState = (state) => ({
  stopUndistributedVisible: state.readBags.stopUndistributedVisible,
  qrCodeNumber_stop_undistributed: state.readBags.qrCodeNumber_stop_undistributed,
  qrCodeNumber_stop_undistributed_list: state.readBags.qrCodeNumber_stop_undistributed_list,
})

const mapDispatch = (dispatch) => ({
  cancelProps() {
    dispatch(actionCreators.hideStopModel());
  },
  // 输入框显示
  chaneQrCodeNumberStop(value) {
    dispatch(actionCreators.chaneQrCodeNumberStop(value))
  },
  // 正式扫码的书包
  changeStopQrCodeNumber(obj) {
    dispatch(actionCreators.changeStopQrCodeNumber(obj))
  },
  // 确认停用或者未分配
  okProps(list, isStop) {
    const obj = {
      qrCodeNumbers: list.join(','),
      type: isStop ? 4 : 0
    }
    if (!obj.qrCodeNumbers) return message.info('选择设置的书包为空')

    dispatch(actionCreators.sureQrCodeNumber(obj));
  }
})

export default connect(mapState, mapDispatch)(Main);
