import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { Input, Button, Modal, Select, Form, DatePicker, Radio, message, Row, Checkbox } from 'antd';
import { actionCreators } from '../store';
import { areaList } from 'libs/area.js';
import { loStorage } from 'libs/storage.js';
import { ajaxAxios } from 'libs/io.js';
let btnWrapStyle = { display: 'flex', justifyContent: 'center' }
let btnStyle = { marginLeft: 10 }
const Option = Select.Option;

const formItemLayout = {
  labelCol: {
    xs: { span: 24 },
    sm: { span: 6 },
  },
  wrapperCol: {
    xs: { span: 24 },
    sm: { span: 12 },
  },
};
class EditUser extends Component {
  constructor(props) {
    super(props);
    this.state = {
      mobile: '',
      kiddieName: '',
      sex: '',
      birthday: '',
      classInfoName: '',
      classes: [],
      cities: [],
      counties: [],
      checked: false
    }
  }
  handleSelectChange = value => {
    if (!value) return;
    const { gradeClassInfos } = this.props.gradeAndClass;

    this.setState({
      classes: gradeClassInfos[value]
    })

    this.props.form.setFieldsValue({ 'classId': null });
  };
  handleSubmit = e => {

    e.preventDefault();

    const category = this.props.userInfo.eduInstitution.category;

    const { semesterNum, yearNum } = this.props;

    this.props.form.validateFields((err, values) => {

      let submitData = JSON.parse(JSON.stringify(values))

      submitData.birthdayStr = values['birthdayStr'] ? values['birthdayStr'].format('YYYY-MM-DD') : null;

      if (category === '2' && !/^1\d{10}$/.test(submitData.mobile)) {
        return message.error('家长手机号码错误，请重新输入')
      }

      if (category === '2' && !/^1\d{10}$/.test(submitData.adPhone)) {
        return message.error('联系人手机号码错误，请重新输入')
      }

      if (submitData.payType === '2' && semesterNum <= 0) {
        return message.error('半年名额不足，请重新选择')
      }

      if (submitData.payType === '2' && yearNum <= 0) {
        return message.error('全年名额不足，请重新选择')
      }

      if (err) return console.log(err)

      this.props.submitChangeBaby(submitData)
    });
  };
  handleOk = () => {
    this.props.cancelDialog(false)
  }
  handleCancel = () => {
    this.props.cancelDialog(false)
  }
  onSexChange = (sex) => {
    this.setState({
      sex
    })
  }
  onDateChange = (date, birthday) => {

    this.setState({
      birthday
    })
  }

  CheckboxFn = (e) => {
    this.setState({
      checked: e.target.checked
    })
  }
  changeMoible = (e) => {
    console.log(e)
  }

  render() {
    const { showAddMemberDialog, semesterNum, yearNum } = this.props;
    const { getFieldDecorator } = this.props.form;
    const { classes, cities, counties, checked } = this.state;
    const { grades } = this.props.gradeAndClass;
    let category = this.props.userInfo.eduInstitution.category;
    return (<Fragment>
      <Modal
        visible={showAddMemberDialog}
        destroyOnClose={true}
        title="添加普通会员"
        onCancel={this.handleCancel}
        footer={null}
      >
        <Form className="login-form" {...formItemLayout} labelAlign='left'
        >
          <Form.Item label="家长手机号：">
            {getFieldDecorator('mobile', {
              rules: [{ required: true, message: '请输入手机号!', pattern: /^1\d{10}$/ } ],
            })(
              <Input placeholder="请输入手机号"/>
            )}
          </Form.Item>
          <Form.Item label="宝宝姓名：">
            {getFieldDecorator('realName', {
              rules: [{ required: true, message: '请输入姓名!' }],
            })(
              <Input placeholder="请输入姓名" />
            )}
          </Form.Item>
          <Form.Item label="性别：">
            {getFieldDecorator('sex', {
              rules: [{ required: true, message: '请选择性别!' }],
            })(
              <Radio.Group onChange={this.onSexChange}>
                <Radio value={1}>男</Radio>
                <Radio value={2}>女</Radio>
              </Radio.Group>
            )}
          </Form.Item>
          <Form.Item label="生日：">
            {getFieldDecorator('birthdayStr', {
              rules: [{ required: true, message: '请输入生日!' }]
            })(
              <DatePicker onChange={this.onDateChange} format='YYYY-MM-DD' />
            )}
          </Form.Item>
          {category !== '2' && <Form.Item label="选择年级：">
            {getFieldDecorator('gradeId', {
              rules: [{ message: '请选择年级' }],
            })(
              <Select
                style={{ width: 200 }}
                placeholder="请选择"
                allowClear
                onChange={this.handleSelectChange}
              >
                {
                  grades && grades.map((item) => <Option key={item.sysNo} value={item.sysNo}>{item.name}</Option>)
                }
              </Select>
            )}
          </Form.Item>}
          {category !== '2' && <Form.Item label="选择班级：">
            {getFieldDecorator('classId', {
              rules: [{ message: '请选择班级' }],
            })(
              <Select
                style={{ width: 200 }}
                placeholder="请选择"
              >
                {
                  classes && classes.map((item) => <Option key={item.sysNo} value={item.sysNo}>{item.name}</Option>)
                }
              </Select>
            )}
          </Form.Item>}
          {category === '2' &&
            <Row>
              <Form.Item label="省份">
                {getFieldDecorator('province', {
                  rules: [{ required: true, message: '请选择省份' }]
                })(
                  <Select
                    style={{ width: 200 }}
                    placeholder="请选择"
                    allowClear
                    onChange={this.changeProvinceFn}
                  >
                    {
                      areaList && areaList.map((item) => <Option key={item.areaId} value={item.areaName}>{item.areaName}</Option>)
                    }
                  </Select>
                )}
              </Form.Item>
              <Form.Item label="城市">
                {getFieldDecorator('city', {
                  rules: [{ required: true, message: '请选择城市' }]
                })(
                  <Select
                    style={{ width: 200 }}
                    placeholder="请选择"
                    allowClear
                    onChange={this.changeCityFn}
                  >
                    {
                      cities && cities.map((item) => <Option key={item.areaId} value={item.areaName}>{item.areaName}</Option>)
                    }
                  </Select>
                )}
              </Form.Item>
              <Form.Item label="区/县">
                {getFieldDecorator('area', {
                  rules: [{ required: true, message: '请选择区' }]
                })(
                  <Select
                    style={{ width: 200 }}
                    placeholder="请选择"
                    allowClear
                    onChange={this.changeCountryFn}
                  >
                    {
                      counties && counties.map((item) => <Option key={item.areaId} value={item.areaName}>{item.areaName}</Option>)
                    }
                  </Select>
                )}
              </Form.Item>
              <Form.Item label="详细地址：">
                {getFieldDecorator('address', {
                  rules: [{ required: true, message: '请输入详细地址' }]
                })(
                  <Input placeholder="请输入详细地址" allowClear />
                )}
              </Form.Item>
              <Form.Item label="联系人">
                {getFieldDecorator('adName', {
                  rules: [{ required: true, message: '请输入联系人' }]
                })(
                  <Input placeholder="请输入联系人" allowClear />
                )}
              </Form.Item>
              <Form.Item label="性别：">
                {getFieldDecorator('adSex', {
                  rules: [{ required: true, message: '请选中性别' }]
                })(
                  <Radio.Group onChange={this.onSexChange}>
                    <Radio value={1}>先生</Radio>
                    <Radio value={2}>女士</Radio>
                  </Radio.Group>
                )}
              </Form.Item>
              <Form.Item label="手机号：">
                {getFieldDecorator('adPhone', {
                  rules: [{ required: true, message: '请输入手机号',pattern: /^1\d{10}$/ }]
                })(
                  <Input placeholder="请输入手机号" allowClear />
                )}
              </Form.Item>
            </Row>}
          <Form.Item label="">
            <Checkbox checked={checked} onChange={this.CheckboxFn}>
              升级高级会员
          </Checkbox>
          </Form.Item>
          {checked && <Form.Item label="会员年限：" >
            {getFieldDecorator('payType')(
              <Radio.Group>
                <div>
                  <Radio value="2">半年</Radio> <span className="l-red l-ml2">半年剩余名额：{semesterNum}</span>
                </div>
                <div>
                  <Radio value="3">全年</Radio> <span className="l-red l-ml2">全年剩余名额：{yearNum}</span>
                </div>
              </Radio.Group>
            )}

          </Form.Item>}

        </Form>
        <div style={btnWrapStyle}>
          <Button onClick={() => { this.handleCancel() }} type="primary">关闭</Button>
          <Button style={btnStyle} onClick={this.handleSubmit} type="primary">确定</Button>
        </div>
      </Modal>
    </Fragment>);
  }
  changeProvinceFn = (e) => {
    let cities = []
    for (let i in areaList) {
      const item = areaList[i]
      if (item.areaName === e) {
        cities = item.cities;
        break;
      }
    }
    this.setState({
      cities
    })
    this.props.form.setFieldsValue({ 'area': null, 'city': null });
  }

  changeCityFn = e => {
    const { cities } = this.state;
    let counties = []
    for (let i in cities) {
      const item = cities[i]
      if (item.areaName === e) {
        counties = item.counties;
        break;
      }
    }
    this.setState({
      counties
    })
    this.props.form.setFieldsValue({ 'area': null });

  }
}

const mapState = (state) => ({
  userInfo: state.login.userInfo || (loStorage.getItem('userMes') && loStorage.getItem('userMes').userInfo) || null,
  gradeAndClass: state.login.gradeAndClass || loStorage.getItem('allClass'),
  showAddMemberDialog: state.regularMember.showAddMemberDialog,
  semesterNum: state.regularMember.semesterNum,
  yearNum: state.regularMember.yearNum,
})

const mapDispatch = (dispatch) => ({
  cancelDialog(type) {
    dispatch(actionCreators.cancelAddMemberDialog(type));
  },
  submitChangeBaby(data) {
    ajaxAxios({
      url: '/curator/member/addKiddie',
      data
    }).then((res) => {
      if (res.result) {
        message.success('添加成功');
        dispatch(actionCreators.cancelAddMemberDialog(false));
        dispatch(actionCreators.getTableListPre())
      } else {
        message.warning(res.logMessage);
      }
    });
  }
})
let formComp = Form.create()(EditUser)
export default connect(mapState, mapDispatch)(formComp);
