import React, { Component, Fragment } from 'react';
import {
  Form, message, Input, Button, Row, Col
} from 'antd';
import { ajaxAxios } from 'libs/io.js';


const formItemLayout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 },
};

class Main extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoadingCode: false,
      deadline: null,
      seconds: 1000 * 60
    }
  }
  handleCancel = () => {
    window.location.href = "/index"
  }
  render() {
    const { getFieldDecorator } = this.props.form;
    return (<Fragment>
      <Form onSubmit={this.handleSubmit}>
        <Form.Item
          label='旧密码'
          {...formItemLayout}
        >
          {getFieldDecorator('oldPassword', {
            rules: [{
              required: true,
              message: '请输入旧密码!'
            }]
          })(
            <Input
              placeholder="请输入旧密码"
              type="password"
              style={{ width: '180px', marginLeft: '5px' }}
            />
          )}
        </Form.Item>
        <Form.Item
          label='新密码'
          {...formItemLayout}
        >
          {getFieldDecorator('newPassword', {
            rules: [{
              required: true,
              // message: '请输入新密码',
              message: '密码最少6位数，支持字母/数字/符号组合或不组合!',
              pattern: new RegExp(/^[^\u4e00-\u9fa5]{6,}$/),
            }]
          })(
            <Input
              type="password"
              placeholder="新密码"
              style={{ width: '180px', marginLeft: '5px' }}
            />
          )}
        </Form.Item>
        <Form.Item
          label='确认新密码'
          {...formItemLayout}
        >
          {getFieldDecorator('affirmPassword', {
            rules: [{
              required: true,
              message: '请确认新密码!'
            }, {
              validator: this.compareToFirstPassword

            }],
          })(
            <Input
              type="password"
              placeholder="确认新密码"
              style={{ width: '180px', marginLeft: '5px' }}
            />
          )}
        </Form.Item>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <Button type="primary" htmlType="submit">
              确认修改
              </Button>
            <Button style={{ marginLeft: 8 }} onClick={this.handleCancel}>
              跳过，暂不设置
              </Button>
          </Col>
        </Row>
      </Form>
    </Fragment>);
  }
  compareToFirstPassword = (rule, value, callback) => {
    const form = this.props.form;
    if (value && value !== form.getFieldValue('newPassword')) {
      callback('两次输入的密码不一致!');
    } else {
      callback();
    }
  }
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.onConfirm(values);
      }
    });
  }
  onConfirm = (values) => {
    console.log(values)
    ajaxAxios({
      url: '/manageUser/resetPasswordByDefault',
      data: {
        ...values
      }
    }).then((res) => {
      if (res.success) {
        message.success('修改成功！')
        return window.location.href = "/index"
      }
      message.error(res.logMessage);

    });
  }
}

export default Form.create()(Main);
