import React, { Component, Fragment } from 'react';
import { Form, message, Input, Button, Statistic } from 'antd';
// import { Link } from 'react-router-dom';
import { ajaxAxios } from 'libs/io.js';
import * as G from 'libs/g.js';

const Countdown = Statistic.Countdown;

const formItemLayout = {
  labelCol: { span: 3 },
  wrapperCol: { span: 16 },
};
const formTailLayout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 2, offset: 3 },
};

class Main extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loadingCurPhoneCode: false,
      loadingNewPhoneCode: false,
      oldDeadline: null,
      newDeadline: null,
      seconds: 1000 * 60
    }
  }
  render() {
    const { getFieldDecorator } = this.props.form;
    return (<Fragment>
      <Form onSubmit={this.handleSubmit}>
        <Form.Item
          label='当前绑定手机号'
          {...formItemLayout}
        >
          {getFieldDecorator('oldMobile', {
            rules: [{
              required: true,
              message: '请输入手机号!'
            }]
          })(
            <Input
              placeholder="手机号"
              style={{ width: '180px', marginRight: '8px' }}
            />
          )}
          {
            this.state.loadingCurPhoneCode ?
              <span>
                还剩
                <Countdown
                  style={{ display: 'inline-block', verticalAlign: 'top' }}
                  value={this.state.oldDeadline}
                  onFinish={() => this.setState({ loadingCurPhoneCode: false })}
                  format="ss"
                />
                秒
              </span>
              :
              <Button
                onClick={() => this.getSmsCode('old')}
              >获取验证码</Button>
          }
        </Form.Item>
        <Form.Item
          label='验证码'
          {...formItemLayout}
        >
          {getFieldDecorator('oldSmsCode', {
            rules: [{
              required: true,
              message: '请输入验证码!'
            }]
          })(
            <Input
              placeholder="验证码"
              style={{ width: '180px' }}
            />
          )}
        </Form.Item>
        <Form.Item
          label='新手机号'
          {...formItemLayout}
        >
          {getFieldDecorator('mobile', {
            rules: [{
              required: true,
              message: '请输入手机号!'
            }]
          })(
            <Input
              placeholder="手机号"
              style={{ width: '180px', marginRight: '8px' }}
            />
          )}
          {
            this.state.loadingNewPhoneCode ?
              <span>
                还剩
                <Countdown
                  style={{ display: 'inline-block', verticalAlign: 'top' }}
                  value={this.state.newDeadline}
                  onFinish={() => this.setState({ loadingNewPhoneCode: false })}
                  format="ss"
                />
                秒
              </span>
              :
              <Button
                onClick={this.getSmsCode}
              >获取验证码</Button>
          }
        </Form.Item>
        <Form.Item
          label='验证码'
          {...formItemLayout}
        >
          {getFieldDecorator('newSmsCode', {
            rules: [{
              required: true,
              message: '请输入验证码!'
            }]
          })(
            <Input
              placeholder="验证码"
              style={{ width: '180px' }}
            />
          )}
        </Form.Item>
        <Form.Item {...formTailLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ width: '100%' }}
          >
            确认更改
            </Button>
        </Form.Item>
      </Form>
    </Fragment>);
  }
  getSmsCode = (type) => {
    let { getFieldValue, validateFields } = this.props.form;
    let mobile = type === 'old' ? getFieldValue('oldMobile') : getFieldValue('mobile');
    let validateObj = type === 'old' ? ['oldMobile'] : ['mobile'];

    validateFields(validateObj,
      (err) => {
        if (!err) {
          this.setState(() => {
            return type === 'old' ? {
              loadingCurPhoneCode: true,
              oldDeadline: Date.now() + this.state.seconds
            } : {
                loadingNewPhoneCode: true,
                newDeadline: Date.now() + this.state.seconds
              }
          });
          G.getSmsCode(mobile).then((success) => {
            if (!success) {
              this.setState(() => {
                return type === 'old' ? {
                  loadingCurPhoneCode: false
                } : {
                    loadingNewPhoneCode: false
                  }
              });
            }
          });
        }
      },
    );
  }
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.setState(() => ({
          loadingCurPhoneCode: false,
          loadingNewPhoneCode: false
        }));
        this.onConfirm(values);
      }
    });
  }
  onConfirm = (values) => {
    ajaxAxios({
      url: '/manageUser/modifyMobile',
      data: values
    }).then((res) => {
      res.success ? message.success('修改成功！') : message.success(res.logMessage || '');
    });
  }
}

export default Form.create()(Main);
