import {
  ajaxAxios
} from 'libs/io.js';
import { message } from 'antd';
import * as actionTypes from './actionTypes';

let params = {}
export const getTableList = (param = {}) => {
  params = param
  return (dispatch) => {
    
    ajaxAxios({
      url: '/bookbag/matched_list',
      method: 'post',
      dataType: 'json',
      data: {
        currentPage: 1,
        pageSize: 9999,
        ...params
      }
    }).then((res) => {
      console.log('查询列表结果：', res)
      if (res.success) {
        dispatch(setList(res.result.records));
      } else {
        res.logMessage && message.error(res.logMessage);
      }

    });
  }
}

const setList = (data) => {
  return {
    type: actionTypes.SET_LIST,
    data
  }
}
const setDetailList = (data) => {
  return {
    type: actionTypes.SET_DETAIL_LIST,
    data
  }
}

export const getDetailSearch = (matchedId) => {
  return (dispatch) => {
    
    ajaxAxios({
      url: '/bookbag/matched_detail',
      method: 'post',
      dataType: 'json',
      data: {
        matchedId
      }
    }).then((res) => {
      if (res.success) {
        dispatch(setDetailList(res.result));
      } else {
        res.logMessage && message.error(res.logMessage);
      }

    });
  }
}

