import React, { Component ,Fragment} from 'react';
import {Input, Button, Modal,
    Form,Row,Col,message
} from 'antd';
import { loStorage } from 'libs/storage.js';
import { connect } from 'react-redux';
import { actionCreators } from '../store';

import EditableFormTable from './table.js';


const formItemLayout = {
    labelCol: { span: 6 },
    wrapperCol: { span: 18 },
};

class showLibrary extends Component {

    handleOk = () => {
        this.props.OK(false)
    };
    
    handleCancel = () => {
        this.props.Cancel(false)
    };

    handleSubmit = (e) => {
        e.preventDefault();
        if(this.props.editingKey)return message.info('请先保存之后再确认！')
        this.props.form.validateFields((err, values) => {
            this.submitEnd(values)
        });
    }
    
    // 确认提交结束
    submitEnd = (v) => {
         // 修改班级
         let changeObj = loStorage.getItem('changeObj')
        let delObj = loStorage.getItem('delObj')  
        
        let changeArr = [];
        let addArr = [];
        let delArr = []
        
        for(var i in delObj) {
            delArr.push(delObj[i])
        }

        for (let i in changeObj) {
            const item = changeObj[i];
            if (item.isAdd) {
                addArr.push(`${item.className}:${item.gradeId}`)
            } else {
                changeArr.push(`${item.sysNo}:${item.gradeId}:${item.className}`)
            }            
        }      
        this.props.changeLibrary({
            classRooms: changeArr.join(';'),
            addClsInfos: addArr.join(';'),
            delRooms: delArr.join(';'),
            library: {
                libraryAddress: v.libraryAddress,
                libraryName: v.libraryName
            }            
        })

        if(this.props.roomList) {
            loStorage.setItem('room', this.props.roomList)
        }  
        
        this.props.OK(false)
    }
    // 点击编辑
    handleEdit = (record) => {
        console.log(record)
        loStorage.setItem('editor', 'editor')
    } 

    
    render() {
        const { getFieldDecorator } = this.props.form;
        let {userInfo,roomList} = this.props;
        let category = userInfo.eduInstitution.category;
        const columns = [
            {
                title: '年级',
                dataIndex: 'gradeName',
                key: 'gradeName',
                align: 'center',
                width: 80,
                render: text => <span className='l-hover'>{text}</span>,
              },
              {
                title: '班级',
                dataIndex: 'className',
                key: 'className',
                align: 'center',
                width: 100,
                render: text => <span className='l-hover'>{text}</span>,
              },
              {
                title: '用户人数',
                dataIndex: 'userCount',
                key: 'userCount',
                align: 'center',
                width: 90,
                render: text => <span className='l-hover'>{text}</span>,
              }
          ];

        columns.push({
            title: '操作',
            align: 'center',
            key: 'action',
            render: (text, record) => {
                return (
                    <div style={{display: 'flex', justifyContent: 'center'}}>
                        <Button type="primary" style={{marginLeft: '15px'}} onClick={()=>this.handleEdit(record)}>编辑</Button>
                        <Button type="primary" style={{marginLeft: '15px'}} onClick={()=>this.handleDel(record)}>删除</Button>
                    </div>
                );
            },
        })

        
      return (
        <Fragment>
            <Modal
                    className = "library"
                    title="图书馆信息设置"
                    visible={this.props.show}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                    width="700px"
                    footer={null}
                    maskClosable={false}
                >
                   <p className="l-red l-fz1 l-align-center">*首次登录，请确认您的图书馆基本信息！</p>
                     
                    <Form onSubmit={this.handleSubmit}>
                        <Form.Item
                            label='图书馆名称'
                            {...formItemLayout}
                        >
                            {getFieldDecorator('libraryName', {
                                initialValue: roomList && roomList.eduName,
                                rules: [{
                                    required: true,
                                    message: '请输入图书馆名称!'
                                }]
                            })(
                                <Input                                    
                                type="text"
                                style={{width:'300px',marginLeft:'5px'}}
                            />
                            )}
                        </Form.Item>
                        <Form.Item
                            label='图书馆地址'
                            {...formItemLayout}
                        >
                            {getFieldDecorator('libraryAddress', {
                                initialValue: roomList && roomList.address,
                                rules: [{
                                    required: true,
                                    message: '请输入图书馆地址!'
                                }]
                            })(
                            <Input
                                type="text"
                                placeholder="请输入图书馆地址"
                                style={{width:'300px',marginLeft:'5px'}}
                            />
                            )}
                        </Form.Item>
                        {category === '2' ?  null : <Form.Item
                            label='班级设置'
                            {...formItemLayout}
                        >
                           <div className="librarySet">
                              <EditableFormTable />   
                           </div>
                           
                        </Form.Item>}
                      
                        <Row>
                            <Col span={24} style={{ textAlign: 'right' }}>
                            <Button type="primary" htmlType="submit">
                                确认修改
                            </Button>
                            <Button style={{ marginLeft: 8 }} onClick={this.handleCancel}>
                                跳过，暂不设置
                            </Button>
                            </Col>
                            </Row>
                    </Form>
            </Modal>
        </Fragment>
      );
    }   

}


let mapStateToProps = (state) => ({
    userInfo: state.login.userInfo || (loStorage.getItem('userMes') && loStorage.getItem('userMes').userInfo) || null,
    roomList: state.index.roomList, //年级信息
    editingKey: state.index.editingKey,
})

let mapDispatchToProps = (dispatch) => {
    return {
        getClassrooms(){
            dispatch(actionCreators.getClassrooms());
        },
        delClassrooms(id) {
            dispatch(actionCreators.delClassrooms(id));
        },
        changeLibrary(values) {
            dispatch(actionCreators.changeLibrary(values));
        },
    }
}

const Libarary = Form.create()(showLibrary)
export default connect(mapStateToProps,mapDispatchToProps)(Libarary)
