import * as actionTypes from './actionTypes';
const defaultState = {
  listData: null,
  selectedRowKeys: [],
  selectedRows: [],
  showAddMemberDialog: false,
  visibleUpMemberModal: false, // 升级会员弹窗
  visibleBatchUpMemberModal: false,
  semesterNum: '', // 半年会员人数
  yearNum: '', // 全年会员人数
  kiddieId: '', // 宝宝id
  tableList: [],
  informVisible: false
}

export default (state = defaultState, action) => {
  switch (action.type) {
    case actionTypes.UPDATE_TABLE_LIST_DATA: {
      return {
        ...state,
        listData: action.data,
      }
    }
    // 选中的会员
    case actionTypes.SELECT_COLUMNS:
      return {
        ...state,
        selectedRowKeys: action.selectedRowKeys,
        selectedRows: action.selectedRows
      }
    // 点击添加会员
    case actionTypes.SHOWADDMEMBERDIALOG:
      return {
        ...state,
        showAddMemberDialog: action.data
      }
    case actionTypes.CANCELADDMEMBERDIALOG:
      return {
        ...state,
        showAddMemberDialog: action.data
      }
    // 显示升级会员弹窗
    case actionTypes.VISIBLEUPMEMBERMODAL:
      return {
        ...state,
        visibleUpMemberModal: action.data,
        kiddieId: action.kiddieId
      }
    // 批量升级会员弹窗
    case actionTypes.SHOWBATCHDIALOG:
      return {
        ...state,
        visibleBatchUpMemberModal: action.data
      }
    // 查询剩余会员
    case actionTypes.GETRESIDUE:
      return {
        ...state,
        semesterNum: action.data.semesterNum,
        yearNum: action.data.yearNum
      }
    case actionTypes.UPDATA_MULTI:
      return {
        ...state,
        tableList: action.data
      }
    case actionTypes.SET_INFORM_VISIBLE:
      return {
        ...state,
        informVisible: action.data
      }
    default:
      return state;
  }
}
