import React, { Component } from 'react';
import { Layout, Menu, Icon } from 'antd';
import logo1 from 'imgs/logo1.png';
import logo from 'imgs/logo2.png';
import { loStorage } from 'libs/storage.js';
import { Link } from 'react-router-dom';
const { Sider } = Layout;
const { SubMenu } = Menu;

class PageSider extends Component {
    state = {
        collapsed: false,
        verifyBookBag: false,
    };

    onCollapse = (collapsed) => {
        this.setState({ collapsed });
    }

    onSelect({ item, key, selectedKeys }) {
        console.table([item, key, selectedKeys]);
    }

    componentWillMount() {
        let userMes = loStorage.getItem('userMes');
        let verifyBookBag = false;
        if (userMes) {
            verifyBookBag = userMes.userInfo.verifyBookBag; //所有图书是否完成校验 true-是 false-否
        }
        this.setState({
            verifyBookBag
        })
    }

    render() {
        return (<Sider
            collapsible
            collapsed={this.state.collapsed}
            onCollapse={this.onCollapse}
            className="siderMenu"
            width={170}
        >
            <div className="logo">
                <img style={{ height: '30px' }} src={this.state.collapsed ? logo : logo1} alt="logo" />
            </div>
            <Menu
                theme="dark"
                mode="inline"
            >
                <Menu.Item key="首页"><Link to="/index"><Icon type="home" /><span>首页</span></Link>
                </Menu.Item>
                <SubMenu key="借阅管理"
                    title={<span><Icon type="read" /><span>借阅管理</span></span>}>
                    <Menu.Item key="借书">
                        <Link to="/borrowBook">
                            <span>借书</span>
                        </Link>
                    </Menu.Item>
                    <Menu.Item key="借阅历史记录">
                        <Link to="/borrowHistory">
                            <span>借阅历史记录</span>
                        </Link>
                    </Menu.Item>
                    <Menu.Item key="还书">
                        <Link to="/returnBook">
                            <span>还书</span>
                        </Link>
                    </Menu.Item>
                    <Menu.Item key="补书">
                        <Link to="/fillBook">
                            <span>补书</span>
                        </Link>
                    </Menu.Item>
                </SubMenu>
                <SubMenu key="会员管理"
                    title={<span><Icon type="team" /><span>会员管理</span></span>}>
                    <Menu.Item key="高级会员管理">
                        <Link to="/seniorMember">
                            <span>高级会员管理</span>
                        </Link>
                    </Menu.Item>
                    <Menu.Item key="普通会员管理">
                        <Link to="/regularMember">
                            <span>普通会员管理</span>
                        </Link>
                    </Menu.Item>
                    <Menu.Item key="全部会员管理">
                        <Link to="/allMember">
                            <span>全部会员管理</span>
                        </Link>
                    </Menu.Item>
                </SubMenu>
                <SubMenu key="资产管理"
                    title={<span><Icon type="money-collect" /><span>资产管理</span></span>}>
                    <Menu.Item key="全部阅读包管理">
                        <Link to="/readPackageManegement">
                            <span>全部阅读包管理</span>
                        </Link>
                    </Menu.Item>
                    <Menu.Item key="图书管理">
                        <Link to="/bookManagement">
                            <span>图书管理</span>
                        </Link>
                    </Menu.Item>
                </SubMenu>
                {/* <SubMenu key="教师管理"
                    title={<span><Icon type="solution" /><span>教师管理</span></span>}>
                    <Menu.Item key="教师管理">
                        <Link to="/teacher">
                            <span>教师管理</span>
                        </Link>
                    </Menu.Item>
                </SubMenu>                 */}
            </Menu>
        </Sider>);
    }
}

export default PageSider;
