import { ajaxAxios } from 'libs/io.js';
import { message } from 'antd';
import * as actionTypes from './actionTypes';


let  params = {}
// 查询 获取列表
export const getTableList = (param = {}) => {
  params = param;
  return (dispatch) => {
  
    ajaxAxios({
      url: '/bookbag/managementInfo/list/page',
      method: 'post',
      data: {
        bookBagStatus: 3, // 3异常书包
        currentPage: 1,
        pageSize: 9999,
        ...params
      }
    }).then((res) => {
      console.log('查询列表结果：', res)
      if (res.success) {
        dispatch(setList(res.result.records));
      } else {
        res.logMessage && message.error(res.logMessage);
      }
    });
  }
}
export const setList = (data) => {
    return {
    type: actionTypes.SET_LIST,
    data
  }
}

