// import store from 'store2';
import { message } from 'antd';
import * as actionTypes from './actionTypes';
import { ajaxAxios } from 'libs/io.js';
import { loStorage } from 'libs/storage.js';

export const login = (values, history) => {
  return (dispatch) => {
			ajaxAxios({
				url:'/login',
				method:'post',
				data:values
			}).then((data)=> {
        // console.log(data)
        if(data.success && data.result.userInfo.eduInstitution) {

          if (data.result) {
            dispatch(setUserInfo(data.result))
            dispatch(getClass())
            dispatch(getClassrooms())
          } 
          // console.log(data.result)
          // setTimeout(() => {
          //   history.push('/index');
          // }, 300);
          /**
           * 判断初始密码，是初始密码弹框，否则就进入
           * data.result.resetPwd 为1 初始  2修改
           */
          // if(data.result && typeof data.result.resetPwd && data.result.resetPwd == 1) {
          //   dispatch(visibileChangeDefaultPwsModal());
          // }else {
          //  history.push('/');
          //   // window.location.href="/";
          // }
          
          localStorage.setItem('firstLogin', JSON.stringify(data.result.userInfo.firstLogin * 1))
          // console.log(data.result.userInfo.firstLogin)
          // 判断是否是首次登录 首次登录进行确认信息，否则跳到首页
           if(data.result.userInfo.firstLogin * 1 === 1) {
            
            dispatch(visibileChangeDefaultPwsModal());

           
          }else {
            setTimeout(()=>{
              history.push('/index');
            },1000)
             
          }


        }else {
          if(data.logMessage === "登录成功"){
            message.error('登录失败')
          }else{
            message.error(data.logMessage);
          }
        }
			}).catch(()=> {
        message.error('您输入账号/密码错误')
			});
  }

}
export const setUserInfo =(result)=> {
  return {
    type:actionTypes.SET_USER_INFO,
    result
  }
}
export const visibileChangeDefaultPwsModal =()=> {
  return {
    type:actionTypes.VISIBILE_CHANGE_DEFAULT_PWS_MODAL
  }
}
export const hideChangeDefaultPwsModal =()=> {
  return {
    type:actionTypes.HIDE_CHANGE_DEFAULT_PWS_MODAL
  }
}

// 获取幼儿园、小学等所有年级的所有的班级
export const getClass = (dispatch) => {
  return (dispatch) => {
      ajaxAxios({
        method: 'get',
        url: '/grade/getGradeClassInfos'
      }).then((res) => {
        if (res.result) {
          loStorage.setItem('allClass', res.result)
          dispatch(setGradeAndClass(res.result))
        }
    });    
  }
}

//获取班级详细信息
export const getClassrooms = (dispatch) => {
  return (dispatch) => {
    ajaxAxios({
      url: '/edu/getEduInfo',
      method: 'get'
    }).then((res) => {
      if (res.result) {
        loStorage.setItem('room', JSON.stringify(res.result))
        dispatch(setClassrooms(res.result))
      }
    });
  }
}

// 获取年级信息
const setClassrooms = (data) => {
  return {
    type: actionTypes.SETCLASSROOMS,
    data
  }
}

export const setGradeAndClass =(r)=> {
  return {
    type:actionTypes.SET_GRADE_AND_CLASS,
    data:r
  }
}
