import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import {
    Modal, Button, Tooltip,
    Row, Col
} from 'antd';
import { host, sessionId } from 'libs/io.js';
import axios from 'axios';
import { actionCreators } from './store';
import { loStorage } from 'libs/storage.js';
import './index.css';
import List from './components/list';
import ShowLiary from './components/showLibrary';

import Dialog from './components/dialog'

class Index extends Component {
    state = {
        visible: false,
        memberCount: '', //高级会员
        userCount: '', //普通会员
        showLibrary: loStorage.getItem(('firstLogin')) * 1 === 1 ? true : false,
        confirmLoading: false,
        isShow: false,
        isRoom: false,
        text: '',
        countList: []
    };
    // 下载邀请码
    displayPic = (invitationCode) => {
        this.props.getCode() //获取二维码 
        this.props.getCodeImgAjax(invitationCode)
        // console.log(invitationCode)
        this.setState({
            visible: true,
            invitationCode
        });
    };
    handleCancelModal = e => {
        this.setState({
            visible: false,
        });
    };

    // 点击确认按钮
    handleOK = (show) => {
        this.setState({
            showLibrary: show,
        });
        localStorage.removeItem('firstLogin')
    }

    // 点击取消按钮
    handleCancel = (show) => {
        this.setState({
            showLibrary: show,
        });
        localStorage.removeItem('firstLogin')
    }

    // 点击确认按钮
    handleok = (show) => {
        this.setState({
            isRoom: show,
        });
    }

    // 点击取消按钮
    handlecancel = (show) => {
        this.setState({
            isRoom: show,
        });
    }

    // 显示弹窗
    showDialog = (e) => {
        if (e.target.previousSibling.innerHTML === '图书馆信息') {
            this.setState({
                isRoom: true,
                text: e.target.previousSibling.innerHTML
            })
        } else if (e.target.previousSibling.innerHTML === '班级信息') {
            this.setState({
                isRoom: true,
                text: e.target.previousSibling.innerHTML
            })
        }

    }

    // 下载邀请码
    handleDown = () => {
        let url = host + '/edu/downEduShare?x-auth-token=' + sessionId
        axios({
            method: 'get',
            url: url,
            headers: { 'x-auth-token': loStorage.getItem('userMes') && loStorage.getItem('userMes').x_auth_token },
            // responseType: 'blob' // 表明返回服务器返回的数据类型
        })
        // console.log(111)
    }
    render() {
        /*图书馆类型：1：幼儿园；2：社区馆；3：小学
        @NotNull(message = "请选择图书馆类型！")
        private Integer category;
        */
        /*收费模式：0false：会员费；1true：服务费
        private Boolean isThirdparty;*/
        let { statistical, userInfo, imgStr, roomList, codeList } = this.props;
        let schoolArr = ['幼儿园', '社区馆', '小学']

        if (userInfo) {
            // let classInfoList = userInfo.classInfoList
        }
        let category = this.props.userInfo.eduInstitution.category;

        let arr1 = [
            {
                name: '班级数',
                count: roomList.classInfoCount,
                mark: false,
            },
            {
                name: '普通会员',
                count: roomList.userCount,
                mark: true,
                title: '普通会员：图书馆全部宝贝数量，包括可参与借阅的宝贝和不可参与借阅的宝贝'
            },
            {
                name: '高级会员',
                count: roomList.memberCount,
                mark: true,
                title: '高级会员：可参与借阅图书的宝贝数量'
            },
            {
                name: '升级高级会员名额(半年)',
                count: statistical.distributableMemberSemesterCount,
                mark: false
            },
            {
                name: '升级高级会员名额(全年)',
                count: statistical.distributableMemberYearCount,
                mark: false
            }
        ]
        let arr2 = [
            {
                name: '普通会员',
                count: roomList.userCount,
                mark: true,
                title: '普通会员：图书馆全部宝贝数量，包括可参与借阅的宝贝和不可参与借阅的宝贝'
            },
            {
                name: '高级会员',
                count: roomList.memberCount,
                mark: true,
                title: '高级会员：可参与借阅图书的宝贝数量'
            },
            {
                name: '升级高级会员名额(半年)',
                count: statistical.distributableMemberSemesterCount,
                mark: false
            },
            {
                name: '升级高级会员名额(全年)',
                count: statistical.distributableMemberYearCount,
                mark: false
            }
        ]
        let readBagList = [
            {
                name: '阅读包总数',
                count: statistical.totalBookbagCount,
                img: '分组 13.png',
                title: '阅读包总数：图书馆内全部的阅读包数量，已借阅、可借阅、异常、停用书包的总数'
            },
            {
                name: '已借阅',
                count: statistical.borrowingBookbagCount,
                img: '分组 15.png',
                title: '已借阅：已经分配给借阅会员的阅读包数量'
            },
            {
                name: '可借阅',
                count: statistical.distributableBookbagCount,
                img: '分组 17.png',
                title: '可借阅：可分配给借阅会员的阅读包数量'
            },
            {
                name: '异常',
                count: statistical.brokenBookbagCount,
                img: '分组 11.png',
                title: '异常：破损/丢失图书的阅读包数量，需补充图书后方可继续参与分配'
            },
            {
                name: '停用',
                count: statistical.stopBagCount,
                img: '分组 14.png',
                title: '停用：不参与分配的阅读包数量，可在全部书包管理中变更为可借阅状态即可参与分配'
            }
        ]
        return (
            <Fragment>
                <div className="containerDiv">
                    <Dialog
                        destroyOnClose={true}
                        show={this.state.isRoom}
                        text={this.state.text}
                        cancel={(isShow) => { this.handlecancel(isShow) }}
                        ok={(isShow) => { this.handleok(isShow) }} />

                    <div className="top">
                        <div className="gardenInfo">
                            <p>图书馆信息</p>
                            <p onClick={(e) => this.showDialog(e)}>编辑</p>
                        </div>
                        <div className="garden">
                            <Row>
                                <Col>
                                    <img src={require('../../../static/images/昵称.png')} alt="" />
                                </Col>
                                <Col className="rightL" span={8} style={{ display: 'flex', width: '100%' }}>
                                    <div>
                                        图书馆名称：
                                    {/*<Col span={1}></Col>*/}

                                        {roomList && roomList.eduName}
                                    </div>

                                    <div>
                                        <span className="label">{userInfo && schoolArr[userInfo.eduInstitution.category - 1]}</span>
                                        <span className="label">{userInfo && userInfo.membershipEduConfig.isThirdparty ? '服务费' : '会员费'}</span>
                                    </div>

                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                    <img src={require('../../../static/images/付款.png')} alt="" />
                                </Col>
                                <Col span={8} className="mcode rightL">
                                    <div>
                                        <div className="class"><span className="content">邀请码：{roomList.eduInvitationCode}</span></div>
                                        <div>
                                            <img className="l-hover" alt="生成邀请码" src={require('../../../static/images/Group 7.png')} onClick={() => {
                                                this.displayPic(codeList)
                                            }} />
                                        </div>
                                    </div>
                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                    <img src={require('../../../static/images/账号-2.png')} alt="" />
                                </Col>
                                <Col span={8} className="rightL">
                                    图书馆地址：{roomList && roomList.address}
                                </Col>
                            </Row>
                        </div>
                        {category === '2' ? <div className="member">
                            {arr2.map(item => {
                                return <dl key={item.name}>
                                    <dt>
                                        <span>{item.name}</span>
                                        <Tooltip placement="topLeft" title={item.title}>
                                            {item.mark === true ? <img src={require("../../../static/images/分组 copy 5.png")} alt="" /> : ""}
                                        </Tooltip>
                                    </dt>
                                    <dd>
                                        {item.count}
                                    </dd>
                                </dl>
                            })}
                        </div> : <div className="member">
                                {arr1.map(item => {
                                    return <dl key={item.name}>
                                        <dt>
                                            <span>{item.name}</span>
                                            <Tooltip placement="topLeft" title={item.title}>
                                                {item.mark === true ? <img src={require("../../../static/images/分组 copy 5.png")} alt="" /> : ""}
                                            </Tooltip>
                                        </dt>
                                        <dd>
                                            {item.count}
                                        </dd>
                                    </dl>
                                })}
                            </div>}
                        <div>
                        </div>
                    </div>

                    {/* 阅读包信息 */}
                    <div className="reader">
                        <p>阅读包信息</p>
                        <div className="content">
                            {readBagList.map(item => {
                                return <div className="infor" key={item.name}>
                                    <div>
                                        <Tooltip placement="topLeft" title={item.title}>
                                            <img src={require("../../../static/images/分组 copy 5.png")} alt="" />
                                        </Tooltip>
                                    </div>
                                    <div>
                                        <dl>
                                            <dt>
                                                <img src={require('../../../static/images/' + item.img)} alt="" />
                                            </dt>
                                            <dd>
                                                <span>{item.count}</span>
                                                <span>{item.name}</span>
                                            </dd>
                                        </dl>
                                    </div>
                                </div>
                            })}

                        </div>
                    </div>

                    {/* 班级信息  */}
                    {category === '2' ? null : <div className="classroom">
                        <div className="addClassroom">
                            <p>班级信息</p>
                            <p style={{ cursor: 'pointer' }} onClick={(e) => this.showDialog(e)}>编辑</p>
                        </div>
                        <div className="detail">
                            <List />
                        </div>
                    </div>}

                </div>
                <Modal
                    title=""
                    visible={this.state.visible}
                    okText="ww"
                    cancelText="33"
                    footer={null}
                    onCancel={this.handleCancelModal}

                >
                    <img src={imgStr} className="qrcode"
                        alt="" />
                    <Button type='primary' style={{ margin: '0 auto', display: 'block' }}>

                        <a href={host + '/edu/downEduShare'}>下载邀请码图片</a>

                    </Button>
                </Modal>

                {/* 修改图书馆弹窗 */}
                <ShowLiary
                    show={this.state.showLibrary}
                    Cancel={(isShow) => { this.handleCancel(isShow) }}
                    OK={(isShow) => { this.handleOK(isShow) }}
                />
            </Fragment>);
    }

    componentDidMount() {
        this.props.getData();
        this.props.getClassrooms() //获取班级详细信息
    }
}

let mapStateToProps = (state) => ({
    statistical: state.index.statistical,
    imgStr: state.index.imgStr,
    schoolList: state.index.schoolList,
    roomList: state.index.roomList, //获取班级详细信息
    codeList: state.index.codeList,
    gradeAndClass: state.login.gradeAndClass || loStorage.getItem('allClass') || [],
    userInfo: state.login.userInfo || (loStorage.getItem('userMes') && loStorage.getItem('userMes').userInfo) || null
})

let mapDispatchToProps = (dispatch) => {
    return {
        getData() {
            dispatch(actionCreators.getData());
        },
        getCodeImgAjax(invitationCode) {
            dispatch(actionCreators.getCodeImgAjax(invitationCode));
        },
        //获取班级详细信息
        getClassrooms() {
            dispatch(actionCreators.getClassrooms());
        },
        // 获取二维码
        getCode() {
            dispatch(actionCreators.getCode());
        },
        // 下载邀请码图片
        downCode() {
            dispatch(actionCreators.downCode());
        }

    }
}
// const index = Form.create()(Index)
// export default connect(mapStateToProps,mapDispatchToProps)(TodoListApp);
export default connect(mapStateToProps, mapDispatchToProps)(Index);
