import { message } from 'antd';
import { ajaxAxios } from 'libs/io.js';
import * as actionTypes from './actionTypes';

let preFlag = null;

export const circulationRecords = (bookbagId) => {
  return (dispatch) => {    
    ajaxAxios({
      url: '/bookbag/circulate_record',
      method: 'get',
      params: {
        bookbagId
      }
    }).then((res) => {
      if (res.success) {
        dispatch(visibleCirculationRecords({ ...res.result }));
      } else {
        message.info(res.logMessage);
      }
    });
  }
}

export const visibleCirculationRecords = (data) => {
  return {
    type: actionTypes.VISIBLE_CIRCULATION_RECORDS,
    data
  }
}
export const CirculationRecords = (data) => {
  return {
    type: actionTypes.HIDDEN_CIRCULATION_RECORDS,
    data
  }
}
export const showEditUser = (val, data) => {
  return {
    type: actionTypes.SHOW_EDIT_USER,
    data: { val, data }
  }
}

export const submitChangeBaby = (data) => {
  return (dispatch) => {
    ajaxAxios({
      url: '/member/updateKiddie',
      data
    }).then((res) => {
      if (res.result) {
        dispatch(showEditUser(false));
        message.success('修改成功');
      } else {
        dispatch(showEditUser(false));
        message.warning(res.logMessage);
      }
    });
  }
}

export const getBooksData = (bookBagSysNo, flag) => {

  return (dispatch) => {
    ajaxAxios({
      url: '/bookbag/findBooksByBookBagSysNo',
      data: {
        bookBagSysNo: bookBagSysNo
      }
    }).then((res) => {
      if (res.success) {
        // borrowState 0未分配 1已分配 2异常  3已归还 4 停用
        dispatch(visibleViewErrorModal(res.result, flag))
      } else {
        message.info(res.logMessage);
      }
    });
  }
}

export const visibleViewErrorModal = (data, flag) => {
  return {
    type: actionTypes.VISIBILE_ERROR_MODAL,
    data,
    flag
  }
}

export const cancelErrorModal = () => {
  return {
    type: actionTypes.CANCEL_ERROR_MODAL
  }
}

export const updateBooksData = (data) => {
  return {
    type: actionTypes.UPDATE_BOOKS_DATA,
    data
  }
}

export const getOtherData = (kiddieId) => {
  return dispatch => {
    ajaxAxios({
      url: '/curator/member/kiddieDetail',
      method: 'get',
      params: {
        kiddieId
      }
    }).then((res) => {
      if (res.success) {
        dispatch(setOtherData({ ...res.result }));
      } else {
        message.info(res.logMessage);
      }
    });
  }
}

export const setOtherData = (data) => {
  return {
    type: actionTypes.SET_OTHER_DATA,
    data
  }
}
