import React from 'react';
import {
  Form, Input, Button, DatePicker
} from 'antd';
import { connect } from 'react-redux';
import { actionCreators } from '../store';
const { RangePicker } = DatePicker;
class MyForm extends React.Component {

  state = {
  }

  render() {
    const {
      getFieldDecorator,
    } = this.props.form;

    return (
      <Form layout="inline" onSubmit={this.handleSubmit}>
        <Form.Item
          label="姓名"
        >
          {
            getFieldDecorator('name')(
              <Input allowClear placeholder="请输入姓名" />
            )
          }
        </Form.Item>
        <Form.Item
          label="手机号"
        >
          {
            getFieldDecorator('phone')(
              <Input allowClear  placeholder="请输入手机号" />
            )
          }
        </Form.Item>
        <Form.Item label="创建时间">
          {getFieldDecorator('range-picker')(<RangePicker style={{width: '250px'}}/>)}
        </Form.Item>
        <Form.Item>
          <Button
            type="primary"
            htmlType="submit"
          >
            查询
          </Button>
        </Form.Item>
      </Form>
    );
  }
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, fieldsValue) => {
      console.log('fieldsValue', fieldsValue)
      const rangeValue = fieldsValue['range-picker'];
      let values = {
        ...fieldsValue,
        startTime: rangeValue ? rangeValue[0].format('YYYY-MM-DD') : undefined,
        endTime: rangeValue ? rangeValue[1].format('YYYY-MM-DD') : undefined
      };
      delete values['range-picker']
      
      if (!err) {
        this.props.onSearch(values);
      }
    });
  }
  componentDidMount() {
    this.props.onSearch()
  }
}

const WrappedMyForm = Form.create({
  name: 'teacher-search'
})(MyForm);

const mapState = (state) => ({
})

const mapDispatch = (dispatch) => ({
  onSearch(param) {
    dispatch(actionCreators.getTableList(param));
  }
})

export default connect(mapState, mapDispatch)(WrappedMyForm);
