import * as actionTypes from './actionTypes';

const defaultState={
  searchParam:{},
  listData: [],
}

export default (state=defaultState,action) => {
  switch(action.type) {
    case actionTypes.STORE_SEARCH_FORM_DATA :
      return {
        ...state,
        searchParam:action.data
      }
    case actionTypes.UPDATE_TABLE_LIST_DATA :
      return {
        ...state,
        listData: action.data
      }
    default :
      return state;
  }
}
