import React, { Component, } from 'react';
import { connect } from 'react-redux';
import { Button, Table, Alert, Icon } from 'antd';
import { actionCreators } from '../store';
import { host, sessionId } from 'libs/io.js';
import axios from 'axios';
import { loStorage } from 'libs/storage.js';

class Main extends Component {
  state = {
    bookSysNosArr: [],
    current: 1,
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps !== this.props) {
      this.setState({
        current: 1,
      })
    }
  }

  render() {
    const { listData } = this.props;
    const { bookSysNosArr } = this.state;
    const listColumns = [{
      title: '书名',
      dataIndex: 'bookName',
      render: text => <span>{text}</span>,
    }, {
      title: 'ISBN',
      dataIndex: 'iSBN'
    },
    {
      title: '状态',
      dataIndex: 'bookStateStr'
    }];


    const rowSelection = {
      selectedRowKeys: bookSysNosArr,
      onSelect: (record, selected) => {
        let arr = JSON.parse(JSON.stringify(bookSysNosArr))
        if (selected) {
          arr.push(record.bookBagDetailSysNo)
        } else {
          arr.splice(arr.findIndex(item => item === record.bookBagDetailSysNo), 1)
        }
        this.setState({
          bookSysNosArr: arr
        })
      },
      onSelectAll: (selected, selectedRows, changeRow) => {
        let arr = JSON.parse(JSON.stringify(bookSysNosArr))
        if (selected) {
          selectedRows.map(item => arr.push(item.bookBagDetailSysNo))
        } else {
          changeRow.map(item => arr.splice(arr.findIndex(ite => ite === item.bookBagDetailSysNo), 1))
        }
        arr = [...new Set(arr)]
        this.setState({
          bookSysNosArr: arr
        })
      }
    };

    const MessageText = () => {
      return (<div>
        <span className="l-mr2 l-hover"><Icon theme="filled" type="info-circle" /></span>
        已选择{bookSysNosArr.length}本书
        <span className="l-ml4 l-hover" onClick={this.clearSelected}>清空选择</span>
      </div>);
    }

    const paginationChange = (page) => {
      this.setState({
        current: page,
      })
    }

    const pagination = {
      current: this.state.current,
      onChange: paginationChange
    }

    return (<div className="l-mt2 l-mb2">
      <br />
      <Button
        className="l-mb3"
        onClick={this.exportFiles}
      >导出</Button>
      {
        bookSysNosArr.length > 0 &&
        <Alert className="l-mb2" message={MessageText()} type="info" />
      }
      <Table
        rowKey={r => r.bookBagDetailSysNo}
        rowSelection={rowSelection}
        columns={listColumns}
        dataSource={listData}
        pagination={pagination}
        bordered={true}
      />
    </div>);

  }
  componentDidMount() {
    this.props.getTableData();
  }

  clearSelected = () => {
    this.setState({
      bookSysNosArr: []
    })
  }

  exportFiles = () => {
    // let { searchParam } = this.props;
    const { bookSysNosArr } = this.state;
    const str = bookSysNosArr.join(',');
    let fileName = loStorage.getItem('userMes')['userInfo']['eduInstitution']['name'];
    let url = host + '/book/exportBookToExcel?x-auth-token=' + sessionId;
    url += str ? '&bookBagDetailSysNos=' + str + '&' : '';

    // url += qs.stringify(searchParam);
    axios({
      method: 'get',
      url: url,
      headers: { 'x-auth-token': loStorage.getItem('userMes') && loStorage.getItem('userMes').x_auth_token },
      responseType: 'blob' // 表明返回服务器返回的数据类型
    })
      .then((res) => { // 处理返回的文件流
        if (!res.data) {
          return
        }
        let url = window.URL.createObjectURL(new Blob([res.data]))
        let link = document.createElement('a')
        link.style.display = 'none'
        link.href = url
        link.setAttribute('download', fileName + '-图书管理.xls')
        document.body.appendChild(link)
        link.click()
      })
  }
}


const mapState = (state) => ({
  listData: state.bookManagement.listData,
  searchParam: state.bookManagement.searchParam,
})

const mapDispatch = (dispatch) => ({
  getTableData() {
    dispatch(actionCreators.getTableData());
  },
})

export default connect(mapState, mapDispatch)(Main);
