import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import {Breadcrumb} from 'antd';
import { actionCreators } from './store';
import SearchForm from './components/searchForm.js';
import Renew from './components/renew.js'; // 批量续费
import Out from './components/out.js'; // 导出
import ErrorBag from 'components/errorBag.js'
import MyList from './components/list.js';
import SeniorRenewal from './components/seniorMemberRenewal.js';
// import { loStorage } from 'libs/storage.js';
import BorrowList from './components/borrowList';
import EditUser from 'components/editUser';

class Main extends Component {
    state = {
    }; 
    
    componentDidMount() {
    };

    render() {
        let { ifShowEditUser, data: editUserData } = this.props.showEditUserData
        return (
            <Fragment>
                 <div style={{background: '#fff',padding: '16px'}}>
                      <Breadcrumb className="l-mb3">
                            <Breadcrumb.Item>会员管理</Breadcrumb.Item>
                            <Breadcrumb.Item>高级会员</Breadcrumb.Item>
                      </Breadcrumb>
                      <SearchForm />
                      <div className="l-mt4">
                         <Renew />
                         <Out />
                      </div>
                      <MyList />
                      <ErrorBag
                        searchFn={() => this.props.getTableListPre()}/>
                      <SeniorRenewal />
                      <BorrowList />
                      {ifShowEditUser && <EditUser editUserData={editUserData} searchFn={() => {
                            this.props.getTableListPre()
                        }} />}
                  </div>
            </Fragment>);
    }
}

let mapStateToProps = (state) => ({
    showEditUserData: state.common.showEditUserData,
})
let mapDispatchToProps = (dispatch) => {
    return {
        getTableListPre() {
            dispatch(actionCreators.getTableListPre());
        },      
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Main);
