import * as actionTypes from './actionTypes';

const defaultState = {
  tableList: [],
  tableDetailList: [],
}

export default (state = defaultState, action) => {
  switch (action.type) {
    case actionTypes.SET_LIST:
      return {
        ...state,
        tableList: action.data
      };
    case actionTypes.SET_DETAIL_LIST:
      return {
        ...state,
        tableDetailList: action.data
      };
    default:
      return state;
  }
}
