import React from 'react';
import {
  Form, Icon,
  Button,
  Upload, message
} from 'antd';
import { connect } from 'react-redux';
import { actionCreators } from '../../../store';
import {
  host,
} from 'libs/io.js';
import { loStorage } from 'libs/storage.js';

const formItemLayout = {
  labelCol: { span: 4 },
  wrapperCol: { span: 19 },
};



class MyForm extends React.Component {
  render() {
    const {
      getFieldDecorator,
    } = this.props.form;
    const { onSearchAddMultiPre, setInform } = this.props;

    const props = {
      name: 'file',
      action: host + '/curator/member/batchIncKiddie',
      headers: { 'x-auth-token': loStorage.getItem('userMes') && loStorage.getItem('userMes').x_auth_token },
      withCredentials: true,
      onChange(info) {
        if (info.file.status !== 'uploading') {
          const res = info.file.response;
          if (res.success) {
            onSearchAddMultiPre();
            setInform(false);
          } else {
            message.error(res.logMessage);
          };
        }
        if (info.file.status === 'done') {
        } else if (info.file.status === 'error') {
          message.error(`${info.file.name} 导入失败.`);
        }
      },
    };

    return (
      <Form onSubmit={this.handleSubmit}>
        <Form.Item
          {...formItemLayout}
          label="上传文件"
        >
          {
            getFieldDecorator('userName')(
              <Upload {...props}>
                <Button>
                  <Icon type="upload" /> 上传文件并导入
                </Button>
              </Upload>
            )
          }
        </Form.Item>
      </Form>
    );
  }
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  }
}

let mapDispatchToProps = (dispatch) => {
  return {
    onSearchAddMultiPre() {
      dispatch(actionCreators.onSearchAddMultiPre());
    },
    setInform(flag) {
      dispatch(actionCreators.setInform(flag))
    }
  }
}

const WrappedMyForm = Form.create()(MyForm);

export default connect(null, mapDispatchToProps)(WrappedMyForm);
