import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import {  Modal,  Row, Col} from 'antd';
import { actionCreators } from './store';
import ErrorBagInput from './errorBagInput.js'

class Main extends Component {

  state = {};

  render() {
    const {
      visibileErrorModal,
      onCancelErrorModal,
      viewBooksData,
      isErrorModal
    } = this.props;

    return (<Fragment>
      <Modal
        visible={visibileErrorModal}
        destroyOnClose={true}
        width="50%"
        title={(viewBooksData.borrowState === '2' && isErrorModal) ? '图书异常' : '查看图书'}
        onCancel={onCancelErrorModal}
        footer = {null}
      >
        {
          viewBooksData.bookVoList && viewBooksData.bookVoList.length > 0 &&
          <Fragment>
            <h3>借阅包资产编号：{viewBooksData.qrCodeNumber}</h3>
            {
              viewBooksData.bookVoList.map((item, index) => {

                return <div
                  key={item.iSBN}
                  className={(item.bookState > 2 && isErrorModal) ? 'l-red' : ''}
                >
                  {/* {++index}, */}
                    <span className="l-mr3">
                    {item.bookName}
                  </span>
                  ISBN: {item.iSBN}
                  {(item.bookState > 2 && isErrorModal) && <ErrorBagInput item={item} viewBooksData={viewBooksData} searchFn={this.props.searchFn} />}
                </div>
              })
            }
          </Fragment>
        }
        <div className="l-pale l-fz1 l-mt4">
          <Row gutter={16}>
            <Col span={5}>
              最新状态记录: {viewBooksData.lastStateStr}
            </Col>
            {viewBooksData.realName && <Col span={5}>
              {viewBooksData.borrowState === '1' ? '当前借阅宝宝：' : '上次借阅宝宝：'}
              {viewBooksData.realName}
            </Col>}

            {viewBooksData.className && <Col span={7}>
              班级信息：{viewBooksData.className}
            </Col>}
            {viewBooksData.lastReturnDate && <Col span={7}>
              {viewBooksData.borrowState === '1' ? `分配时间: ${viewBooksData.borrowDate}` : `归还时间: ${viewBooksData.lastReturnDate}`}
            </Col>
            }
          </Row>

        </div>
      </Modal>     
    </Fragment>);
  }
}

const mapState = (state) => ({
  visibileErrorModal: state.common.visibileErrorModal,
  viewBooksData: state.common.viewBooksData,
  isErrorModal: state.common.isErrorModal,
})

const mapDispatch = (dispatch) => ({
  onCancelErrorModal() {
    dispatch(actionCreators.cancelErrorModal());
  }
})

export default connect(mapState, mapDispatch)(Main);
