import React, {
	Component
} from 'react';
import { Layout, 
  // Menu, Icon 
} from 'antd';

import PageSider from './sider.js';
import PageHeader from './header.js';
import PageFooter from './footer.js';

const {
  //  Header, Sider,
   Content, 
  //  Footer
   } = Layout;
const { Fragment } = React;

export default class PageLayout extends Component {
  componentWillMount(){
  }
  render(){
    return (<Fragment>
      <Layout>
        <PageSider />
        <Layout className="contentBox">
          <PageHeader />
          <Content className="mainContent">
            {this.props.children}
          </Content>
          <PageFooter />
        </Layout>
      </Layout>
    </Fragment>);
  }
}
