import React,{ Component } from 'react';
import { Button } from 'antd';
import qs from 'qs';
import { connect } from 'react-redux';
import axios from 'axios';
import { loStorage } from 'libs/storage.js';
import { host,sessionId } from 'libs/io.js';

class Main extends Component {
  render() {
    return(<Button className="l-mr2" type="primary" style={{marginLeft: '15px'}} 
    onClick={this.exportData}>导出</Button>);
  }
  
  exportData = ()=> {
    let { searchParam } = this.props;
    console.log(searchParam)
    searchParam = qs.stringify(searchParam);
    let url = host + '/member/exportKiddieListToExcel?x-auth-token=' + sessionId + '&' + searchParam;
    let fileName = loStorage.getItem('userMes')['userInfo']['eduInstitution']['name'];
    axios({
      method: 'get',
      params: {
        type: 1
      },
      url: url, 
      headers: { 'x-auth-token': loStorage.getItem('userMes') && loStorage.getItem('userMes').x_auth_token },
      responseType: 'blob' // 表明返回服务器返回的数据类型
    })
      .then((res) => { // 处理返回的文件流
        if (!res.data) {
          return
        }
        let url = window.URL.createObjectURL(new Blob([res.data]))
        let link = document.createElement('a')
        link.style.display = 'none'
        link.href = url
        link.setAttribute('download', fileName+'-会员管理.xls')
        document.body.appendChild(link)
        link.click()
      })
   }
}

const mapState = (state)=> ({
  selectedRowKeys:state.regularMember.selectedRowKeys,
  searchParam:state.regularMember.searchParam
})

export default connect(mapState,null)(Main);