import React, { Component, Fragment } from 'react';
import { Table} from 'antd';
import { connect } from 'react-redux';
import { actionCreators } from '../store';

class Main extends Component {
  
    render() {
      let {roomList } = this.props;
          const columns = [
            {
              title: '年级',
              dataIndex: 'gradeName',
              key: 'gradeName',
              align: 'center',
              render: text => <span className="l-bule">{text ? text : '无'}</span>,
            },
            {
              title: '班级',
              dataIndex: 'className',
              key: 'className',
              align: 'center',
              render: text => <span className="l-bule">{text ? text : '无'}</span>,
            },
          
            {
              title: '普通会员',
              dataIndex: 'userCount',
              key: 'userCount',
              align: 'center',
              render: text => <span className="l-bule">{text ? text : '无'}</span>,
            },
            {
              title: '高级会员',
              dataIndex: 'memberCount',
              key: 'memberCount',
              align: 'center',
              render: text => <span className="l-bule">{text ? text : '无'}</span>,
            }
          ];
      return (
        <Fragment>
            <Table rowKey={record => record.sysNo} columns={columns} dataSource={roomList.classInfoVoList} />
        </Fragment>
      );
    }
    componentDidMount() {
    }
  }

let mapStateToProps = (state) => ({
    roomList: state.index.roomList, //年级信息
})

let mapDispatchToProps = (dispatch) => {
    return {
        getClassrooms(){
            dispatch(actionCreators.getClassrooms());
        }

    }
}

  export default connect(mapStateToProps, mapDispatchToProps)(Main)
