import React, {
  Component,
  Fragment
} from 'react';
import { connect } from 'react-redux';
import { Modal, Button, Checkbox, message, Row, Col } from 'antd';
import { actionCreators } from '../store';
class Main extends Component {
  state = {
    confirmLoading: true,
    dataSource: {},
    num: 0
  }

  render() {
    const { visibleModal, statistical } = this.props;
    const { confirmLoading, dataSource, num } = this.state;
    return <Fragment>
      <div className='l-mt2 l-mb2'><Button type="primary" onClick={this.showBorrowSureModel}>生成借阅单</Button></div>
      <Modal
        title="生成借阅单"
        okText="确定生成"
        visible={visibleModal}
        onOk={this.handleOk}
        onCancel={this.handleCancel}
        destroyOnClose={true}
        width="60%"
        confirmLoading={confirmLoading}
      >
        <div>
          <div>未分配书包数量： {statistical.distributableBookbagCount}</div>
          <Row>
            <Col span={8}>已选择宝贝数量： {num}</Col>
            <Col span={16} className="l-red">{statistical.distributableBookbagCount < num && <span>*可借阅书包数量不足，请重新选择宝贝人数</span>}
            </Col>
          </Row>
          {Object.keys(dataSource).map((key, index) => {
            const t = dataSource[key]; // 年级

            return <div key={key + index} className="l-bottom-border l-mt2 l-mb2">
              <span className="l-bac-blue">{key}</span>
              {
                Object.keys(t).map((ke, inx) => {
                  const arr = t[ke]; // 班级
                  return <div key={ke + inx} className="l-top-border l-mt2 l-mb2 borrow-class">
                    <span className="l-bold">{ke}</span>
                    <div className="borrow-child">
                      {arr.map((item, index) => {
                        // 具体的小朋友
                        return <Checkbox
                          className="l-mb2"
                          key={item.familyMemberShipId}
                          onChange={(e) => this.checkboxFn(e, key, ke, index)}
                          checked={item.checked}
                          lable={item.familyMemberShipId}
                          value={item.kiddieName}
                        >
                          {item.kiddieName}
                        </Checkbox>
                      }
                      )}
                    </div>
                  </div>
                })
              }
            </div>
          })
          }
        </div>
      </Modal>
    </Fragment>
  }

  checkboxFn = (e, gradeName, className, index) => {
    let { num, dataSource } = this.state;

    const checked = e.target.checked;
    let predataSource = JSON.parse(JSON.stringify(dataSource));
    // 对dataSource进行处理
    predataSource[gradeName][className][index]['checked'] = checked;
    num += checked ? 1 : -1;
    this.setState({
      dataSource: predataSource,
      num
    })

  }
  handleOk = () => {
    const { statistical } = this.props;
    const { num } = this.state;
    if (statistical.distributableBookbagCount < num) {
      return message.error('可借阅书包数量不足，请重新选择宝贝人数')
    }
    const list = this.state.dataSource;
    // {年级:{班级:[{...},{...}]}}
    let familyShipIds = '';
    for (let i in list) {
      const ite = list[i];
      for (let j in ite) {
        const ites = ite[j];
        for (let k in ites) {
          const item = ites[k];
          if (item.checked) {
            familyShipIds += ',' + item.familyMemberShipId;
          }
        }
      }
    }
    familyShipIds = familyShipIds.slice(1)
    
    this.props.createReadList(familyShipIds)
  };

  handleCancel = () => {
    this.props.visibleModalFn(false)
  };

  showBorrowSureModel = async () => {
    if (this.props.selectArr.length === 0) return message.info('请勾选宝贝')
    // 弹窗 loading 显示
    this.setState({
      confirmLoading: true
    })
    this.props.visibleModalFn(true)

    // 等待对数据的处理
    const dataSource = await this.dealWithSelectData(this.props.selectObj)

    // 设置loading隐藏  并设置处理后的数据 {年级： {班级：[{name:'zs',id:11}]}}
    this.setState({
      confirmLoading: false,
      dataSource
    })
  }

  dealWithSelectData = (obj) => {
    let o = {};// {年级： {班级：[{name:'zs',id:11}]}}
    return new Promise((resolve, reject) => {
      let num = 0;
      for (let i in obj) {
        num += 1;
        const item = obj[i];
        item.checked = true; // 设置默认都勾选上
        // 年级 班级已经存在，直接往数组添加； 否则创建只有当前项的数组

        /**
         * 一、 大分类  1.1年级不存在 班级不存在  1.2 年级不存在 班级存在   
         *  */
        
        // 是否没有年级
        if (!item.gradeName) {
          //  1 分组里面没有年级 先添加年级空对象
          if (!o['无年级']) o['无年级'] = {};

          // 没有班级
          if (!item.className) {
            // 2 分组里面没有班级  先添加空班级 空数组
            if (!o['无年级']['无班级']) o['无年级']['无班级'] = [];
            o['无年级']['无班级'].push(item)
            
          } else {
            // 有班级
            //  3 分组里面没有对应的班级 先添加对应班级空数组
            if (!o['无年级'][item.className]) o['无年级'][item.className] = []
            o['无年级'][item.className].push(item)
          }
          continue
        }  

        /**
         * 二 年级 班级 都存在的情况
         *  */ 
        // 2.1分组里面 年级不存在
        if (!o[item.gradeName])o[item.gradeName] ={}
         
        // if (!o[item.gradeName]) {
        //   o[item.gradeName] = { [item.className]: [item] }
        //   continue
        // }

        // 2.2分组里面  年级存在 班级不存在
        if (o[item.gradeName] && !o[item.gradeName][item.className]) o[item.gradeName][item.className]=[]
        // if (o[item.gradeName] && !o[item.gradeName][item.className]) {
        //   o[item.gradeName][item.className] = [item]
        //   continue
        // }

        // 2.3分组里面  年级班级都存在 数组里面添加       
        o[item.gradeName][item.className].push(item)
          
        // if (o[item.gradeName] && o[item.gradeName][item.className]) {
        //   o[item.gradeName][item.className].push(item)
        //   continue
        // }
      }
      this.setState({
        num
      })
      resolve(o)
    })
  }

  componentDidMount() {

  }


}


const mapState = (state) => ({
  selectArr: state.borrowBook.selectArr,
  selectObj: state.borrowBook.selectObj,
  visibleModal: state.borrowBook.visibleModal,
  statistical: state.borrowBook.statistical,
})

const mapDispatch = (dispatch) => ({
  visibleModalFn(flag) {
    dispatch(actionCreators.visibleModalFn(flag))
  },
  createReadList(str) {
    dispatch(actionCreators.createReadList(str))
  }
})

export default connect(mapState, mapDispatch)(Main);
