import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { Modal, Button, Table, Row, Col, Radio, Divider, Input } from 'antd';
import { loStorage } from 'libs/storage.js';
import { actionCreators } from '../store';
import gun from 'imgs/gun.jpg';
const RadioGroup = Radio.Group
class Main extends Component {
  constructor(props) {
    super(props);
    this.myRef = React.createRef();
  }
  state = {
    category: null, // 1：幼儿园；2：社区馆；3：小学    
    loading: false,
    sourceList: [],
    value: ''
  }
  timer = null;

  componentDidMount() {
    let category = this.props.userInfo.eduInstitution.category;

    this.setState({
      category,
      sourceList: this.listColumns()
    })
    // 清空数据
    this.props.changeScanQrcodeList([])

  }

  hideModelFn = () => {
    this.props.setScanReturnAllBagsModel(false)
  }
  showModelFn = () => {
    this.props.setScanReturnAllBagsModel(true)
    this.setState({
      loading: false
    })
    setTimeout(() => {
      this.myRef.current.focus()
    }, 200)
  }

  // 改变书包中对应图书的归还状态
  onChangeBookState = (qrcodeNumber, value, iSBN) => {
    let list = this.props.scanQrcodeReturnList;

    for (let i = 0, len = list.length; i < len; i++) {

      if (qrcodeNumber === list[i].qrCodeNumber) {

        const arr = list[i].bookVos;

        for (let j = 0, leng = arr.length; j < leng; j++) {

          if (iSBN === arr[j].iSBN) {

            arr[j].bookState = value;

            break;
          }
        }
      }
    }

    this.props.changeScanQrcodeList(list)
  }

  // 确认归还
  sureReturnFn = () => {
    this.setState({
      loading: true
    })
    const { selectScanQrcode, scanQrcodeReturnList, returnScanMultipleBags } = this.props;
    const selectArr = selectScanQrcode
    const dataArr = scanQrcodeReturnList


    returnScanMultipleBags(selectArr, dataArr)
  }

  listColumns = () => {
    const { category } = this.state
    let list = [{
      title: '阅读包编号',
      dataIndex: 'bagNumber',
      render: text => <span className="l-bule">{text}</span>,
    },
    {
      title: '宝贝姓名',
      dataIndex: 'kiddieRealName',
    },
    {
      title: '家长手机号',
      dataIndex: 'householderMobile',
    }]
    const list2 = category === '2' ? [{
      title: '收货地址',
      dataIndex: 'address',
    }] :
      [
        {
          title: '年级',
          dataIndex: 'gradeName',
        },
        {
          title: '班级',
          dataIndex: 'className',
        },
      ]
    const actions = [{
      title: '图书信息',
      key: 'action',
      width: 450,
      render: (text, record) => {
        return (
          <Fragment>
            {record.bookVos.map((item, index) => {
              return <Row key={item.iSBN}>
                <Col span={2}>
                  <h3>{++index}:</h3>
                </Col>
                <Col span={22}>
                  <Row gutter={24}>
                    <Col span={12}>
                      <span className="l-mr3">{item.bookName}</span> ISBN:{item.iSBN}
                    </Col>
                    <Col span={12}>

                      <RadioGroup
                        key={record.qrCodeNumber}
                        onChange={(e) => this.onChangeBookState(record.qrCodeNumber, e.target.value, item.iSBN)}
                        value={item.bookState}

                      >
                        <Radio key={record.qrCodeNumber + '1'} className="l-mb2" value='2'>正常</Radio>
                        <Radio key={record.qrCodeNumber + '2'} className="l-mb2" value='3'>损坏</Radio>
                        <Radio key={record.qrCodeNumber + '3'} className="l-mb2" value='4'>丢失</Radio>
                      </RadioGroup>
                    </Col>
                  </Row>
                </Col>
                <Divider />
              </Row>
            })}
          </Fragment>
        )
      }
    }]
    return list.concat(list2, actions);
  }

  render() {
    const { loading, sourceList } = this.state
    const { scanQrcodeReturnList, returnScanBagsModelVisible, selectScanQrcode, changeSelectScanArrFn } = this.props;

    const rowSelection = {
      selectedRowKeys: selectScanQrcode,
      onSelect: (record, selected) => {
        let arr = JSON.parse(JSON.stringify(selectScanQrcode))
        if (selected) {
          arr.push(record.qrCodeNumber)
        } else {
          arr.splice(arr.findIndex(item => item === record.qrCodeNumber), 1)
        }
        changeSelectScanArrFn(arr)
      },
      onSelectAll: (selected, selectedRows, changeRow) => {
        let arr = JSON.parse(JSON.stringify(selectScanQrcode))
        if (selected) {
          selectedRows.map(item => arr.push(item.qrCodeNumber))
        } else {
          changeRow.map(item => arr.splice(arr.findIndex(ite => ite === item.qrCodeNumber), 1))
        }
        arr = [...new Set(arr)]
        changeSelectScanArrFn(arr)
      }
    };

    return (<Fragment>
      <Button className="l-mr2" type="primary" onClick={this.showModelFn}>扫码归还阅读包</Button>
      <Modal
        visible={returnScanBagsModelVisible}
        width="90%"
        title="扫码归还阅读包"
        onCancel={this.hideModelFn}
        footer={[
          <Button key="back" onClick={this.hideModelFn}>
            取消
            </Button>,
          <Button key="submit" type="primary" loading={loading} onClick={this.sureReturnFn}>
            确认归还
            </Button>,
        ]}
        destroyOnClose
      >
        <div className="l-mb3">
          <Row>
            <Col span={10}>
              <span className="l-bold l-mr3">阅读包编号:</span>
              <Input
                ref={this.myRef}
                style={{ background: `url(${gun}) no-repeat right top`, width: '300px' }}
                placeholder="扫描图书条形码进行补书"
                value={this.state.value}
                onChange={(e) => this.scanQrcodeBook(e.target.value)}
              />
            </Col>
            <Col span={14}>
              <div className="l-fz2">*点击扫码枪图标之后，拿起扫码枪对准书包二维码进行扫描即可</div>
              <div className="l-fz2">
                为给您提供更好的借阅体验，请检查阅读包内图书是否有损坏或者丢失</div>
            </Col>
          </Row>
        </div>
        <Table
          size='small'
          pagination={false}
          bordered={true}
          rowKey={r => r.qrCodeNumber}
          rowSelection={rowSelection}
          columns={sourceList}
          dataSource={scanQrcodeReturnList}
        />
      </Modal>
    </Fragment>);
  }

  scanQrcodeBook = (str) => {
    this.setState({ value: str });
    clearTimeout(this.timer)
    this.timer = setTimeout(() => {
      console.log('input扫码获取对应书包编码', str)
      // 发送请求获取
      this.props.requestScanQrcodeBags(str)
      this.setState({ value: '' });
    }, 100)
  }
}

const mapState = (state) => ({
  returnScanBagsModelVisible: state.returnBook.returnScanBagsModelVisible, // 弹窗显示隐藏
  userInfo: (loStorage.getItem('userMes') && loStorage.getItem('userMes').userInfo) || null,
  // selectQrcodeArr: state.returnBook.selectQrcodeArr, // 列表勾选的书包
  scanQrcodeReturnList: state.returnBook.scanQrcodeReturnList, // 当前弹窗接口获取的数据
  selectScanQrcode: state.returnBook.selectScanQrcode, // 当前弹窗勾选的书包
})

const mapDispatch = (dispatch) => ({
  // 请求书包
  requestScanQrcodeBags(str) {
    dispatch(actionCreators.requestScanQrcodeBags(str))
  },
  // 设置弹框展示
  setScanReturnAllBagsModel(flag) {
    dispatch(actionCreators.setScanReturnAllBagsModel(flag))
  },
  changeScanQrcodeList(list) {
    dispatch(actionCreators.changeScanQrcodeList(list))
  },
  // 弹窗勾选的书包
  changeSelectScanArrFn(selectArr) {
    dispatch(actionCreators.changeSelectScanArrFn(selectArr))
  },
  // 确认归还
  returnScanMultipleBags(selectArr, dataArr) {
    dispatch(actionCreators.returnScanMultipleBags(selectArr, dataArr))
  },
})

export default connect(mapState, mapDispatch)(Main);
