import React from 'react';
import {
  Form, Input, Button, Select, DatePicker
} from 'antd';
import { connect } from 'react-redux';
import { actionCreators } from '../store';
import { loStorage } from 'libs/storage.js';
import { actionCreators as actionCreatorsLogin } from 'pages/login/login/store';
const { RangePicker } = DatePicker;
const Option = Select.Option;

class MyForm extends React.Component {
  state = {
    grades: [],
    classes: []
  }

  handleSelectChange = value => {
    const { gradeClassInfos } = this.props.gradeAndClass;
    this.setState({
      classes: gradeClassInfos[value]
    })
    this.props.form.resetFields('classId');
  };

  render() {
    const { classes, grades } = this.state;
    const {
      getFieldDecorator,
    } = this.props.form;
    const rangePickerConfig = {
      normalize: function (value) {
        return value
      }
    }
    let category = this.props.userInfo.eduInstitution.category;

    return (
      <Form layout="inline" onSubmit={this.handleSubmit}>
        {category !== '2' && <Form.Item
          label="选择年级"
        >
          {
            getFieldDecorator('gradeId')(
              <Select
                style={{ width: 200 }}
                placeholder="请选择"
                allowClear
                onChange={this.handleSelectChange}
              >
                {
                  grades && grades.map((item) => {
                    return <Option key={item.sysNo} value={item.sysNo}>{item.name}</Option>
                  })
                }
              </Select>
            )
          }
        </Form.Item>}
        {category !== '2' && <Form.Item
          label="选择班级"
        >
          {
            getFieldDecorator('classId')(
              <Select
                style={{ width: 200 }}
                placeholder="请选择"
                allowClear
              >
                {
                  classes && classes.map((item) => {
                    return <Option key={item.sysNo} value={item.sysNo}>{item.name}</Option>
                  })
                }
              </Select>
            )
          }
        </Form.Item>
        }
        <Form.Item
          label="阅读包编号"
        >
          {
            getFieldDecorator('bagNumber')(
              <Input allowClear style={{ width: 200 }} placeholder="请输入阅读包编号" />
            )
          }
        </Form.Item>
        <Form.Item
          label="宝宝姓名"
        >
          {
            getFieldDecorator('kiddieName')(
              <Input allowClear style={{ width: 200 }} placeholder="请输入宝宝姓名" />
            )
          }
        </Form.Item>
        <Form.Item
          label="家长手机号"
        >
          {
            getFieldDecorator('householderMobile')(
              <Input allowClear style={{ width: 200 }} placeholder="请输入手机号" />
            )
          }
        </Form.Item>

        <Form.Item label="借阅时间">
          {getFieldDecorator('range', rangePickerConfig)(<RangePicker format="YYYY-MM-DD" />)}
        </Form.Item>

        <Form.Item>
          <Button
            type="primary"
            htmlType="submit"
          >
            查询
          </Button>
          {/* <Button style={{ marginLeft: 20 }} onClick={this.handleReset}>
            重置
          </Button> */}
        </Form.Item>
      </Form>
    );
  }

  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        if (values.range && values.range.length !== 0) {
          values.startDate = values.range[0].format('YYYY-MM-DD')
          values.endDate = values.range[1].format('YYYY-MM-DD')
          delete values.range
        }

        this.props.onSearch(values);
      }
    });
  }

  handleReset = () => {
    this.props.form.resetFields();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.props.onSearch(values);
      }
    });
  }
  componentDidMount() {
    this.props.onSearch()
    this.props.getAllClass()
    const obj = loStorage.getItem('allClass');
    if (!obj) return;
    let newGrades = obj.grades;

    const gradesList = obj.grades;

    if (gradesList.length > 0 && gradesList[gradesList.length - 1].sysNo !== '0') {
      newGrades = obj.grades.concat({
        sysNo: '0',
        name: '无'
      })
    }

    this.setState({
      grades: newGrades,
      gradeClassInfos: obj.gradeClassInfos
    })
  }
}

const WrappedMyForm = Form.create({})(MyForm);

const mapState = (state) => ({
  // classInfoList: state.returnBook.classInfoList,
  // searchDate: state.returnBook.searchDate,
  userInfo: state.login.userInfo || (loStorage.getItem('userMes') && loStorage.getItem('userMes').userInfo) || null,
  gradeAndClass: state.login.gradeAndClass || loStorage.getItem('allClass')
})

const mapDispatch = (dispatch) => ({
  onSearch(param) {
    dispatch(actionCreators.getTableList(param));
  },
  getAllClass() {
    dispatch(actionCreatorsLogin.getClass());
  }
})

export default connect(mapState, mapDispatch)(WrappedMyForm);
