import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { Button, Modal, Table } from 'antd';
import { actionCreators } from '../store';
let btnWrapStyle = { display: 'flex', justifyContent: 'center' }
let btnStyle = { marginTop: 30 }
const columns = [
  {
    title: '借阅包资产编号',
    dataIndex: 'bookBagId',
    width: 200,
  },
  {
    title: '借阅时间',
    dataIndex: 'startDate',
    width: 170,
  },
  {
    title: '归还时间',
    dataIndex: 'endDate',
    width: 170,
  },
  {
    title: '包含图书',
    dataIndex: 'bookList',
    render: (text, record) => {
      return <div>
        {
          record.bookList.map((item, index) => {
            return <div key={item.iSBN}>
              <span>{++index}、{item.bookName}</span>
              <span style={{ fontWeight: 'bold', marginLeft: 20 }}>ISBN:{item.iSBN}</span>
            </div>
          })
        }

      </div>
    }
  }
];
class BorrowList extends Component {
  handleCancel = () => {
    this.props.setBorrowListShow(false)
  }
  render() {
    const { borrowList, ifShowBorrow } = this.props;
    return (<Fragment>
      <Modal
        visible={ifShowBorrow}
        destroyOnClose={true}
        title="会员借阅记录"
        onCancel={this.handleCancel}
        maskClosable={false}
        footer={null}
        width="80%"
      >
        <Table columns={columns} dataSource={borrowList} scroll={{ y: 320 }} rowKey={record => record.bookBagId} pagination={false} />
        <div style={btnWrapStyle}>
          <Button onClick={() => { this.handleCancel() }} style={btnStyle}>关闭</Button>
        </div>
      </Modal>
    </Fragment>);
  }
}

const mapState = (state) => ({
  borrowList: state.seniorMember.borrowList,
  ifShowBorrow: state.seniorMember.ifShowBorrow,
})

const mapDispatch = (dispatch) => ({
  // handleCancel() {
  //   dispatch(actionCreators.stopUseCancel());
  // },
  setBorrowListShow(val) {
    dispatch(actionCreators.setBorrowListShowAction(val));
  },
})
export default connect(mapState, mapDispatch)(BorrowList);
