/**
 * 封装localStorage
 */
export const loStorage = {
  setItem(key, val) {
    return localStorage.setItem(key, typeof (val) == 'object' ? JSON.stringify(val) : val);
  },
  getItem(key) {
    return JSON.parse(localStorage.getItem(key));
  },
  removeItem(key) {
    return loStorage.removeItem(key)
  },
  clear() {
    localStorage.clear();
  }
}
/**
 * 封装sessionStorage
 */
export const seStorage = {
  setItem(key, val) {
    return sessionStorage.setItem(key, typeof (val) == 'object' ? JSON.stringify(val) : val);
  },
  getItem(key) {
    return JSON.parse(sessionStorage.getItem(key));
  },
  removeItem(key) {
    return loStorage.removeItem(key)
  },
  clear() {
    sessionStorage.clear();
  }
}
