import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { Input, Button, Modal, Form, message } from 'antd';
import { actionCreators } from '../store';
let btnWrapStyle = { display: 'flex', justifyContent: 'center' }
let btnStyle = { marginLeft: 10 }

const formItemLayout = {
  labelCol: {
    xs: { span: 24 },
    sm: { span: 6 },
  },
  wrapperCol: {
    xs: { span: 24 },
    sm: { span: 12 },
  },
};
class Main extends Component {

  render() {
    const { type, visibleEditOrAdd, name, phone } = this.props;
    const { getFieldDecorator } = this.props.form;
    const title = type === 'add' ? '添加教师' : '编辑教师';
    return (
      <Fragment>
        <Modal
          title={title}
          visible={visibleEditOrAdd}
          maskClosable={false}
          destroyOnClose={true}
          onCancel={this.handleCancel}
          footer={null}
        >
          <Form className="login-form" {...formItemLayout} labelAlign='left'>
            <Form.Item label="教师手机号">
              {getFieldDecorator('phone', {
                rules: [{ required: true, message: '请输入教师手机号' }]
                , initialValue: phone
              })(
                <Input placeholder="请输入教师手机号" />
              )}
            </Form.Item>
            <Form.Item label="教师姓名">
              {getFieldDecorator('name', {
                rules: [{ required: true, message: '请输入教师姓名' }], initialValue: name
              })(
                <Input placeholder="请输入教师姓名" />
              )}
            </Form.Item>
          </Form>
          <div style={btnWrapStyle}>
            <Button onClick={() => { this.handleCancel() }} type="primary">关闭</Button>
            <Button style={btnStyle} onClick={this.handleOk} type="primary">确定</Button>
          </div>
        </Modal>
      </Fragment>
    );
  }
  handleOk = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, v) => {
      
      const phone = v.phone;
      var reg = /^1\d{10}$/;
      if (!reg.test(phone)) {
        return message.error('手机格式错误，请重新输入！')
      }
      if (!err) {
        // props 提交 
        console.log('修改数据中', v)
        this.props.addOrEditTeacher(v)
      } 
    });
  }
  handleCancel = () => {
    this.props.hideEditOrAddFn()
  }
  componentDidMount() {
  }
}

const mapState = (state) => ({
  visibleEditOrAdd: state.teacher.visibleEditOrAdd,
  type: state.teacher.type,
  name: state.teacher.name,
  phone: state.teacher.phone
})

const mapDispatch = (dispatch) => ({
  hideEditOrAddFn() {
    dispatch(actionCreators.showOrHideEditOrAddFn(false))
  },
  addOrEditTeacher(v) {
    dispatch(actionCreators.addOrEditTeacher(v))
  }

})
let EidtModal = Form.create()(Main)
export default connect(mapState, mapDispatch)(EidtModal);
