import React, { Component, Fragment } from 'react';
import { Table, Icon, Alert, Button } from 'antd';
import { connect } from 'react-redux';
import { listColumns1, listColumns2 } from '../config';
import { actionCreators } from '../store';
import { loStorage } from 'libs/storage.js';
import { actionCreators as actionCreators2 } from '../../../../components/store';
import { actionCreators as actionCreatorsCommon } from 'components/store';
class Main extends Component {
  // 处理异常
  dealwithListColumns(list) {
    return list.map(item => {
      if (item.title === '阅读包') {
        item.render = (text, record) => {
          if (record.borrowState === '2') {
            return (<Fragment> {text || '暂无'}<Icon type="exclamation-circle" className="l-red l-ml1" theme="filled" /></Fragment>)
          } else {
            return text || '暂无'
          }
        }
      }
      return item
    })
  }
  clearSelected = () => {
    this.props.onSelectedRows([], [])
  }
  getBorrowList = (id) => {
    this.props.getBorrowList(id);
    this.props.showDialog(true)
  }
  render() {
    const { listData, userInfo, onSelectedRows, } = this.props;
    const { selectedRowKeys } = this.props;
    let listColumns;
    let category = userInfo.eduInstitution.category;
    listColumns = category === '2' ? listColumns2 : listColumns1;
    this.dealwithListColumns(listColumns)
    if (listColumns[listColumns.length - 1]['title'] !== '操作') {
      listColumns.push({
        title: '操作',
        key: 'action',
        width: 100,
        render: (text, record) => {
          return < Fragment >
            <Button className="l-mt2" type="primary" onClick={() => this.props.visibleSeniorRenewal(record.kiddieSysNo)}>续费</Button>
            <Button className="l-mt2" type="primary" onClick={() => this.props.showEditUser(true, record)}>编辑</Button>
            <Button className="l-mt2" type="primary" onClick={() => this.getBorrowList(record.kiddieSysNo)}>借阅记录</Button>
            {record.bookBagBorrowState === '2' && <Button className="l-mt2" type="primary" onClick={() => this.props.onViewErrorBooks(record)}>补书</Button>}
          </Fragment>
        }
      })
    }

    const rowSelection = {
      selectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        onSelectedRows(selectedRowKeys, selectedRows);
      }
    };
    const MessageText = () => {
      return (<div>
        <span className="l-mr2 l-hover"><Icon theme="filled" type="info-circle" /></span>
        已选择{selectedRowKeys.length}个宝宝
        <span className="l-ml4 l-hover" onClick={this.clearSelected}>清空选择</span>
      </div>);
    }
    return (
      <div className="l-mt2 l-mb2">
        {
          selectedRowKeys.length > 0 &&
          <Alert className="l-mb2" message={MessageText()} type="info" />
        }
        <Table
          rowKey={record => record.kiddieSysNo}
          columns={listColumns}
          dataSource={listData}
          rowSelection={rowSelection}
          bordered={true}
        />
      </div>
    );
  }
}

const mapState = (state) => ({
  userInfo: state.login.userInfo || (loStorage.getItem('userMes') && loStorage.getItem('userMes').userInfo) || null,
  listData: state.seniorMember.listData,
  selectedRowKeys: state.seniorMember.selectedRowKeys,
})

const mapDispatch = (dispatch) => ({
  // 补书
  onViewErrorBooks(bookBagSysNo) {
    dispatch(actionCreatorsCommon.getBooksData(bookBagSysNo.bookbagId, true));
  },
  onSelectedRows(selectedRowKeys, selectedRows) {
    dispatch(actionCreators.selectColumns(selectedRowKeys, selectedRows));
  },
  visibleSeniorRenewal(kiddieSysNo) {
    dispatch(actionCreators.visibleSeniorRenewalModal(kiddieSysNo));
  },
  getBorrowList(familyId) {
    dispatch(actionCreators.getBorrowList(familyId));
  },
  showDialog(data) {
    dispatch(actionCreators.setBorrowListShowAction(data));
  },
  showEditUser(val, data) {
    dispatch(actionCreators2.showEditUser(val, data));
  },
})
export default connect(mapState, mapDispatch)(Main);
