import * as actionTypes from './actionTypes';

const defaultState={
  statistical:{},
  schoolList:[
    {
      name:'2016届小1班',
      code:'FQW211',
      num:'99'
    },
    {
      name:'2016届小1班',
      code:'FQW211',
      num:'99'
    }, {
      name:'2016届小1班',
      code:'FQW211',
      num:'99'
    }, {
      name:'2016届小1班',
      code:'FQW211',
      num:'99'
    }, {
      name:'2016届小1班',
      code:'FQW211',
      num:'99'
    }, {
      name:'2016届小1班',
      code:'FQW211',
      num:'99'
    },
  ],
  imgStr:'',
  roomList: {},
  codeList: '', // 二维码
  gradesList: [],
  changeObj:{},
  delObj: {},
  editingKey: ''
}

export default (state=defaultState,action) => {
  switch(action.type) {
    case actionTypes.STATISTICAL :
    return {
      ...state,
      statistical: action.data
    };
    case actionTypes.GETCODEIMG :
    return {
      ...state,
      imgStr: action.data
    }
    //获取班级详细信息
    case actionTypes.SETCLASSROOMS :
      return {
        ...state,
        roomList: action.data
      }
    
      // 获取二维码
      case actionTypes.SETCODE :
        return {
          ...state,
          codeList: action.data
        }

        // 获取年级列表
      case actionTypes.STAGRADES :
        return {
          ...state,
          gradesList: action.data
        }
        // 修改班级信息
      case actionTypes.CHANG_OBJ_CLASS :
          return {
            ...state,
            changeObj: action.data
          }
          // 修改班级信息
      case actionTypes.DEL_OBJ_CLASS :
            return {
              ...state,
              delObj: action.data
            }
      case actionTypes.CHANG_EDITING_KEY :
            return {
              ...state,
              editingKey: action.data
            }
    default :
      return state;
  }
}
