import * as actionTypes from './actionTypes';

const defaultState = {
  listData: [],
  selectedRowKeys: [],
  selectQrcodeArr: [],
  putInStorageVisible: false,
  qrCodeNumber: '',
  willInBooksData: [],
  willInBooksDataObj: {},
  qrcodeReturnList: [],
  scanQrcodeReturnList: [],
  CHANGE_SCAN_QRCODE_LIST: [],
  returnAllBagsModelVisible: false, // 批量归还书包弹窗是否显示
  returnScanBagsModelVisible: false, // 扫码归还书包弹窗是否显示
  selectAllQrcode: [], //批量归还书包弹窗 勾选的书包
  selectScanQrcode: [], //扫码归还书包弹窗 勾选的书包
}

export default (state = defaultState, action) => {
  switch (action.type) {
    case actionTypes.SET_LIST:
      return {
        ...state,
        listData: action.data
      };
    case actionTypes.SELECT_COLUMNS:
      return {
        ...state,
        selectedRowKeys: action.selectedRowKeys,
        selectedRows: action.selectedRows
      }
    case actionTypes.CLEAR_SELECTED_ROW_KEYS:
      return {
        ...state,
        selectedRowKeys: [],
        selectedRows: []
      }
    case actionTypes.CHANGE_SELECT_QRCODE_ARR:
      return {
        ...state,
        selectQrcodeArr: action.arr
      }
    case actionTypes.VISIBLE_PUT_IN_MODAL:
      return {
        ...state,
        putInStorageVisible: true,
        qrCodeNumber: action.data
      }
    case actionTypes.CANCEL_PUT_IN:
      return {
        ...state,
        putInStorageVisible: false
      }
    case actionTypes.STORE_WILL_IN_BOOKS_DATA:

      return {
        ...state,
        willInBooksData: action.data.bookVos,
        willInBooksDataObj: action.data
      }
    case actionTypes.CHANGE_BOOK_STATE:
      return changeBookState(state, action);
    case actionTypes.CLEAR_IN_DATAS:
      return {
        ...state,
        qrCodeNumber: '',
        willInBooksData: [],
        willInBooksDataObj: {},
      };
      case actionTypes.SET_RETURN_ALLBAGS_MODEL:      
      return {
        ...state,
        returnAllBagsModelVisible: action.flag
      }
      case actionTypes.SET_SCAN_RETURN_BAGS_MODEL:      
      return {
        ...state,
        returnScanBagsModelVisible: action.flag
      }
      case actionTypes.CHANGE_QRCODE_LIST:      
      return {
        ...state,
        qrcodeReturnList: action.arr
      }
      case actionTypes.CHANGE_SCAN_QRCODE_LIST:      
      return {
        ...state,
        scanQrcodeReturnList: action.arr
      }
      case actionTypes.SELECT_ALL_Qrcode:      
      return {
        ...state,
        selectAllQrcode: action.data
      }
      case actionTypes.SELECT_SCAN_Qrcode:      
      return {
        ...state,
        selectScanQrcode: action.data
      }
    default:
      return state;
  }
}

function changeBookState(state, action) {

  let willInBooksData = [...state.willInBooksData];
  let newData = willInBooksData.map((d, i) => {
    if (d.bookBagDetailSysNo === action.item.bookBagDetailSysNo) {
      d.bookState = action.bookState;
    }
    return d;
  });
  return {
    ...state,
    willInBooksData: newData
  }
}
