import React, {
} from 'react';
import ReactDOM from 'react-dom';
import {
  BrowserRouter as Router, Route, Switch
} from 'react-router-dom';
import { Provider } from 'react-redux';
import store from './store';
import PageLayout from 'components/layout.js';
import Login from 'pages/login/login';
import Index from 'pages/index/index';

import ForgetPassword from 'pages/login/forgetPassword';
import SetNewPassword from 'pages/login/setNewPassword';
import SetCenter from 'pages/index/setCenter';
import NoMatch from 'pages/noMatch';
import BookManagement from 'pages/assetsManagement/bookManagement'
import ReadPackageManegement from 'pages/assetsManagement/readPackageManegement'
import BorrowBook from 'pages/borrowManagement/borrowBook'
import BorrowHistory from 'pages/borrowManagement/borrowHistory'
import BorrowHistoryDetail from 'pages/borrowManagement/borrowHistory/pages/detail'
import FillBook from 'pages/borrowManagement/fillBook'
import ReturnBook from 'pages/borrowManagement/returnBook'
import AllMember from 'pages/memberManagement/allMember'
import RegularMember from 'pages/memberManagement/regularMember'
import AddMultiRegularMember from 'pages/memberManagement/regularMember/pages/addMultiRegularMember'
import SeniorMember from 'pages/memberManagement/seniorMember'
import Teacher from 'pages/teacherManagement/teacher'


import { LocaleProvider } from 'antd';
import zh_CN from 'antd/lib/locale-provider/zh_CN';
import moment from 'moment';
import 'moment/locale/zh-cn';
import './less/index.less';
moment.locale('zh-cn');
const RouterDom = () => {
  return (
    <LocaleProvider locale={zh_CN}>
      <Provider store={store}>
        <Router>
          <Switch>
            <Route path="/" exact component={Login} />
            <Route path="/forgetPassword" exact component={ForgetPassword} />
            <Route path="/setNewPassword/:username" exact component={SetNewPassword} />
            <PageLayout>
              <Route path="/index" exact component={Index} />
              <Route path="/bookManagement" exact component={BookManagement} />
              <Route path="/readPackageManegement" exact component={ReadPackageManegement} />
              <Route path="/borrowBook" exact component={BorrowBook} />
              <Route path="/borrowHistory" exact component={BorrowHistory} />
              <Route path="/borrowHistory/detail" exact component={BorrowHistoryDetail} />
              <Route path="/fillBook" exact component={FillBook} />
              <Route path="/returnBook" exact component={ReturnBook} />
              <Route path="/allMember" exact component={AllMember} />
              <Route path="/regularMember" exact component={RegularMember} />
              <Route path="/regularMember/addMultiRegularMember" exact component={AddMultiRegularMember} />
              <Route path="/seniorMember" exact component={SeniorMember} />
              <Route path="/teacher" exact component={Teacher} />
              <Route path="/setCenter" exact component={SetCenter} />
              <Route path="/404" component={NoMatch} />
              <Route component={NoMatch} />             
            </PageLayout>
          </Switch>
        </Router>
      </Provider>
    </LocaleProvider>

  );
}

ReactDOM.render(<RouterDom />, document.getElementById('root'));
