import React, {
  Component,
  Fragment
} from 'react';
import { connect } from 'react-redux';
import { Modal } from 'antd';
import { actionCreators } from '../store';
import { withRouter } from 'react-router-dom';
class Main extends Component {
  state = {
  }

  render() {
    const { visibleModal2 } = this.props;
    return <Fragment>
      <Modal
        title="生成借阅单结果"
        okText="立即查看"
        visible={visibleModal2}
        onOk={this.handleOk}
        onCancel={this.handleCancel}
        destroyOnClose={true}
        width="60%"
      >
        <div>
          <div>借阅单已生成，
请前往【借阅单历史记录】
查看阅读包分配详情。</div>
        </div>
      </Modal>
    </Fragment>
  }
  handleOk = () => {
    // this.props.history.push('/borrowHistory')
    this.props.visibleModal2Fn(false)
  };

  handleCancel = () => {
    this.props.visibleModal2Fn(false)
  };
  componentDidMount() {

  }
}


const mapState = (state) => ({
  visibleModal2: state.borrowBook.visibleModal2,
})

const mapDispatch = (dispatch) => ({
  visibleModal2Fn(flag) {
    dispatch(actionCreators.visibleModal2Fn(flag))
  }
})

export default connect(mapState, mapDispatch)(withRouter(Main));
