import React, { Component } from 'react';
import { Button } from 'antd';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';

class Main extends Component {
  render() {
    return (<Button className="l-mr2" type="primary" style={{ marginLeft: '15px' }}
      onClick={this.goTo}>批量添加普通会员</Button>);
  }

  goTo = () => {
    this.props.history.push({ pathname: '/regularMember/addMultiRegularMember' })
  }
}

const mapState = (state) => ({
})

export default connect(mapState, null)(withRouter(Main));
