// import qs from 'qs';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Button } from 'antd';
import { host, sessionId } from 'libs/io.js';
import axios from 'axios';
import { loStorage } from 'libs/storage.js';


class Main extends Component {
  render() {
    return (<Button onClick={this.exportFiles}>下载借阅单</Button>);
  }

  
  exportFiles = () => {
    let url = host + '/bookbag/export_bookbag_to_pdf?x-auth-token=' + sessionId;
    let fileName = loStorage.getItem('userMes')['userInfo']['eduInstitution']['name'];
    axios({
      method: 'get',
      url: url, 
      headers: { 'x-auth-token': loStorage.getItem('userMes') && loStorage.getItem('userMes').x_auth_token },
      responseType: 'blob' // 表明返回服务器返回的数据类型
    })
      .then((res) => { // 处理返回的文件流
        if (!res.data) {
          return
        }
        let url = window.URL.createObjectURL(new Blob([res.data]))
        let link = document.createElement('a')
        link.style.display = 'none'
        link.href = url
        link.setAttribute('download', fileName+'-归还书包.pdf')
        document.body.appendChild(link)
        link.click()
      })
  }
}

const mapState = (state) => ({
  // searchParam: state.returnBook.searchParam
})

const mapDispatch = (dispatch) => ({

})

export default connect(mapState, mapDispatch)(Main);
