import React, { Component, Fragment } from 'react';
import { Modal, Radio, message } from 'antd';
import { connect } from 'react-redux';
import { actionCreators } from '../store';
import { ajaxAxios } from 'libs/io.js';

class Main extends Component {
  state = {
    value: 2,
    payType: 2
  }
  handleOk1 = e => {
    const { value } = this.state;
    const { semesterNum, yearNum, kiddieId } = this.props;

    if (value === 2 && semesterNum === '0') {
      return message.error('半年名额不足')
    }

    if (value === 3 && yearNum === '0') {
      return message.error('全年名额不足')
    }

    this.props.upMember(value, kiddieId)

  };

  handleCancel1 = e => {
    this.props.showUpMemberModal(false)
  };
  onChange1 = e => {
    this.setState({
      value: e.target.value,
    });
  };

  onChange2 = e => {
    this.setState({
      payType: e.target.value,
    });
  };
  handleOk2 = e => {

    const { payType } = this.state;
    const { semesterNum, yearNum, selectedRowKeys } = this.props;
    if (payType === 2 && selectedRowKeys.length > semesterNum) {
      return message.error('半年名额不足，请重新选择人数')
    }
    if (payType === 3 && selectedRowKeys.length > yearNum) {
      return message.error('全年名额不足，请重新选择人数')
    }
    this.props.upBatchMember(payType, selectedRowKeys)
    this.props.showDialog(false)

  };

  handleCancel2 = e => {
    this.props.showDialog(false)
  };
  render() {
    let { visibleUpMemberModal, visibleBatchUpMemberModal, selectedRowKeys, semesterNum, yearNum } = this.props;
    const radioStyle = {
      display: 'block',
      height: '30px',
      lineHeight: '30px',
      marginLeft: '40px'
    };
    const Style1 = {
      display: 'inline-block'
    };
    const Style2 = {
      display: 'inline-block',
      marginLeft: '30px'
    };
    return (
      <Fragment>
        <Modal
          title="升级会员"
          visible={visibleUpMemberModal}
          onOk={this.handleOk1}
          onCancel={this.handleCancel1}
          maskClosable={false}
          width="300px"
        >
          <Radio.Group onChange={this.onChange1} value={this.state.value}>
            <Radio style={radioStyle} value={2}>
              <p style={Style1}>半年</p>
              <p style={Style2}>剩余名额：<span>{semesterNum}</span></p>
            </Radio>
            <Radio style={radioStyle} value={3}>
              <p style={Style1}>全年</p>
              <p style={Style2}>剩余名额：<span>{yearNum}</span></p>
            </Radio>
          </Radio.Group>
        </Modal>
        <Modal
          title="批量升级"
          visible={visibleBatchUpMemberModal}
          onOk={this.handleOk2}
          onCancel={this.handleCancel2}
          maskClosable={false}
          width="350px"
        >
          <div>
            <div>
              <p style={Style2}>剩余名额：</p>
              <p style={Style1}>【半年】：<span>{semesterNum}</span></p>
              <p style={Style1}>【全年】：<span>{yearNum}</span></p>
            </div>
            <div style={{ marginLeft: '30px' }}>已选择人数{selectedRowKeys.length}人</div>
            <div style={{ marginLeft: '30px', marginTop: '15px' }}>高级会员续费设置：</div>
          </div>
          <Radio.Group onChange={this.onChange2} value={this.state.payType}>
            <Radio style={radioStyle} value={2}>
              <p style={Style1}>半年</p>
            </Radio>
            <Radio style={radioStyle} value={3}>
              <p style={Style1}>一年</p>
            </Radio>
          </Radio.Group>
        </Modal>
      </Fragment>
    );
  }
  componentDidMount() {
    this.props.getResidue()
  }
}

const mapState = (state) => ({
  visibleUpMemberModal: state.regularMember.visibleUpMemberModal,
  visibleBatchUpMemberModal: state.regularMember.visibleBatchUpMemberModal,
  semesterNum: state.regularMember.semesterNum,
  yearNum: state.regularMember.yearNum,
  kiddieId: state.regularMember.kiddieId,
  selectedRowKeys: state.regularMember.selectedRowKeys,
})

const mapDispatch = (dispatch) => ({
  showUpMemberModal(flag) {
    dispatch(actionCreators.visibleUpMemberModal(flag));
  },
  showDialog(data) {
    dispatch(actionCreators.visibleBatchUpMemberModal(data))
  },
  getResidue() {
    dispatch(actionCreators.getResidue())
  },
  // 升级会员
  upMember(payType, kiddieId) {
    ajaxAxios({
      url: '/curator/familymembership/upgradeMember',
      data: {
        payType,
        kiddieId
      }
    }).then((res) => {
      if (res.result) {
        message.success('升级成功');
        dispatch(actionCreators.visibleUpMemberModal(false))
        dispatch(actionCreators.getTableListPre())
      } else {
        message.warning(res.logMessage);
      }
    });
  },
  // 批量升级会员
  upBatchMember(payType, kiddieIds) {
    const id = kiddieIds.join()
    ajaxAxios({
      url: '/curator/familymembership/batchUpgradeMember',
      data: {
        payType,
        kiddieIds: id
      }
    }).then((res) => {
      if (res.result) {
        message.success('升级成功');
        dispatch(actionCreators.getResidue())
        dispatch(actionCreators.visibleUpMemberModal(false))
        dispatch(actionCreators.getTableListPre())
        dispatch(actionCreators.selectColumns([], []))
      } else {
        message.warning(res.logMessage);
      }
    });
  }
})
export default connect(mapState, mapDispatch)(Main);
