import {
  ajaxAxios
} from 'libs/io.js';
import { message } from 'antd';
import * as actionTypes from './actionTypes';

let params = {}
export const getTableList = (param) => {
  params = param
  return dispatch => {
    ajaxAxios({
      url: '/member/pageKiddieVOByCnd',
      method: 'get',
      params: {
        type: 0,
        currentPage: 1,
        pageSize: 9999,
        ...params
      }
    }).then((res) => {
      if (res.success) {
        dispatch(updateData(res.result.records));
      } else {
        res.logMessage && message.error(res.logMessage);
      };
    });
  }
}

const updateData = (data) => {
  return {
    type: actionTypes.UPDATE_TABLE_LIST_DATA,
    data,
  }
};

// 列表勾选的数据  
export const setSelectArr = (kiddleIdArr, objArr) => {
  return {
    type: actionTypes.CHANGE_SELECT_ARRAY,
    selectedRowKeysArr: kiddleIdArr,
    selectedRowsARR: objArr
  }
}

// 批量更换班级
export const changeClassFn = (selectArr, classId) => {
  return dispatch => {
    ajaxAxios({
      url: '/curator/member/batchClassChange',
      data: {
        kiddieIds: selectArr.join(),
        classId
      }
    }).then((res) => {
      if (res.success) {
        dispatch(getTableList(params))
        dispatch(setSelectArr([], []));
      } else {
        res.logMessage && message.error(res.logMessage);
      };
    });
  }
}

export const getTableListPre = () => {
  return (dispatch) => {
    dispatch(getTableList(params))
  }
}
