import React, { Fragment } from 'react';
import { Table, Input, Select, Popconfirm, Form, message, Button } from 'antd';
import { connect } from 'react-redux';
import { loStorage } from 'libs/storage.js';
import { actionCreators } from '../store';

const Option = Select.Option;

let gradesLists = null;
const EditableContext = React.createContext();
class EditableCell extends React.Component {
  state = {
    gradesList: null
  }
  getInput = () => {
    if (this.props.inputType === 'select') {

      return <Select
        onChange={this.handleChange}
      >
        {gradesLists ? gradesLists.grades.map((item, index) => {
          return <Option key={index} value={item.name}>{item.name}</Option>
        }) : null}

      </Select>
    }
    return <Input />;
  };

  // 下拉列表改变年级
  handleChange = (value) => {

    let target = gradesLists.grades.filter(item => {
      return item.name === value
    })

    target.map(item => {
      // 找到年级id存储到本地
      return loStorage.setItem('gradeId', item.sysNo)
    })
  }
  renderCell = ({ getFieldDecorator }) => {
    const {
      editing,
      dataIndex,
      title,
      inputType,
      record,
      index,
      children,
      ...restProps
    } = this.props;
    return (
      <td {...restProps}>
        {editing ? (
          <Form.Item style={{ margin: 0 }}>
            {getFieldDecorator(dataIndex, {
              rules: [
                {
                  required: true,
                  message: `请输入 ${title}!`,
                },
              ],
              initialValue: record[dataIndex],
            })(this.getInput())}
          </Form.Item>
        ) : (
            children
          )}
      </td>
    );
  };

  render() {

    return <EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>;
  }
}


class EditableTable extends React.Component {
  constructor(props) {
    super(props);
    this.state = { data: null, canAdd: true };
    this.columns = [
      {
        title: '年级',
        dataIndex: 'gradeName',
        key: 'gradeName',
        align: 'center',
        width: '30%',
        editable: true,
        render: text => <span className="l-hover">{text}</span>,
      },
      {
        title: '班级',
        dataIndex: 'className',
        key: 'className',
        align: 'center',
        width: '25%',
        editable: true,
        render: text => <span className="l-hover">{text}</span>,
      },
      {
        title: '用户人数',
        dataIndex: 'userCount',
        key: 'userCount',
        align: 'center',
        width: '20%',
        editable: false,
        render: text => <span className="l-hover">{text}</span>,
      },
      {
        title: '操作',
        dataIndex: 'operation',
        render: (text, record) => {
          const { editingKey } = this.props;
          const editable = this.isEditing(record);
          return editable ? (
            <span>
              <EditableContext.Consumer>
                {form => (
                  <span className="l-hover"
                    onClick={() => this.save(form, record.sysNo)}
                    style={{ marginRight: 8 }}
                  >
                    保存
                  </span>
                )}
              </EditableContext.Consumer>
              <span className="l-hover" onClick={() => this.cancel(record)}>
                取消
            </span>

            </span>
          ) : (
              <Fragment>
                <span className="l-hover l-mr3" disabled={editingKey !== ''} onClick={() => this.edit(record.sysNo, record)}>
                  编辑
            </span>

                <Popconfirm title="您确认要删除吗？" onConfirm={() => this.deleteFn(record)}>
                  <span className="l-hover">删除</span>
                </Popconfirm>
              </Fragment>
            );
        },
      },
    ];
  }

  isEditing = record => record.sysNo === this.props.editingKey;

  cancel = (record) => {
    // 点击取消的时候删掉新增的
    if (record.isAdd === true) {
      const newData = this.state.data.filter(item => {
        return item.isAdd !== true
      })
      this.setState({
        data: newData,
        canAdd: true
      })
    }
    this.props.changeEditingKey('')
  };

  save = (form, sysNo) => {
    let { changeObj } = this.props;
    form.validateFields((error, row) => {
      // console.log(row)
      if (row.className.length > 10) {
        message.info('班级名字不能超过10个字！')
        return
      }
      if (error) {
        return;
      }
      const newData = [...this.state.data];
      const index = newData.findIndex(item => sysNo === item.sysNo);
      const i = index + '';

      const item = newData[index];
      const gradeId = loStorage.getItem('gradeId')

      // console.log('新的值',item)
      if (index > -1) {
        changeObj[i] = {
          isAdd: item.isAdd,
          sysNo: item.sysNo,
          gradeId,
          className: row.className
        }
        newData.splice(index, 1, {
          ...item,
          ...row,
        });
        loStorage.setItem('changeObj', changeObj)
        this.props.changObjFn(changeObj)
      }

      this.setState({ data: newData, canAdd: true });
      this.props.changeEditingKey('')

    });
  }

  edit(key, record) {
    if (record.sysNo === '0') return message.info('无年级不允许修改')

    this.props.changeEditingKey(key)

    // this.setState({ editingKey: key });

  }

  // 点击删除班级
  deleteFn(record) {
    let { delObj } = this.props;
    console.log(typeof record.userCount)
    if (record.userCount === '0') {
      const newData = [...this.state.data];
      const index = newData.findIndex(item => record.sysNo === item.sysNo);

      // 数据删除掉
      newData.splice(index, 1);
      this.setState({
        data: newData
      })
      const i = index + '';
      delObj[i] = record.sysNo
      this.props.changObjFn(delObj)
      loStorage.setItem('delObj', delObj)
    } else if (record.userCount > '0') {
      message.error('该班级存在用户，暂时无法删除!')
    }
  }
  componentDidMount() {
    gradesLists = loStorage.getItem('allClass')
    this.props.getClassrooms()
    localStorage.removeItem('delObj')
    loStorage.setItem('changeObj', {})
    this.props.changObjFn({})
    this.setState({
      data: this.props.roomList.classInfoVoList || []
    })
  }

  addDataList = () => {
    const { data, canAdd } = this.state;
    
    if (!canAdd) return;
    const sysNo = +new Date();
    console.log('data', data)
    data.unshift({ sysNo, isAdd: true, userCount: '0', className: '', gradesLists: this.props.gradeAndClass })

    this.setState({
      data,
      canAdd: false
      // editingKey: sysNo
    })
    this.props.changeEditingKey(sysNo)
  }

  render() {
    const { data } = this.state;
    // 过滤出年级中没有无的数据
    const dataSource = data && data.filter(item => item.sysNo !== '0')
    const components = {
      body: {
        cell: EditableCell,
      },
    };


    const columns = this.columns.map(col => {
      if (!col.editable) {
        return col;
      }
      return {
        ...col,
        onCell: record => ({
          record,
          inputType: col.dataIndex === 'gradeName' ? 'select' : 'input',
          dataIndex: col.dataIndex,
          title: col.title,
          editing: this.isEditing(record),
        })
      };
    });

    return (

      <EditableContext.Provider value={this.props.form}>
        <Table
          components={components}
          bordered
          dataSource={dataSource}
          columns={columns}
          rowClassName="editable-row"
          pagination={false}
          scroll={{ y: 165 }}
          rowKey={record => record.sysNo}
        />
        <div className="btn">
          <Button type="primary" onClick={this.addDataList}>
            添加班级
          </Button>
        </div>
      </EditableContext.Provider>
    );
  }
}



const EditableFormTable = Form.create()(EditableTable);

const Libarary = Form.create()(EditableFormTable)

let mapStateToProps = (state) => ({
  userInfo: state.login.userInfo || (loStorage.getItem('userMes') && loStorage.getItem('userMes').userInfo) || null,
  roomList: state.index.roomList || loStorage.getItem('indexRoomList') || [],
  changeObj: state.index.changeObj,
  delObj: state.index.delObj,
  editingKey: state.index.editingKey,
  gradeAndClass: state.login.gradeAndClass || loStorage.getItem('allClass') || []
})

let mapDispatchToProps = (dispatch) => {
  return {
    getClassrooms() {
      dispatch(actionCreators.getClassrooms());
    },
    changObjFn(obj) {
      dispatch(actionCreators.changObjFn(obj))
    },
    delObjFn(obj) {
      dispatch(actionCreators.delObjFn(obj))
    },
    changeEditingKey(key) {
      dispatch(actionCreators.changeEditingKey(key))
    }

  }
}
export default connect(mapStateToProps, mapDispatchToProps)(Libarary)
