import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import {
  Form,  Button, DatePicker
} from 'antd';
import { actionCreators } from '../../../store';
const { RangePicker } = DatePicker;
class Main extends Component {

  render() {
    const {
      getFieldDecorator,
    } = this.props.form;

    return (
      <Fragment >
        <Form className="l-mb3" layout="inline" onSubmit={this.handleSubmit}>
          <Form.Item label="生成时间">
            {getFieldDecorator('range-picker')(<RangePicker />)}
          </Form.Item>
          {/* <Form.Item
            label="操作人"
          >{getFieldDecorator('managerName')(<Input allowClear placeholder="请输入操作人姓名" />)}
          </Form.Item>*/}
          <Form.Item> 
            <Button type="primary" htmlType="submit">查询</Button>
          </Form.Item>
        </Form>
      </Fragment>);
  }
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, fieldsValue) => {

      const rangeValue = fieldsValue['range-picker'] || [];
      const startDateStr = rangeValue.length > 0 ? rangeValue[0].format('YYYY-MM-DD') : ''
      const endDateStr = rangeValue.length > 0 ? rangeValue[1].format('YYYY-MM-DD') : ''

      let values = {
        ...fieldsValue,
        startDateStr,
        endDateStr
      };
      delete values['range-picker']
      if (!err) {
        this.props.onSearch(values);
      }
    });
  }
  componentDidMount() {
    this.props.onSearch()
  }
}

const WrappedMyForm = Form.create({
  name: 'addMultiRegularMember-search'
})(Main);

let mapStateToProps = (state) => ({
})
let mapDispatchToProps = (dispatch) => {
  return {
    onSearch(param) {
      dispatch(actionCreators.onSearchAddMulti(param));
    }
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(WrappedMyForm);
