import React from 'react';
import { connect } from 'react-redux';
import {
    Form, Input, Button, Select
} from 'antd';
import { actionCreators } from '../store';
import { ajaxAxios } from 'libs/io.js';

const Option = Select.Option;

class MyForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            types: []
        }
    }

    render() {
        const {
            getFieldDecorator
        } = this.props.form;

        return (
            <Form layout="inline" onSubmit={this.handleSubmit}>
                <Form.Item
                    label="ISBN"
                >
                    {
                        getFieldDecorator('isbn')(
                            <Input allowClear style={{ width: 200 }} placeholder="请输入ISBN" />
                        )
                    }
                </Form.Item>                
                
                <Form.Item
                    label="书名"
                >
                    {
                        getFieldDecorator('bookName')(
                            <Input allowClear style={{ width: 200 }} placeholder="请输入书名" />
                        )
                    }
                </Form.Item>
                <Form.Item
                    label="状态"
                >
                    {
                        getFieldDecorator('bookState')(
                            <Select
                                style={{ width: 200 }}
                                placeholder="请选择"
                                allowClear
                            >
                                <Option value="2">正常</Option>
                                <Option value="3">破损</Option>
                                <Option value="4">丢失</Option>
                            </Select>
                        )
                    }
                </Form.Item>
                <Form.Item>
                    <Button
                        type="primary"
                        htmlType="submit"
                    >
                        查询
                    </Button>
                    {/* <Button style={{ marginLeft: 20 }} onClick={this.handleReset}>
                        重置
                    </Button> */}
                </Form.Item>
            </Form>
        );
    }

    componentDidMount() {
        this.getTypes();
    }

    getTypes = () => {
        ajaxAxios({
            url: '/book/findBookCategory'
        }).then((res) => {
            if (res.result) {
                this.setState(() => ({
                    types: res.result
                }));
            }
        });
    }
    handleSubmit = (e) => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                this.props.onSearch(values);
            }
        });
    }
    handleReset = (e) => {
        this.props.form.resetFields();
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                this.props.onSearch(values);
            }
        });
    }
}

const WrappedMyForm = Form.create({
    onFieldsChange(props, fields) {
        props.onChangeSearchForm(props.form.getFieldsValue());
    }
})(MyForm);

const mapState = (state) => ({
    searchParam: state.bookManagement.searchParam
});

const mapDispatch = (dispatch) => ({
    onChangeSearchForm(data) {
        dispatch(actionCreators.updateSearchFormData(data));
    },
    onSearch(param) {
        dispatch(actionCreators.getTableData(param));
    },
})

export default connect(mapState, mapDispatch)(WrappedMyForm);
