import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import moment from 'moment';
import {
  Input, Button, Modal,
  Select,
  Form, DatePicker, Radio, message,
  Row
} from 'antd';
import { areaList } from 'libs/area.js';
import { ajaxAxios } from 'libs/io.js';
import * as actionCreators from './store/actionCreators';
import { loStorage } from 'libs/storage.js';
let btnWrapStyle = { display: 'flex', justifyContent: 'center' }
let btnStyle = { marginLeft: 10 }
const Option = Select.Option;

const formItemLayout = {
  labelCol: {
    xs: { span: 24 },
    sm: { span: 6 },
  },
  wrapperCol: {
    xs: { span: 24 },
    sm: { span: 12 },
  },
};

class EditUser extends Component {
  constructor(props) {
    super(props);
    this.state = {
      classes: [],
      cities: [],
      counties: [],
    }
  }

  componentDidMount() {
    const category = this.props.userInfo.eduInstitution.category;    
    const { editUserData } = this.props;
    // 社区馆 需要查询接口获取用户的  部分数据
    if (category === '2') {
      this.props.getOtherData(editUserData.kiddieSysNo || editUserData.kiddleId)
    }
    
  };

  handleSelectChange = value => {
    if (!value) return;
    const { gradeClassInfos } = this.props.gradeAndClass;

    this.setState({
      classes: gradeClassInfos[value]
    })

    // this.props.form.setFields({ 'classId': null });
    this.props.form.setFieldsValue({ 'classId': null });
  };

  handleSubmit = e => {
    // if(!this.props.editUserData.bagNumber.length>0){
    //   message.info('该会员暂未分配到借阅包，暂时无法变更班级，请对该会员分配完借阅包之后再变更班级信息');
    // }

    e.preventDefault();

    const { editUserData } = this.props;
    const category = this.props.userInfo.eduInstitution.category;
    this.props.form.validateFields((err, values) => {

      let submitData = JSON.parse(JSON.stringify(values))

      submitData.birthdayStr = values['birthdayStr'] ? values['birthdayStr'].format('YYYY-MM-DD') : null;

      submitData.kiddieId = editUserData.kiddieSysNo || editUserData.kiddieId

      if (submitData.classId === editUserData.className) {
        submitData.classId = editUserData.classId
      }

      if (submitData.gradeId === editUserData.gradeName) {
        submitData.gradeId = editUserData.gradeId
      }

      if (category === '2' && !/^1\d{10}$/.test(submitData.adPhone)) {
        return message.error('手机号码错误，请重新输入')
      }

      if (err) return console.log(err)

      this.submitChangeBaby(submitData)
    });
  };

  handleOk = () => {
    this.props.showEditUser(false, {})
  }
  handleCancel = () => {
    this.props.showEditUser(false, {})
  }
  onSexChange = (e) => {
    this.setState({
      sex: e.target.value
    })
  }
  onDateChange = (date, birthday) => {
    this.setState({
      birthday
    })
  }

  render() {
    const { editUserData, otherData } = this.props;
    const { grades } = this.props.gradeAndClass;
    const { classes, cities, counties } = this.state;
    const { getFieldDecorator } = this.props.form;
    let category = this.props.userInfo.eduInstitution.category;
    return (
      <Fragment>
        <Modal
          visible={true}
          destroyOnClose={true}
          title="编辑会员"
          onCancel={this.handleCancel}
          footer={null}
          width="480px"
        >
          <Form className="login-form" {...formItemLayout} labelAlign='left' >
            <Form.Item label="家长手机号：">
              <Input placeholder="Username" disabled value={editUserData.mobile || editUserData.parentMobile} />
            </Form.Item>
            <Form.Item label="宝宝姓名：">
              {getFieldDecorator('realName', {
                rules: [{ required: true, message: '请输入宝宝姓名' }],
                initialValue: editUserData.kiddieName || editUserData.babyName,
              })(
                <Input placeholder="Username" allowClear />
              )}
            </Form.Item>
            <Form.Item label="性别：">
              {getFieldDecorator('sex', {
                rules: [{ required: true, message: '请选中性别' }],
                initialValue: +editUserData.sex || 1,
              })(
                <Radio.Group onChange={this.onSexChange}>
                  <Radio value={1}>男</Radio>
                  <Radio value={2}>女</Radio>
                </Radio.Group>
              )}
            </Form.Item>
            <Form.Item label="生日：">
              {getFieldDecorator('birthdayStr', {
                rules: [{ type: 'object', required: true, message: '请选择日期' }],
                initialValue: moment(editUserData.birthday)
              })(<DatePicker format="YYYY-MM-DD" />)}
            </Form.Item>
            {category !== '2' && <Form.Item label="选择年级：">
              {getFieldDecorator('gradeId', {
                rules: [{ message: '请选择年级' }],
                initialValue: editUserData.gradeName,
              })(
                <Select
                  placeholder="请选择"
                  allowClear
                  onChange={this.handleSelectChange}
                >
                  {
                    grades && grades.map((item) => <Option key={item.sysNo} value={item.sysNo}>{item.name}</Option>)
                  }
                </Select>
              )}
            </Form.Item>}

            {category !== '2' && <Form.Item label="选择班级：">
              {getFieldDecorator('classId', {
                rules: [{ message: '请选择班级' }],
                initialValue: editUserData.className,
              })(
                <Select
                  placeholder="请选择"
                  allowClear
                >
                  {
                    classes && classes.map((item) => <Option key={item.sysNo} value={item.sysNo}>{item.name}</Option>)
                  }
                </Select>
              )}
            </Form.Item>}
            {category === '2' &&
              <Row>
                <Form.Item label="省份">
                  {getFieldDecorator('province', {
                    rules: [{ required: true, message: '请选择省份' }],
                    initialValue: otherData.province
                  })(
                    <Select
                      style={{ width: 200 }}
                      placeholder="请选择"
                      allowClear
                      onChange={this.changeProvinceFn}
                    >
                      {
                        areaList && areaList.map((item) => <Option key={item.areaId} value={item.areaName}>{item.areaName}</Option>)
                      }
                    </Select>
                  )}
                </Form.Item>
                <Form.Item label="城市">
                  {getFieldDecorator('city', {
                    rules: [{ required: true, message: '请选择城市' }],
                    initialValue: otherData.city
                  })(
                    <Select
                      style={{ width: 200 }}
                      placeholder="请选择"
                      allowClear
                      onChange={this.changeCityFn}
                    >
                      {
                        cities && cities.map((item) => <Option key={item.areaId} value={item.areaName}>{item.areaName}</Option>)
                      }
                    </Select>
                  )}
                </Form.Item>
                <Form.Item label="区/县">
                  {getFieldDecorator('area', {
                    rules: [{ required: true, message: '请选择区' }],
                    initialValue: otherData.area
                  })(
                    <Select
                      style={{ width: 200 }}
                      placeholder="请选择"
                      allowClear
                      onChange={this.changeCountryFn}
                    >
                      {
                        counties && counties.map((item) => <Option key={item.areaId} value={item.areaName}>{item.areaName}</Option>)
                      }
                    </Select>
                  )}
                </Form.Item>
                <Form.Item label="详细地址：">
                  {getFieldDecorator('address', {
                    rules: [{ required: true, message: '请输入详细地址' }],
                    initialValue: otherData.address
                  })(
                    <Input placeholder="请输入详细地址" allowClear />
                  )}
                </Form.Item>
                <Form.Item label="联系人">
                  {getFieldDecorator('adName', {
                    rules: [{ required: true, message: '请输入联系人' }],
                    initialValue: otherData.adName
                  })(
                    <Input placeholder="请输入联系人" allowClear />
                  )}
                </Form.Item>
                <Form.Item label="性别：">
                  {getFieldDecorator('adSex', {
                    rules: [{ required: true, message: '请选中性别' }],
                    initialValue: +otherData.adSex || 1
                  })(
                    <Radio.Group onChange={this.onSexChange}>
                      <Radio value={1}>先生</Radio>
                      <Radio value={2}>女士</Radio>
                    </Radio.Group>
                  )}
                </Form.Item>
                <Form.Item label="手机号：">
                  {getFieldDecorator('adPhone', {
                    rules: [{ required: true, message: '请输入手机号' }],
                    initialValue: otherData.adPhone
                  })(
                    <Input placeholder="请输入手机号" allowClear />
                  )}
                </Form.Item>
              </Row>}

          </Form>
          <div style={btnWrapStyle}>
            <Button onClick={() => { this.handleCancel() }} type="primary">关闭</Button>
            <Button style={btnStyle} onClick={this.handleSubmit} type="primary">确定</Button>
          </div>
        </Modal>
      </Fragment>

    )
  }
  changeProvinceFn = (e) => {
    let cities = []
    for (let i in areaList) {
      const item = areaList[i]
      if (item.areaName === e) {
        cities = item.cities;
        break;
      }
    }
    this.setState({
      cities
    })
    // this.props.form.setFields({ 'area': null, 'city': null });
    this.props.form.setFieldsValue({ 'area': null, 'city': null });
  }

  changeCityFn = e => {
    const { cities } = this.state;
    let counties = []
    for (let i in cities) {
      const item = cities[i]
      if (item.areaName === e) {
        counties = item.counties;
        break;
      }
    }
    this.setState({
      counties
    })
    // this.props.form.setFields({ 'area': null });
    this.props.form.setFieldsValue({ 'area': null });

  }

  submitChangeBaby = (data) => {
    ajaxAxios({
      url: '/member/updateKiddie',
      data
    }).then((res) => {
      console.log(res)
      if (res.result) {
        this.props.showEditUser(false)
        message.success('修改成功');
        this.props.searchFn()
      } else {
        message.warning(res.logMessage);
      }
    });
  }
}

const mapState = (state) => ({
  userInfo: state.login.userInfo || (loStorage.getItem('userMes') && loStorage.getItem('userMes').userInfo) || null,
  gradeAndClass: state.login.gradeAndClass || loStorage.getItem('allClass'),
  otherData: state.common.otherData
})

const mapDispatch = (dispatch) => ({
  showEditUser(flag, data) {
    dispatch(actionCreators.showEditUser(flag, data));
  },
  getOtherData(kiddieId) {
    dispatch(actionCreators.getOtherData(kiddieId))
  }
})
let formComp = Form.create()(EditUser)
export default connect(mapState, mapDispatch)(formComp);
