import React,{ Component } from 'react';
import { connect } from 'react-redux';
import { Row, Col} from 'antd';
import { actionCreators } from '../store';

class BagDetail extends Component {
  render() {
    const { statistical: info } = this.props;
    return(<div className="l-mt3 l-mb3">
      <Row gutter={16}>
        <Col span={6}>阅读包总数: {info.totalBookbagCount}</Col>
        <Col span={6}>高级会员总人数: {info.eduMembershipCount}</Col>
        <Col span={6}>可借阅会员人数: {info.noBorrowMemberCount}</Col>
        <Col span={6}>已借阅会员人数:  {info.borrowMemberCount}</Col>
     </Row>
      <Row gutter={16}>
        <Col span={6}>停用阅读包总数: {info.stopBagCount}</Col>
        <Col span={6}>异常阅读包总数: {info.brokenBookbagCount}</Col>
        <Col span={6}>未分配阅读包总数: {info.distributableBookbagCount}</Col>
        <Col span={6}>已分配阅读包总数:{info.borrowingBookbagCount}</Col>
     </Row>
    </div>);
  }
  componentDidMount() {
    this.props.getData();
}
}

const mapStateToProps = (state)=> ({
  statistical: state.readBags.statistical
})

const mapDispatchToProps = (dispatch) => {
  return {
      getData() {
          dispatch(actionCreators.getData());
      }

  }
}

export default connect(mapStateToProps, mapDispatchToProps)(BagDetail);
