import React from 'react';
import {
	Spin,
	Icon,
	message
} from 'antd';
import { ajaxAxios } from './io.js';

export const Loading = () => {
	return(<div style={{
		textAlign:'center',
		paddingTop:'50px'
	}}>
		<Spin /> <br /><br />
		<span className="colorGray">数据加载中...</span>
	</div>);
}

export function _getDays(start, end) { //计算两个日期之间的天数
	let arr = start.split("-");
	let starttime = new Date(arr[0], arr[1], arr[2]);
	let starttimes = starttime.getTime();
	let arrs = end.split("-");
	let endtime = new Date(arrs[0], arrs[1], arrs[2]);
	let endtimes = endtime.getTime();
	let intervalTime = endtimes - starttimes; //两个日期相差的毫秒数 一天86400000毫秒
	let Inter_Days = ((intervalTime).toFixed(2) / 86400000) + 1; //加1，是让同一天的两个日期返回一天
	return Inter_Days;
}
export const NoData = () => {
	return(<div style={{
    textAlign:'center',
    paddingTop:'50px'
  }}>
    <Icon
      style={{
        color:'#999',
        fontSize:'40px'
      }}
      type="exclamation-circle"
    /> <br /><br />
    <span className="colorGray">暂无数据...</span>
  </div>);
}

/*获取url中的参数*/
export function getUrlParam(name) {
	var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
	var r = window.location.search.substr(1).match(reg);
	if(r != null) return decodeURIComponent(r[2]);
	return null;
}

export function BrowserType() {
	var userAgent = navigator.userAgent; //取得浏览器的userAgent字符串
	var isOpera = userAgent.indexOf("Opera") > -1; //判断是否Opera浏览器
	var isIE = userAgent.indexOf("compatible") > -1 && userAgent.indexOf("MSIE") > -1 && !isOpera; //判断是否IE浏览器
	// var isEdge = userAgent.indexOf("Windows NT 6.1; Trident/7.0;") > -1 && !isIE; //判断是否IE的Edge浏览器
	// var isFF = userAgent.indexOf("Firefox") > -1; //判断是否Firefox浏览器
	// var isSafari = userAgent.indexOf("Safari") > -1 && userAgent.indexOf("Chrome") == -1; //判断是否Safari浏览器
	// var isChrome = userAgent.indexOf("Chrome") > -1 && userAgent.indexOf("Safari") > -1; //判断Chrome浏览器

	if(!isIE) {
		var reIE = new RegExp("MSIE (\\d+\\.\\d+);");
		reIE.test(userAgent);
		var fIEVersion = parseFloat(RegExp["$1"]);
		return {
			name: 'ie',
			version: fIEVersion
		}
	};

	return null;
}

export let undefinedToEmpty = (values) => { //将undefined 或者 null 转换为空,并去除首位空格

	for(let key in values) {
		if(values.hasOwnProperty(key) && (values[key] === undefined || values[key] == null)) {
			values[key] = '';
		} else if(typeof values[key] === 'string') {
			values[key] = values[key].trim();
		} else if(typeof values[key] == 'object') {
			values[key] = undefinedToEmpty(values[key]);
		}
	}
	return values;
}

export let numberFormate = (v) => { //金额千分位加保留两位小数的展示
	v = v ? v : 0;
	v = Number(v).toFixed(2);
	v = v ? (v.toString()).replace(/\B(?=(\d{3})+(?!\d))/g, ',') : 0;

	return v;
}
export let getSmsCode = (mobile)=> {
	return ajaxAxios({
		url:'/manageUser/sendSmsCode',
		data:{
			mobile
		}
	}).then((res)=>{
		if(res.success) {
			message.success('获取成功！');
		}else {
			res.logMessage && message.error(res.logMessage);
		};
		return res.success;
	});
}
