import React, { Component, Fragment } from 'react';
import { Table, Button } from 'antd';
import { connect } from 'react-redux';
// import { actionCreators } from '../store';
import axios from 'axios';
import { loStorage } from 'libs/storage.js';
import { host, sessionId } from 'libs/io.js';
// import qs from 'qs';
import { withRouter } from 'react-router-dom';

class Main extends Component {

  render() {
    let { tableList } = this.props;

    const columns = [
      {
        title: '批号',
        dataIndex: 'orderNumber',
        key: 'orderNumber',
        align: 'center',
        render: text => <span>{text ? text : '无'}</span>,
      },
      {
        title: '生成时间',
        dataIndex: 'formatDate',
        key: 'formatDate',
        align: 'center',
        render: text => <span>{text ? text : '无'}</span>,
      },

      {
        title: '分配宝贝人数',
        dataIndex: 'allCount',
        key: 'allCount',
        align: 'center',
        render: text => <span>{text ? text : '无'}</span>,
      },
      {
        title: '分配成功人数',
        dataIndex: 'matchedCount',
        key: 'matchedCount',
        align: 'center',
        render: text => <span>{text ? text : '无'}</span>,
      },
      {
        title: '分配失败人数',
        dataIndex: 'noMatchedCount',
        key: 'noMatchedCount',
        align: 'center',
        render: text => <span>{text ? text : '无'}</span>,
      },
      {
        title: '操作人',
        dataIndex: 'createUserName',
        key: 'createUserName',
        align: 'center',
        render: text => <span>{text ? text : '无'}</span>,
      },
      {
        title: '操作',
        key: 'action',
        render: (text, record) => {
          const id = record.id
          return <Fragment>
            <Button className="l-mr2" type="primary" onClick={() => this.checkFn(record)}>查看</Button>
            <Button type="primary" onClick={() => this.exportData(id)}>下载</Button>
          </Fragment>
        },
      }
    ];
    const pagination = {
      showSizeChanger: true,
      defaultPageSize: 30,
      pageSizeOptions: ['10', '20', '30', '50', '100', '150', '200']
    }
    return (
      <Fragment>
        <Table pagination={pagination} rowKey={record => record.id} columns={columns} dataSource={tableList} bordered={true} />
      </Fragment>
    );
  }
  // 查看
  checkFn = (record) => {
    this.props.history.push({ pathname: '/borrowHistory/detail', state: record })
  }
  // 下载
  exportData = (id) => {
    let url = host + '/bookbag/export_matched_detail_pdf?x-auth-token=' + sessionId + '&matchedId=' + id;
    let fileName = loStorage.getItem('userMes')['userInfo']['eduInstitution']['name'];
    axios({
      method: 'get',
      url: url,
      headers: { 'x-auth-token': loStorage.getItem('userMes') && loStorage.getItem('userMes').x_auth_token },
      responseType: 'blob' // 表明返回服务器返回的数据类型
    })
      .then((res) => { // 处理返回的文件流
        if (!res.data) {
          return
        }
        let url = window.URL.createObjectURL(new Blob([res.data]))
        let link = document.createElement('a')
        link.style.display = 'none'
        link.href = url
        link.setAttribute('download', fileName + '-借阅历史记录.pdf')
        document.body.appendChild(link)
        link.click()
      })
  }

  componentDidMount() {

  }
}

let mapStateToProps = (state) => ({
  tableList: state.borrowHistory.tableList,
})

let mapDispatchToProps = (dispatch) => {
  return {
    // getClassrooms(){
    //     dispatch(actionCreators.getClassrooms());
    // }

  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Main))
