// import store from 'store2';
import { message } from 'antd';
// import * as actionTypes from './actionTypes';
import { ajaxAxios } from 'libs/io.js';
import { getSmsCode } from 'libs/g.js';

export const getMessageCode =(mobile)=> {
  return (dispatch)=> {
    getSmsCode(mobile);
  }
}
export const confirm =(values,history)=> {
  return (dispatch)=> {
    ajaxAxios({
      url:'/manageUser/retrievePassword',
      data:{
        ...values
      }
    }).then((res)=>{
      if(res.success) {
        message.success('修改成功！');
        history.push('/login');
      }else {
        message.success(res.logMessage || '');
      };
    });
  }
}
