/**
 * 对axios 的封装
 */
import { IsOnline } from 'libs/env'

import axios from 'axios';
import qs from 'qs';
import { loStorage } from 'libs/storage.js';


const userMes = loStorage.getItem('userMes');

export const sessionId = userMes && userMes.x_auth_token;

export const host = IsOnline ? 'http://47.97.25.101:8080' : 'http://118.178.134.150:8088'; //测试环境

export const ajaxAxios = (param = {}) => {
  let axiosInstance = null,
    domain = undefined;
  let { url, data, params, method, baseURL, dataType } = param;
  if (!url) { alert('请求url不能为空!'); }
  domain = baseURL || host;
  url = domain + url;
  if (!axiosInstance) {
    axiosInstance = axios.create({
      headers: { 'x-auth-token': loStorage.getItem('userMes') && loStorage.getItem('userMes').x_auth_token },
       
      timeout: 10000,
      withCredentials: true
    })
  }
  // 添加请求拦截器
  axiosInstance.interceptors.request.use(function (config) {
    // 在发送请求之前做些什么
    if (dataType === 'json' || dataType === 'JSON') {
      config.headers['Content-Type'] = 'application/json';
    }
    
    return config;
  }, function (error) {
    // 对请求错误做些什么
    return Promise.reject(error);
  });

  // 添加响应拦截器
  axiosInstance.interceptors.response.use(function (response) {
    // 对响应数据做点什么
    return response;
  }, function (error) {
    // 对响应错误做点什么
    if (error.message.indexOf('401') !== -1) {
      window.location.href = "/";
    };
    return Promise.reject(error);
  });

  let axiosObj = {
    url: url,
    method: method || 'post',
    transformRequest: [function (data) {
      // 对 data 进行任意转换处理
      let isJSON = false;
      if (dataType === 'json' || dataType === 'JSON') {
        isJSON = true;
      }
      
      data = isJSON ? JSON.stringify(data) : qs.stringify(data);
      
      return data;
    }],
    transformResponse: [function (data) {
      if (typeof data === "string") { data = JSON.parse(data) }
      
      return data;
    }],
    params: params || {},
    paramsSerializer: function (params) {
      return qs.stringify(params, { arrayFormat: 'brackets' })
    },
    data: data
  }
  if (url.indexOf('file/upload/invCode') !== -1) {
    
    axiosObj.responseType = 'arraybuffer'
    
  }
  return axiosInstance(axiosObj).then((res) => {
    if (res.status === 200 && res.data) {
      return res.data;
    }
  });
}
