import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import {
  Table, Button
} from 'antd';
// import { actionCreators } from '../store';
import { actionCreators as actionCreatorsCommon } from 'components/store';
import { loStorage } from 'libs/storage.js';

class Main extends Component {

  handleMoreItem = () => {
    let category = this.props.userInfo.eduInstitution.category;
    let list1 = [
      {
        title: '宝贝姓名',
        dataIndex: 'kiddieRealName',
      }, {
        title: '家长手机号',
        dataIndex: 'householderMobile',
      },
      {
        title: '阅读包',
        dataIndex: 'bagNumber',
      },
      {
        title: '借阅时间',
        dataIndex: 'borrowDate',
      },
      {
        title: '归还时间',
        dataIndex: 'lastReturnDate',
      },
      {
        title: '性别',
        dataIndex: 'sex',
        render: (text, record) => {
          const arr = ['无', '男', '女']
          return arr[record.sex]
        }
      },
    ]
    const common = [{
      title: '年级',
      dataIndex: 'gradeName',
      render: text => text || '暂无'
    },
    {
      title: '班级',
      dataIndex: 'className',
      render: text => text || '暂无'
    }]

    const community = [{
      title: '家庭地址',
      dataIndex: 'address',
      render: text => text || '暂无'
    }]

    const list2 = [{
      title: '操作',
      key: 'action',
      render: (text, record) => {

        return <Fragment>
          <Button className="l-mr2" type="primary" onClick={() => this.props.onViewErrorBooks(record.sysNo)}>补书</Button>
        </Fragment>
      },
    }]

    return category === '2' ? list1.concat(community).concat(list2) : list1.concat(common).concat(list2);
  }

  render() {
    const {
      listData,
    } = this.props;
    let listColumns = this.handleMoreItem()

    const pagination = {
      showSizeChanger: true,
      defaultPageSize: 30,
      pageSizeOptions: ['10', '20', '30', '50', '100', '150', '200']
    }

    return (<div className="l-mt2 l-mb2">
      <Table
        size='small'
        pagination={pagination}
        rowKey={r => r.sysNo}
        columns={listColumns}
        dataSource={listData}
        bordered={true}
      />
    </div>);
  }
  componentDidMount() {
  }
}

const mapState = (state) => ({
  listData: state.fillBook.listData,
  userInfo: state.login.userInfo || (loStorage.getItem('userMes') && loStorage.getItem('userMes').userInfo) || null,
})

const mapDispatch = (dispatch) => ({
  onViewErrorBooks(bookBagSysNo) {
    dispatch(actionCreatorsCommon.getBooksData(bookBagSysNo, true));
  },
})

export default connect(mapState, mapDispatch)(Main);
