import * as actionTypes from './actionTypes';

const defaultState = {
  listData: [],
  selectedRowKeys:[],
  selectedRows: [],
  classInfoList: [],
}

export default (state = defaultState, action) => {
  switch (action.type) {
    case actionTypes.UPDATE_TABLE_LIST_DATA:
      return {
        ...state,
        listData: action.data,
      };
      case actionTypes.CHANGE_SELECT_ARRAY: {
        return {
          ...state,
          selectedRowKeys: action.selectedRowKeysArr,
          selectedRows: action.selectedRowsARR
        }
      }
    default:
      return state;
  }
}
