
import { message } from 'antd';
import * as actionTypes from './actionTypes';
import {
  ajaxAxios
} from 'libs/io.js';
let params = {}

// 改变勾选的借阅包
export const changeselectArrFn = (arr = [], obj = {}) => {
  return {
    type: actionTypes.CHANGE_SELECT_QRCODE_ARR,
    arr,
    obj
  }
}
// 生成借阅单弹窗
export const visibleModalFn = (flag) => {
  return {
    type: actionTypes.IF_SHOW_OR_HIDE_MODAL,
    data: flag
  }
}
// 查看借阅单生成结果弹窗
export const visibleModal2Fn = (flag) => {
  return {
    type: actionTypes.IF_SHOW_OR_HIDE_MODAL2,
    data: flag
  }
}

// 查询 获取列表
export const getTableList = (param = {}) => {
  params = param;
  return (dispatch) => {
    ajaxAxios({
      url: '/bookbag/memberList',
      method: 'post',
      dataType: 'json',
      data: {
        type: 1, // 1 借书 2还书 3补书
        currentPage: 1,
        pageSize: 9999,
        ...params
      }
    }).then((res) => {
      if (res.success) {
        dispatch(setList(res.result.records));
        dispatch(getData());
      } else {
        res.logMessage && message.error(res.logMessage);
      }

    });
  }
}

const setList = (data) => {
  return {
    type: actionTypes.SET_LIST,
    data
  }
}

// 生成借阅单
export const createReadList = (familyShipIds = '') => {
  return (dispatch) => {  
    ajaxAxios({
      url: '/bookbag/createReadList',
      method: 'post',
      // dataType: 'json',
      data: {
        familyShipIds
      }
    }).then((res) => {      
      if (res.success) {
        dispatch(visibleModalFn(false));
        dispatch(visibleModal2Fn(true));
        dispatch(getTableList(params));
        dispatch(changeselectArrFn([], {}));
      } else {
        res.logMessage && message.error(res.logMessage);
      }
    });
  }
}

export const getData = () => {
  return (dispatch) => {
    ajaxAxios({
      url: '/stats/eduFirstPage',
      method: 'get'
    }).then((res) => {
      dispatch(storeData(res.result));
    });
  }
};

const storeData = (data) => {
  return {
    type: actionTypes.STATISTICAL,
    data
  }
}
