import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { Breadcrumb } from 'antd';
import { Row, Col, Button, Table } from 'antd';
import axios from 'axios';
import { loStorage } from 'libs/storage.js';
import { host, sessionId } from 'libs/io.js';
import { actionCreators } from '../../store';

class Main extends Component {
  state = {
    id: null,
    formatDate: '',
    matchedCount: '',
    createUserName: '',
    noMatchedCount: '',
  };

  componentDidMount() {
    const obj = this.props.location.state;
    this.setState({
      id: obj.id,
      formatDate: obj.formatDate,
      matchedCount: obj.matchedCount,
      createUserName: obj.createUserName,
      noMatchedCount: obj.noMatchedCount,
    })
    this.props.getDetailSearch(obj.id)
  };

  getColumns = () => {
    let category = this.props.userInfo.eduInstitution.category;
    const list1 = [
      {
        title: '编号',
        dataIndex: 'orderNumber',
        align: 'center',
        render: text => <span>{text ? text : '无'}</span>

      },
      {
        title: '宝贝姓名',
        dataIndex: 'kiddieRealName',
        key: 'kiddieRealName',
        align: 'center',
        render: text => <span>{text ? text : '无'}</span>,
      },
      {
        title: '借阅包编号',
        align: 'center',
        render: (text, record) => {
          return <span>{record.bagNumber}</span>
        }
      },
      {
        title: '家长手机号',
        dataIndex: 'householderMobile',
        key: 'householderMobile',
        align: 'center',
        render: text => <span>{text ? text : '无'}</span>,
      }
    ];
    const common = [{
      title: '年级',
      dataIndex: 'gradeName',
      key: 'gradeName',
      align: 'center',
      render: text => <span>{text ? text : '无'}</span>,
    },
    {
      title: '班级',
      dataIndex: 'className',
      key: 'className',
      align: 'center',
      render: text => <span>{text ? text : '无'}</span>,
    }]

    const community = [{
      title: '家庭地址',
      dataIndex: 'address',
      render: text => {
        return text || '暂无'
      }
    }]
    const list2 = [
      {
        title: '图书信息',
        key: 'action',
        render: (text, record) => {
          if (record.type === '1') {
            return (
              <Fragment>
                {record.bookVos.map((item, index) => {
                  return <Row key={item.iSBN}>
                    <Col span={2}>
                      <h3>{++index}:</h3>
                    </Col>
                    <Col span={22}>
                      <span className="l-mr3">{item.bookName}</span> ISBN:{item.iSBN}
                    </Col>
                  </Row>
                })}
              </Fragment>
            )
          } else {
            return <span>{record.remark}</span>
          }
        },
      }
    ]
    let list = []
    if (category === '2') {
      list = list1.concat(community).concat(list2)
    } else {
      list = list1.concat(common).concat(list2)
    }

    return list
  }

  render() {
    let { tableDetailList } = this.props;
    let { formatDate, matchedCount, noMatchedCount, createUserName } = this.state;

    return (
      <Fragment>
        <div style={{ background: '#fff', padding: '16px' }}>
          <Breadcrumb className="l-mb3">
            <Breadcrumb.Item>借阅管理</Breadcrumb.Item>
            <Breadcrumb.Item>借阅历史记录</Breadcrumb.Item>
            <Breadcrumb.Item>借阅单详情</Breadcrumb.Item>
          </Breadcrumb>
          <div className="l-bold l-mb3">
            <Row>
              <Col span={10}>借阅单生成时间：{formatDate}</Col>
              <Col span={3}>分配成功：{matchedCount}人</Col>
              <Col span={3}>分配失败：{noMatchedCount}人</Col>
              <Col span={4}>操作人：{createUserName}</Col>
              <Col span={4}>
                <Button type="primary" onClick={() => this.exportData()}>下载借阅单</Button>
              </Col>
            </Row>
          </div>
          <Table rowKey={record => record.id} columns={this.getColumns()} dataSource={tableDetailList} bordered={true}/>
        </div>
      </Fragment>);
  }
  // 下载
  exportData = () => {
    const { id } = this.state;

    let url = host + '/bookbag/export_matched_detail_pdf?x-auth-token=' + sessionId + '&matchedId=' + id;
    let fileName = loStorage.getItem('userMes')['userInfo']['eduInstitution']['name'];
    axios({
      method: 'get',
      url: url,
      headers: { 'x-auth-token': loStorage.getItem('userMes') && loStorage.getItem('userMes').x_auth_token },
      responseType: 'blob' // 表明返回服务器返回的数据类型
    })
      .then((res) => { // 处理返回的文件流
        if (!res.data) {
          return
        }
        let url = window.URL.createObjectURL(new Blob([res.data]))
        let link = document.createElement('a')
        link.style.display = 'none'
        link.href = url
        link.setAttribute('download', fileName + '-借阅历史记录.pdf')
        document.body.appendChild(link)
        link.click()
      })
  }
}

let mapStateToProps = (state) => ({
  tableDetailList: state.borrowHistory.tableDetailList,
  userInfo: state.login.userInfo || (loStorage.getItem('userMes') && loStorage.getItem('userMes').userInfo) || null,
})
let mapDispatchToProps = (dispatch) => {
  return {
    getDetailSearch(id) {
      dispatch(actionCreators.getDetailSearch(id));
    }
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(Main);
