import React, { Component } from 'react';
import {
  Form, Input, Button, Select
} from 'antd';
import { connect } from 'react-redux';
import { actionCreators } from '../store';

const Option = Select.Option;
class Main extends Component {
  render() {
    const {
      getFieldDecorator,
    } = this.props.form;

    return (<Form layout="inline" onSubmit={this.handleSubmit}>
      <Form.Item
        label="阅读包编号"
      >
        {
          getFieldDecorator('bagNumber')(
            <Input allowClear style={{ width: 200 }} placeholder="请输入阅读包编号" />
          )
        }
      </Form.Item>
      <Form.Item
        label="阅读包状态"
      >
        {
          getFieldDecorator('borrowState')(
            <Select
              style={{ width: 200 }}
              placeholder="请选择"
              allowClear
            >
              <Option value="">全部</Option>
              <Option value="0">未分配</Option>
              <Option value="1">已分配</Option>
              <Option value="2">异常</Option>
              {/* <Option value="3">已归还</Option> */}
              <Option value="4">停用</Option>
            </Select>
          )
        }
      </Form.Item>
      <Form.Item>
        <Button
          type="primary"
          htmlType="submit"
        >
          查询
   </Button>
      </Form.Item>
    </Form>);
  }
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, fieldsValue) => {   
      
      const rangeValue = fieldsValue['range-picker'] || [];
      const startDate = rangeValue.length > 0 ? rangeValue[0].format('YYYY-MM-DD'): ''
      const endDate = rangeValue.length > 0 ? rangeValue[1].format('YYYY-MM-DD'): ''

      let values = {
        ...fieldsValue,
        startDate,
        endDate        
      };    
      delete values['range-picker']  
      
      if (!err) {
        this.props.onSearch(values);
      }
    });
  }
  componentDidMount() {
    this.props.onSearch()
  }
}

// const WrappedMyForm = Form.create({
//   onFieldsChange(props, fields) {
//     props.onChangeSearchForm(props.form.getFieldsValue());
//   }
// })(Main);

const WrappedMyForm = Form.create({
  name: 'readPackageManegement-search'
})(Main);

const mapStateToProps = (state) => ({

})

const mapDispatchToProps = (dispatch) => ({
  onSearch(param) {
    dispatch(actionCreators.getTableList(param));
  }
})
export default connect(mapStateToProps, mapDispatchToProps)(WrappedMyForm);
