import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { Modal, Button, Table, Row, Col, Radio, Divider, message } from 'antd';
import { loStorage } from 'libs/storage.js';
import { actionCreators } from '../store';
const RadioGroup = Radio.Group
class Main extends Component {
  state = {
    category: null, // 1：幼儿园；2：社区馆；3：小学    
    loading: false,
    sourceList: []
  }


  componentDidMount() {
    let category = this.props.userInfo.eduInstitution.category;
    
    this.setState({
      category,      
      sourceList: this.listColumns()
    })
    // 清空数据
    this.props.changeQrcodeList([])
    
  }

  hideModelFn = () => {
    this.props.setReturnAllBagsModel(false)
  }
  showModelFn = () => {
    const { selectQrcodeArr, requestQrcodeBags } = this.props;
    if(selectQrcodeArr && selectQrcodeArr.length===0)return message.info('请勾选借阅包')
    this.props.setReturnAllBagsModel(true)
    requestQrcodeBags(selectQrcodeArr)
    this.setState({
      loading: false
    })
  }


  // 改变书包中对应图书的归还状态
  onChangeBookState = (qrcodeNumber, value, iSBN) => {
    let list = this.props.qrcodeReturnList;
    
    for (let i = 0, len = list.length; i < len; i++) {

      if (qrcodeNumber === list[i].qrCodeNumber) {
        
        const arr = list[i].bookVos;

        for (let j = 0, leng = arr.length; j < leng; j++) {

          if (iSBN === arr[j].iSBN) {

            arr[j].bookState = value;

            break;
          }
        }
      }
    }
    
    this.props.changeQrcodeList(list)
  }

  // 确认归还
  sureReturnFn = () => {
    this.setState({
      loading: true
    })
    const { selectAllQrcode, qrcodeReturnList, returnMultipleBags } = this.props;
    const selectArr = selectAllQrcode
    const dataArr = qrcodeReturnList
      

    returnMultipleBags(selectArr, dataArr)
  }

  listColumns = () => {
    const { category } = this.state
    let list = [{
      title: '阅读包编号',
      dataIndex: 'bagNumber',
      render: text => <span className="l-bule">{text}</span>,
    },
    {
      title: '宝贝姓名',
      dataIndex: 'kiddieRealName',
    },
    {
      title: '家长手机号',
      dataIndex: 'householderMobile',
    }]
    const list2 = category === '2' ? [{
      title: '收货地址',
      dataIndex: 'address',
    }] :
      [
        {
          title: '年级',
          dataIndex: 'gradeName',
        },
        {
          title: '班级',
          dataIndex: 'className',
        },
      ]
    const actions = [{
      title: '图书信息',
      key: 'action',
      width: 450,
      render: (text, record) => {
        return (
          <Fragment>
            {record.bookVos.map((item, index) => {
              return <Row key={item.sysNo}>
                <Col span={2}>
                  <h3>{++index}:</h3>
                </Col>
                <Col span={22}>
                  <Row gutter={24}>
                    <Col span={12}>
                      <span className="l-mr3">{item.bookName}</span> ISBN:{item.iSBN}
                    </Col>
                    <Col span={12}>
                      
                      <RadioGroup
                        key={record.qrCodeNumber}
                        onChange={(e) => this.onChangeBookState(record.qrCodeNumber, e.target.value, item.iSBN)}
                        value={item.bookState}

                      >
                        <Radio key={record.qrCodeNumber+'1'} className="l-mb2" value='2'>正常</Radio>
                        <Radio key={record.qrCodeNumber+'2'} className="l-mb2" value='3'>损坏</Radio>
                        <Radio key={record.qrCodeNumber+'3'} className="l-mb2" value='4'>丢失</Radio>
                      </RadioGroup>
                    </Col>
                  </Row>
                </Col>
                <Divider />
              </Row>
            })}
          </Fragment>
        )
      }
    }]
    return list.concat(list2, actions);
  }

  render() {
    const { loading, sourceList } = this.state
    const { qrcodeReturnList, returnAllBagsModelVisible, selectAllQrcode, changeSelectAllArrFn } = this.props;

    const rowSelection = {
      selectedRowKeys: selectAllQrcode,
      onSelect: (record, selected) => {
        let arr = JSON.parse(JSON.stringify(selectAllQrcode))
        if (selected) {
          arr.push(record.qrCodeNumber)
        } else {
          arr.splice(arr.findIndex(item => item === record.qrCodeNumber), 1)
        }
        changeSelectAllArrFn(arr)
      },
      onSelectAll: (selected, selectedRows, changeRow) => {
        let arr = JSON.parse(JSON.stringify(selectAllQrcode))
        if (selected) {
          selectedRows.map(item => arr.push(item.qrCodeNumber))
        } else {
          changeRow.map(item => arr.splice(arr.findIndex(ite => ite === item.qrCodeNumber), 1))
        }
        arr = [...new Set(arr)]
        changeSelectAllArrFn(arr)
      }
    };

    return (<Fragment>
      <Button className="l-mr2" type="primary" onClick={this.showModelFn}>批量归还阅读包</Button>      
      <Modal
        visible={returnAllBagsModelVisible}
        width="90%"
        title="批量归还阅读包"
        onCancel={this.hideModelFn}
        footer={[
          <Button key="back" onClick={this.hideModelFn}>
            取消
            </Button>,
          <Button key="submit" type="primary" loading={loading} onClick={this.sureReturnFn}>
            确认归还
            </Button>,
        ]}
        destroyOnClose
      >

        <Table
          size='small'
          pagination={false}
          bordered={true}
          rowKey={r => r.qrCodeNumber}        
          rowSelection={rowSelection}
          columns={sourceList}
          dataSource={qrcodeReturnList}
        />
      </Modal>
    </Fragment>);
  }
}

const mapState = (state) => ({
  returnAllBagsModelVisible: state.returnBook.returnAllBagsModelVisible, // 弹窗显示隐藏
  userInfo: (loStorage.getItem('userMes') && loStorage.getItem('userMes').userInfo) || null,
  selectQrcodeArr: state.returnBook.selectQrcodeArr, // 列表勾选的书包
  qrcodeReturnList: state.returnBook.qrcodeReturnList, // 当前弹窗接口获取的数据
  selectAllQrcode: state.returnBook.selectAllQrcode, // 当前弹窗勾选的书包
})

const mapDispatch = (dispatch) => ({
  // 批量请求书包
  requestQrcodeBags(list) {
    dispatch(actionCreators.requestQrcodeBags(list))
  },
  // 设置弹框展示
  setReturnAllBagsModel(flag) {
    dispatch(actionCreators.setReturnAllBagsModel(flag))
  },
  changeQrcodeList(list) {
    dispatch(actionCreators.changeQrcodeList(list))
  },
  // 弹窗勾选的书包
  changeSelectAllArrFn(selectArr){
    dispatch(actionCreators.changeSelectAllArrFn(selectArr))  
  },
  // 确认归还
  returnMultipleBags(selectArr, dataArr){
    dispatch(actionCreators.returnMultipleBags(selectArr,dataArr))  
  },
})

export default connect(mapState, mapDispatch)(Main);
