export const listColumns1 = [{
    title: '宝宝姓名',
    dataIndex: 'kiddieName',
}, {
    title: '家长手机号',
    dataIndex: 'mobile',
},
{
    title: '性别',
    dataIndex: 'sex',
    render: (text, record) => {
        return record.sex === '1' ? '男' : '女'
    }
},
{
    title: '年级',
    dataIndex: 'gradeName',
    render: (text, record) => {
        return record.gradeName || '暂无'
    }
},
{
    title: '班级',
    dataIndex: 'className',
    render: (text, record) => {
        return record.className || '暂无'
    }
},
{
    title: '添加宝贝日期',
    dataIndex: 'kiddieRegisterDate',
    render: (text, record) => {
        return record.kiddieRegisterDate || '暂无'
    }
}, 
{
    title: '会员状态',
    dataIndex: 'memberShipStatusStr',
}]





