import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { Button, Input, Radio, message } from 'antd';
import { actionCreators } from './store';
import gun from 'imgs/gun.jpg';
import { ajaxAxios } from 'libs/io.js';
class Main extends Component {

  state = {
    radio: 0,
    equalSBNStr: '',
    colorRed: null,
    scanIsbn: null
  };

  onChange = e => {
    this.setState({
      radio: e.target.value,
    });
  };

  onCheckFillBook = (scanIsbn, iSBN, bookbagId) => {
    scanIsbn = scanIsbn.trim();
    if (this.timer) {
      clearTimeout(this.timer);
    };
    if (!scanIsbn || !scanIsbn.length) {
      return;
    };
    this.timer = setTimeout(() => {

      if (scanIsbn === iSBN) {
        return this.checkFillBookAjax(scanIsbn, bookbagId);
      }
      this.setState({
        equalSBNStr: '*请选择相同ISBN码的图书进行补充',
        colorRed: true,
        scanIsbn: null
      })

    }, 500);
  }

  checkFillBookAjax(scanIsbn, bookbagId) {
    ajaxAxios({
      url: '/bookbag/book_supplement_check',
      method: 'get',
      params: {
        isbn: scanIsbn,
        bookbagId
      }
    }).then(res => {
      if (res.success) {
        this.setState({
          equalSBNStr: `识别成功  书名《${this.props.item.bookName}》`,
          colorRed: false,
          scanIsbn
        })
      } else {
        message.error(res.logMessage);
      }

    }).catch(err => {
      console.log('图书补书ISBN码校验接口失败', err)
    })
  }


  render() {
    const {
      item,
      viewBooksData
    } = this.props;
    const {
      equalSBNStr,
      colorRed
    } = this.state
    return (
      <Fragment>
        <span className="l-ml4">{item.bookStateStr}</span>
        <div>
          <Input
            style={{ background: `url(${gun}) no-repeat right top` }}
            placeholder="扫描图书条形码进行补书"
            onChange={(e) => this.onCheckFillBook(e.target.value, item.iSBN, viewBooksData.sysNo)}
          />
        </div>
        <div className={colorRed ? 'l-red' : 'l-black'}>{equalSBNStr}</div>
        <div style={{ height: '44px', lineHeight: '44px' }}>
          <Radio.Group name='radiogroup' onChange={this.onChange} defaultValue={0}>
            <Radio value={0}>公司补书</Radio>
            {item.supplementState !== '1' && <Radio value={1}>家长归还补书</Radio>}
          </Radio.Group>
          <Button className="l-right l-mt2" type="primary" onClick={() => this.sureFillBook(viewBooksData.sysNo)}>确定补书</Button>
        </div>
      </Fragment>

    )
  }

  sureFillBook = (sysNo) => {
    const { colorRed, radio, scanIsbn } = this.state;
    if (colorRed !== false || !scanIsbn) return message.info('请选择相同ISBN码的图书进行补充');
    this.sureFillBookFn(scanIsbn, sysNo, radio)
  }

  // 确认补书ISBN码
  sureFillBookFn = (scanIsbn, bookBagSysNo, type) => {
    const { updateBooksData, getBooksData, searchFn, isErrorModal } = this.props
    ajaxAxios({
      url: '/bookbag/supplementCheckBook',
      data: {
        scanIsbn,
        bookBagSysNo,
        type
      }
    }).then((res) => {
      if (res.success) {
        message.success(res.logMessage);
        updateBooksData(res.result)
        getBooksData(bookBagSysNo, isErrorModal)
        searchFn()
        return res
      } else {
        message.error(res.logMessage);
      }
    });
  }

}

const mapState = (state) => ({
  isErrorModal: state.common.isErrorModal,
})

const mapDispatch = (dispatch) => ({
  updateBooksData(result) {
    dispatch(actionCreators.updateBooksData(result))
  },
  getBooksData(bookBagSysNo, isErrorModal) {
    dispatch(actionCreators.getBooksData(bookBagSysNo, isErrorModal))
  }
  // sureFillBook(scanIsbn, sysNo, radio) {
  //   dispatch(actionCreators.sureFillBook(scanIsbn, sysNo, radio))
  // }
})

export default connect(mapState, mapDispatch)(Main);
