import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { Breadcrumb } from 'antd';
import { actionCreators } from './store';
import SearchForm from './components/searchForm.js';
import AddRegularMember from './components/addRegularMember.js';
import BatchAddRegularMember from './components/batchAddRegularMember.js';
import BatchUpSeniorMember from './components/batchUpSeniorMember.js';
import Out from './components/out';
import MyList from './components/list.js';
import AddRegularMemberDialog from './components/addRegularMemberDialog.js'
import { loStorage } from 'libs/storage.js';
import EditUser from '../../../components/editUser';
import UpMemberDialog from './components/upMemberDialog';

class Main extends Component {
    render() {
        let { ifShowEditUser, data: editUserData } = this.props.showEditUserData
        return (
            <Fragment>
                <div style={{ background: '#fff', padding: '16px' }}>
                    <Breadcrumb className="l-mb3">
                        <Breadcrumb.Item>会员管理</Breadcrumb.Item>
                        <Breadcrumb.Item>普通会员</Breadcrumb.Item>
                    </Breadcrumb>
                    <SearchForm />
                    <div className="l-mt4">
                        <AddRegularMember />
                        <BatchAddRegularMember />
                        <BatchUpSeniorMember />
                        <Out />
                    </div>
                    {ifShowEditUser ? <EditUser editUserData={editUserData}
                        searchFn={() => {
                            this.props.getTableListPre()
                        }} /> : null}
                    <MyList />
                    <AddRegularMemberDialog />
                    <UpMemberDialog />
                </div>
            </Fragment>);
    }
}

let mapStateToProps = (state) => ({
    userInfo: state.login.userInfo || (loStorage.getItem('userMes') && loStorage.getItem('userMes').userInfo) || null,
    showEditUserData: state.common.showEditUserData,
})
let mapDispatchToProps = (dispatch) => {
    return {
        getTableListPre() {
            dispatch(actionCreators.getTableListPre());
        },
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Main);
