import React  from 'react';
import { Modal, Table } from 'antd';
import { connect } from 'react-redux';
import { actionCreators } from './store';

class CirculationRecordList extends React.Component {
  

  handleCancel = e => {
    this.props.hideModel()
  };  

  render() {
    const {
      visibileCirculationRecords,
      viewCirculationRecordsData
    } = this.props;    
    
    const columns = [
      {
        title: '借阅宝宝姓名',
        dataIndex: 'babyName',
        key: 'babyName',
      },
      {
        title: '家长手机号',
        dataIndex: 'parentMobile',
        key: 'parentMobile',
      },
      {
        title: '班级信息',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: '借阅时间',
        dataIndex: 'startDate',
        key: 'startDate',
      },
      {
        title: '归还时间',
        dataIndex: 'returnDate',
        key: 'returnDate',
      },
    ];
    return (
      <div className="circulation-box">
        <Modal
          title="借阅包流通记录"
          centered={true}
          footer={null}
          visible={visibileCirculationRecords}
          onCancel={this.handleCancel}
          width="80%"
          destroyOnClose
        >
          <div style={{ 'marginBottom': '10px' }} >借阅包资产编号：{viewCirculationRecordsData.qrCodeNumber}</div> 
          <Table style={{ 'maxHeight': '300px', overflow: 'auto' }}
            rowKey='sysNo'
            dataSource={viewCirculationRecordsData.recordVos} columns={columns} pagination={false} />
        </Modal>
      </div>
    );
  }
}


const mapState = (state)=> ({
  viewCirculationRecordsData: state.common.viewCirculationRecordsData,
  visibileCirculationRecords: state.common.visibileCirculationRecords,
})
const mapDispatch = (dispatch)=> ({
  hideModel(){
    dispatch(actionCreators.CirculationRecords());
  }
})

export default connect(mapState,mapDispatch)(CirculationRecordList);
