import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { Button, Modal} from 'antd';
import SetStopOrUndistributedModal from './setStopOrUndistributedModal.js';
import { actionCreators } from '../store';
// import gun from 'imgs/gun.jpg';
// import BagDetail from './bagDetail';

class Main extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: false,
      flag: true,
      matchLength: 0,
      otherLength: 0,
    }
  }
  render() {
    const { visible, flag, otherLength, matchLength } = this.state;
    return (<Fragment>
      <Button
        onClick={() => { this.buttunFn(true) }}
        className="l-mr2" type="primary">设置为停用</Button>
      <Button
        onClick={() => { this.buttunFn(false) }}
        className="l-mr2" type="primary">设置为未分配</Button>
      <Modal
        visible={visible}
        width="80%"
        title={'勾选状态'}
        onCancel={this.cancelFn}
        footer={[
          <Button key="back" onClick={this.cancelFn}>
            取消
            </Button>,
          <Button key="submit" type="primary"  onClick={this.okFn}>
            确认
            </Button>,
        ]}
      >
        {flag ? <div className="l-bold">已选择未分配状态阅读包：{matchLength}个</div> : <div className="l-bold">已选择停用状态阅读包：{matchLength}个</div>}
        <div className="l-mt3 l-bold">其他状态阅读包：{otherLength}个</div>
 
      </Modal>
      <SetStopOrUndistributedModal isStop={this.state.flag}/>
    </Fragment>);
  }
  buttunFn = (flag) => {
    /** 
     * flag true 对应 borrowState=0 (未分配状态 设置为停用) 
       flag false 对应 borrowState=4 (停用状态 设置为未分配) */
    const { selectedRows: list } = this.props;

    let arr = []
    list.map(item => {
      if (flag && item.borrowState === '0') {
        return arr.push(item.qrCodeNumber)
      }
      if (!flag && item.borrowState === '4') {
        return arr.push(item.qrCodeNumber)
      }
      return []
    })
    this.setState({
      visible: true,
      flag,
      matchLength: arr.length,
      otherLength: list.length - arr.length
    })
    this.props.setQrCodeNumber_stop_undistributed_list(arr)
  }
  cancelFn = () => {
    this.setState({
      visible: false
    })
    this.props.cancelProps()
  }
  okFn = () => {
    this.setState({
      visible: false
    })
    this.props.showStopModel()
  }
}

const mapState = (state) => ({
  selectedRowKeys: state.readBags.selectedRowKeys,
  selectedRows: state.readBags.selectedRows,
})

const mapDispatch = (dispatch) => ({
  showStopModel() {
    dispatch(actionCreators.showStopModel())
  },
  cancelProps() {
    dispatch(actionCreators.hideStopModel());
  },
  setQrCodeNumber_stop_undistributed_list(arr) {
    dispatch(actionCreators.setQrCodeNumber_stop_undistributed_list(arr));
  },
})

export default connect(mapState, mapDispatch)(Main);
