import * as actionTypes from './actionTypes';

const defaultState = {
  listData: [],
  // visibileErrorModal: false,
  // viewBooksData: {},
  visibileFillModal: false
}

export default (state = defaultState, action) => {
  switch (action.type) {
    case actionTypes.SET_LIST:
      return {
        ...state,
        listData: action.data
      };
    // case actionTypes.VISIBILE_ERROR_MODAL:
    //   return {
    //     ...state,
    //     visibileErrorModal: true,
    //     viewBooksData: action.data
    //   };
    // case actionTypes.UPDATE_BOOKS_DATA:
    //   return {
    //     ...state,
    //     viewBooksData: action.data
    //   };
    // case actionTypes.CANCEL_ERROR_MODAL:
    //   return {
    //     ...state,
    //     visibileErrorModal: false
    //   };
    case actionTypes.VISIBILE_FILL_MODAL:
      return {
        ...state,
        visibileFillModal: true
      };
    case actionTypes.CANCEL_FILL_MODAL:
      return {
        ...state,
        visibileFillModal: false
      };
    default:
      return state;
  }
}
