import React,{ Component } from 'react';
import { Layout,Icon,Avatar,Popover,message } from 'antd';
import { withRouter,Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { ajaxAxios } from '../libs/io.js';
import { loStorage } from '../libs/storage.js';

const { Header } = Layout;

const tipStyle={
  fontSize: '18px'
}

class PageHeader extends Component {
  render() {
    const text = <Link to="/setCenter">设置中心</Link>;

    const content = (
      <div>
        <p className="l-pointer" onClick={this._loginOut.bind(this)}>退出</p>
      </div>
    );

    return(<Header className="pageHeader l-box-shadow1 ">
      <div className="header">
        <div className="left">
          <Icon type="unordered-list" />
          <span>欢迎来到互动宝宝</span>
        </div>
        <div className="right">
          <Icon className="l-vertical-middle l-mr5" type="bell" style={ tipStyle } />
          <Popover placement="topLeft" title={text} content={content} trigger="hover">
            <Avatar icon="user"  className="l-pointer"/>
            {/* <Avatar className="l-vertical-middle" size="small" src="https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png" /> */}
            <span className="l-vertical-middle l-ml2 l-pointer">{this.props.userInfo ? this.props.userInfo.currentUser.name : '游客'}</span>
          </Popover>
        </div>
      </div>
    </Header>);
  }
  _loginOut() {
    let self = this;
		ajaxAxios({
			url:'/login/logout',
			method:'post'
		}).then((res)=> {
			if (res.success === true) {
					message.success('退出成功');
          loStorage.clear();
          setTimeout(()=> {
            self.props.history.push('/');
          },1000);
			} else {
					message.error(res.logMessage)
			}
		});
  }
}

const mapState = (state)=> ({
  userInfo:state.login.userInfo || (loStorage.getItem('userMes') && loStorage.getItem('userMes').userInfo) || null
})

export default connect(mapState,null)(withRouter(PageHeader));
