import React,{ Component } from 'react';
import { Button , message} from 'antd';
import { connect } from 'react-redux';
import { actionCreators } from '../store';
class Main extends Component {
  render() {
    return(<Button className="l-mr2" type="primary" style={{marginLeft: '15px'}} 
    onClick={this.upSeniorMember}>批量升级为高级会员</Button>);
  }
  upSeniorMember = () => {
    let { selectedRowKeys} = this.props;
    if (selectedRowKeys.length === 0) return message.info('请勾选宝贝')
    this.props.showDialog(true)
  }
}

const mapState = (state)=> ({
  selectedRowKeys:state.regularMember.selectedRowKeys,
  selectedRows: state.regularMember.selectedRows
})

const mapDispatch = (dispatch) => ({
  showDialog(data) {
    dispatch(actionCreators.visibleBatchUpMemberModal(data))
  }
})
export default connect(mapState,mapDispatch)(Main);