import * as actionTypes from './actionTypes';

const defaultState = {
  visibleEditOrAdd: false, // 弹窗是否显示
  type: 'add', // 添加add  编辑edit  
  name: '', // 编辑教师姓名
  phone: '',   // 编辑教师手机
  dataSource: []
}

export default (state = defaultState, action) => {
  switch (action.type) {
    case actionTypes.EDITORADDSHOW:
      return {
        ...state,
        ...action.data
      };
    case actionTypes.SETDATASOURCE:
      return {
        ...state,
        dataSource: action.data
      };
    default:
      return state;
  }
}
