import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Form, Input, Button, Select } from 'antd';
import { actionCreators } from '../store';
import { actionCreators as actionCreatorsLogin } from 'pages/login/login/store';
import { loStorage } from 'libs/storage.js';
const Option = Select.Option;

class MyForm extends Component {
  state = {
    grades: [],
    classes: []
  };
  handleSelectChange = value => {
    const { gradeClassInfos } = this.props.gradeAndClass;
    this.setState({
      classes: gradeClassInfos[value]
    })
    this.props.form.resetFields('classId');
  };

  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.props.onSearch(values);
      }
    });
  }
  componentDidMount() {
    this.props.onSearch()
    this.props.getAllClass()
    const obj = loStorage.getItem('allClass');
    if (!obj) return;
    let newGrades = obj.grades;

    const gradesList = obj.grades;

    if (gradesList.length > 0 && gradesList[gradesList.length - 1].sysNo !== '0') {
      newGrades = obj.grades.concat({
        sysNo: '0',
        name: '无'
      })
    }

    this.setState({
      grades: newGrades,
      gradeClassInfos: obj.gradeClassInfos
    })
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const { classes, grades } = this.state;
    let category = this.props.userInfo.eduInstitution.category;
    return (
      <Form layout="inline" onSubmit={this.handleSubmit}>
        {category !== '2' && <Form.Item label="选择年级">
          {
            getFieldDecorator('gradeId')(
              <Select
                style={{ width: 200 }}
                placeholder="请选择"
                allowClear
                onChange={this.handleSelectChange}
              >
                {
                  grades && grades.map((item) => {
                    return <Option key={item.sysNo} value={item.sysNo}>{item.name}</Option>
                  })
                }
              </Select>
            )
          }
        </Form.Item>}
        {category !== '2' && <Form.Item label="选择班级">
          {
            getFieldDecorator('classId')(
              <Select
                style={{ width: 200 }}
                placeholder="请选择"
                allowClear
              >
                {
                  classes && classes.map((item) => {
                    return <Option key={item.sysNo} value={item.sysNo}>{item.name}</Option>
                  })
                }
              </Select>
            )
          }
        </Form.Item>
        }
        <Form.Item label="借阅状态">
          {
            getFieldDecorator('hasBagState')(
              <Select
                style={{ width: 200 }}
                placeholder="请选择"
                allowClear
              >
                {/* <Option value="5">全部</Option> */}
                <Option value="1">已借阅</Option>
                <Option value="2">可借阅</Option>
                <Option value="4">不可借阅</Option>
              </Select>
            )
          }
        </Form.Item>
        <Form.Item label="会员状态">
          {
            getFieldDecorator('memberShipStatus')(
              <Select
                style={{ width: 200 }}
                placeholder="请选择"
                allowClear
              >
                {/* <Option value="0">未升级</Option> */}
                <Option value="1">使用中</Option>
                <Option value="3">已过期</Option>
              </Select>
            )
          }
        </Form.Item>

        <Form.Item label="宝宝姓名">
          {
            getFieldDecorator('kiddieName')(
              <Input allowClear style={{ width: 200 }} placeholder="请输入宝宝姓名" />
            )
          }
        </Form.Item>
        <Form.Item label="家长手机号">
          {
            getFieldDecorator('mobile')(
              <Input allowClear style={{ width: 200 }} placeholder="请输入手机号" />
            )
          }
        </Form.Item>
        <Form.Item label="阅读包编号">
          {
            getFieldDecorator('bagNumber')(
              <Input allowClear style={{ width: 200 }} placeholder="请输入阅读包编号" />
            )
          }
        </Form.Item>
        <Form.Item>
          <Button type="primary" htmlType="submit">
            查询
          </Button>
        </Form.Item>
      </Form>
    )
  }
}

const WrappedMyForm = Form.create({
  onFieldsChange(props, fields) {
    props.onChangeSearchForm(props.form.getFieldsValue());
  }
})(MyForm);

let mapStateToProps = (state) => ({
  userInfo: state.login.userInfo || (loStorage.getItem('userMes') && loStorage.getItem('userMes').userInfo) || null,
  gradeAndClass: state.login.gradeAndClass || loStorage.getItem('allClass')
})
let mapDispatchToProps = (dispatch) => ({
  getAllClass() {
    dispatch(actionCreatorsLogin.getClass());
  },
  onSearch(param) {
    dispatch(actionCreators.getTableList(param));
  },
  onChangeSearchForm(data) {
    dispatch(actionCreators.updateSearchFormData(data));
  }
})
export default connect(mapStateToProps, mapDispatchToProps)(WrappedMyForm);
