import React, {Component} from 'react';
import { connect } from 'react-redux';
import {
  Table, Alert, Icon
} from 'antd';
import { actionCreators } from '../store';
import { loStorage } from 'libs/storage.js';

class Main extends Component {

  handleMoreItem = () => {
    let category = this.props.userInfo.eduInstitution.category;
    let list = [
      {
        title: '宝贝姓名',
        dataIndex: 'kiddieName',
      }, {
        title: '家长手机号',
        dataIndex: 'mobile',
      },
      {
        title: '性别',
        dataIndex: 'sex',
        render: (text, record) => {
          const arr = ['无', '男', '女']
          return arr[record.sex]
        }
      }]
    const common = [{
      title: '年级',
      dataIndex: 'gradeName'
    },
    {
      title: '班级',
      dataIndex: 'className'
    }]

    const community = [{
      title: '家庭地址',
      dataIndex: 'address',
      render: text => {
        return text || '暂无'
      }
    }]

    return category === '2' ? list.concat(community) : list.concat(common);
  }

  render() {
    const { selectArr, selectObj } = this.props;
    const {
      listData,
    } = this.props;
    let listColumns = this.handleMoreItem()

    const rowSelection = {
      selectedRowKeys: selectArr,
      onSelect: (record, selected) => {
        let arr = JSON.parse(JSON.stringify(selectArr))
        let obj = JSON.parse(JSON.stringify(selectObj))
        if (selected) {
          arr.push(record.familyMemberShipId)
          obj[record.familyMemberShipId] = record
        } else {
          arr.splice(arr.findIndex(item => item === record.familyMemberShipId), 1)
          delete obj[record.familyMemberShipId]
        }
        console.log('onSelect',arr,obj)
        this.changeselectArrFn(arr, obj)
      },
      onSelectAll: (selected, selectedRows, changeRow) => {
        let arr = JSON.parse(JSON.stringify(selectArr))
        let obj = JSON.parse(JSON.stringify(selectObj))
        if (selected) {
          selectedRows.map(item => {
            obj[item.familyMemberShipId] = item;
            return arr.push(item.familyMemberShipId)
          })
        } else {
          changeRow.map(item => {
            delete obj[item.familyMemberShipId];
           return arr.splice(arr.findIndex(ite => ite === item.familyMemberShipId), 1)
          })

        }
        arr = [...new Set(arr)]
        console.log('onSelectAll',arr,obj)
        this.changeselectArrFn(arr, obj)
      }
    };

    const MessageText = () => {
      return (<div>
        <span className="l-mr2 l-hover"><Icon theme="filled" type="info-circle" /></span>
        已选择{selectArr.length}个宝贝
        <span className="l-ml4 l-hover" onClick={() => { this.changeselectArrFn([], {}) }}>清空选择</span>
      </div>);
    }

    const pagination = {
      showSizeChanger: true,
      defaultPageSize: 30,
      pageSizeOptions: ['10', '20', '30', '50', '100', '150', '200']
    }

    return (<div className="l-mt2 l-mb2">
      {
        selectArr.length > 0 &&
        <Alert className="l-mb2" message={MessageText()} type="info" />
      }
      <Table
        size='small'
        pagination={pagination}
        rowKey={r => r.familyMemberShipId}
        rowSelection={rowSelection}
        columns={listColumns}
        dataSource={listData}
        bordered={true}
      />
    </div>);
  }
  componentDidMount() {
    this.changeselectArrFn([], {})
  }
  changeselectArrFn = (arr, obj = {}) => {
    this.props.changeselectArrFn(arr, obj)
  }
}

const mapState = (state) => ({
  listData: state.borrowBook.listData,
  userInfo: state.login.userInfo || (loStorage.getItem('userMes') && loStorage.getItem('userMes').userInfo) || null,
  selectArr: state.borrowBook.selectArr,
  selectObj: state.borrowBook.selectObj,
})

const mapDispatch = (dispatch) => ({
  changeselectArrFn(arr = [], obj = {}) {
    dispatch(actionCreators.changeselectArrFn(arr, obj))
  }
})

export default connect(mapState, mapDispatch)(Main);
