import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { Breadcrumb } from 'antd';
import { actionCreators } from './store';
import SearchForm from './components/searchForm.js';
import SetStopOrUn from './components/setStopOrUn.js';
import BagDetail from './components/bagDetail'
import MyList from './components/list.js';
import ErrorBag from 'components/errorBag.js'
import CirculationRecordList from 'components/circulationRecordList'
class Main extends Component {
    state = {
    };

    componentDidMount() {
    };

    render() {
        return (
            <Fragment>
                <div style={{ background: '#fff', padding: '16px' }}>
                    <Breadcrumb className="l-mb3">
                        <Breadcrumb.Item>资产管理</Breadcrumb.Item>
                        <Breadcrumb.Item>全部阅读包管理</Breadcrumb.Item>
                    </Breadcrumb>
                    <SearchForm />
                    <div className="l-mt4">
                        <SetStopOrUn />
                    </div>
                    <BagDetail />
                    <MyList />
                    <ErrorBag
                        searchFn={() => this.props.getTableListPre()}/>
                    <CirculationRecordList/>
                </div>
            </Fragment>);
    }
}

let mapStateToProps = (state) => ({
    // statistical: state.index.statistical
})
let mapDispatchToProps = (dispatch) => {
    return {
        getTableListPre() {
            dispatch(actionCreators.getTableListPre())
        }      
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Main);
