import * as actionTypes from './actionTypes';
import { loStorage } from 'libs/storage.js';
// import { setToken } from 'libs/handleCookies.js';

const defaultState = {
  username: '',
  password: '',
  remember: true,
  visibileChangeDefaultPwsModal: false,
  gradeAndClass: null,
  roomList: []
}

function setUserInfo(state, action) {
  loStorage.setItem('userMes', action.result);
  return {
    ...state,
    ...action.result
  }
}

export default (state = defaultState, action) => {
  switch (action.type) {
    case actionTypes.UPDATE_LOGIN_MESSAGE:
      return {
        ...state,
        ...action.values
      };
    case actionTypes.SET_USER_INFO:
      return setUserInfo(state, action);
    case actionTypes.VISIBILE_CHANGE_DEFAULT_PWS_MODAL:
      return {
        ...state,
        visibileChangeDefaultPwsModal: true
      };
    case actionTypes.HIDE_CHANGE_DEFAULT_PWS_MODAL:
      return {
        ...state,
        visibileChangeDefaultPwsModal: false
      };
    case actionTypes.SET_GRADE_AND_CLASS:
      return {
        ...state,
        gradeAndClass: action.data
      };
    //获取班级详细信息
    case actionTypes.SETCLASSROOMS :
      return {
        ...state,
        roomList: action.data
      }
    default:
      return {
        ...state
      }
  }
}
