import React, {
  Component,
  Fragment
} from 'react';
import { connect } from 'react-redux';
import {
  Button,
  Table, Alert, Icon, Message
} from 'antd';
import { actionCreators } from '../store';
import { loStorage } from 'libs/storage.js';


class Main extends Component {

  handleMoreItem = () => {
    let category = this.props.userInfo.eduInstitution.category;
    let list = [{
      title: '借阅包编号',
      dataIndex: 'bagNumber',
      render: text => <span>{text}</span>,
    }, {
      title: '借阅宝宝姓名',
      dataIndex: 'kiddieName',
    }, {
      title: '家长手机号',
      dataIndex: 'mobile',
    },
    {
      title: '年级',
      dataIndex: 'gradeName',
      render: text => text || '暂无'
    },
    {
      title: '班级',
      dataIndex: 'className',
      render: text => text || '暂无'
    }
      , {
      title: '书包借阅时间',
      dataIndex: 'startDate'
    }
    ]
    if (category === '2') {
      list.splice(3, 2, {
        title: '收货地址',
        dataIndex: 'address',
        render: text => text || '暂无'
      })
    }
    list.push({
      title: '操作',
      key: 'action',
      width: 250,
      render: (text, record) => {
        return (
          <Fragment>
            {record.borrowState === '1' && <Button className="l-mt1 l-mr2" type="primary" onClick={() => this.returnBags(record.qrCodeNumber)}>归还阅读包</Button>}
          </Fragment>
        )
      }

    })
    return list;
  }

  returnBags = (qrCodeNumber) => {
    this.props.visiblePutInModal(qrCodeNumber)
  }
  render() {
    const { selectQrcodeArr } = this.props;
    const {
      listData,
    } = this.props;
    let listColumns = this.handleMoreItem()

    const rowSelection = {
      selectedRowKeys: selectQrcodeArr,
      onSelect: (record, selected) => {
        let arr = JSON.parse(JSON.stringify(selectQrcodeArr))
        if (record.borrowState !== '1') return Message.info('未分配书包不可勾选');
        if (selected) {
          arr.push(record.qrCodeNumber)
        } else {
          arr.splice(arr.findIndex(item => item === record.qrCodeNumber), 1)
        }
        this.changeSelectQrcodeArrFn(arr)
      },
      onSelectAll: (selected, selectedRows, changeRow) => {
        let arr = JSON.parse(JSON.stringify(selectQrcodeArr))
        if (selected) {
          selectedRows.map(item => {
            if (item.borrowState === '1') return arr.push(item.qrCodeNumber)
            return null
          })
        } else {

          changeRow.map(item => arr.splice(arr.findIndex(ite => ite === item.qrCodeNumber), 1))

        }
        arr = [...new Set(arr)]
        this.changeSelectQrcodeArrFn(arr)
      }
    };

    const MessageText = () => {
      return (<div>
        <span className="l-mr2 l-hover"><Icon theme="filled" type="info-circle" /></span>
        已选择{selectQrcodeArr.length}个借阅包
        <span className="l-ml4 l-hover" onClick={() => { this.changeSelectQrcodeArrFn([]) }}>清空选择</span>
      </div>);
    }

    const pagination = {
      showSizeChanger: true,
      defaultPageSize: 30,
      pageSizeOptions: ['10', '20', '30', '50', '100', '150', '200']
    }

    return (<div className="l-mt2 l-mb2">
      {
        selectQrcodeArr.length > 0 &&
        <Alert className="l-mb2" message={MessageText()} type="info" />
      }
      <Table
        size='small'
        pagination={pagination}
        rowKey={r => r.qrCodeNumber}
        rowSelection={rowSelection}
        columns={listColumns}
        dataSource={listData}
        bordered={true}
      />
    </div>);
  }
  componentDidMount() {
    this.changeSelectQrcodeArrFn([])
  }
  changeSelectQrcodeArrFn = (arr) => {
    this.props.changeSelectQrcodeArrFn(arr)
  }
}

const mapState = (state) => ({
  listData: state.returnBook.listData,
  selectedRowKeys: state.returnBook.selectedRowKeys,
  userInfo: state.login.userInfo || (loStorage.getItem('userMes') && loStorage.getItem('userMes').userInfo) || null,
  selectQrcodeArr: state.returnBook.selectQrcodeArr
})

const mapDispatch = (dispatch) => ({
  visiblePutInModal(qrCodeNumber) {
    dispatch(actionCreators.visiblePutInModal(qrCodeNumber));
  },
  changeSelectQrcodeArrFn(arr) {
    dispatch(actionCreators.changeSelectQrcodeArrFn(arr))
  }
})

export default connect(mapState, mapDispatch)(Main);
