import { message } from 'antd';
import { ajaxAxios } from 'libs/io.js';
// import qs from 'qs';
// import store from 'store';

import * as actionTypes from './actionTypes';
let preParam = {}

export const getTableList = (param = {}) => {
  preParam = param
  return (dispatch) => {

    for (const key in param) {
      if (!param[key]) {
        param[key] = undefined
      }
    }
    ajaxAxios({
      url: '/member/pageKiddieVOByCnd',
      method: 'get',
      params: {
        currentPage: 1,
        pageSize: 9999,
        ...param,
        type: 2,
      }
    }).then((res) => {
      if (res.success) {
        dispatch(updateData(res.result.records));
      } else {
        res.logMessage && message.error(res.logMessage);
      };
    });
  }
}

export const getTableListPre = () => {
  return (dispatch) => {
    dispatch(getTableList(preParam))
    dispatch(getResidue())
  }
}

const updateData = (data) => {
  return {
    type: actionTypes.UPDATE_TABLE_LIST_DATA,
    data
  }
};
export const updateSearchFormData = (data) => {
  return {
    type: actionTypes.UPDATE_SEARCH_FORM_DATA,
    data
  }
};

export const getClass = () => {
  return (dispatch) => {
    ajaxAxios({
      url: '/member/findFamilyMemberVoForAdd'
    }).then((res) => {
      if (res.result) {
        dispatch(storeClassInfo(res.result));
      }
    });
  }
}
const storeClassInfo = (data) => ({
  type: actionTypes.STORE_CLASS_INFO,
  data
})

export const getBorrowList = (familyId) => {
  return (dispatch) => {
    ajaxAxios({
      url: '/member/getBookBagBorrowList',
      method: 'get',
      params: {
        kiddieId: familyId
      }
    }).then((res) => {
      if (res.success) {
        let result = res.result
        result.map(item => {
          item.key = Math.random()
          return item
        })
        dispatch(setBorrowListAction(result));
      } else {
        message.info(res.logMessage);
      }
    });
  }
}
export const setBorrowListAction = (val) => {
  return {
    type: actionTypes.SET_BORROW_LIST,
    val
  }
}
export const setBorrowListShowAction = (val) => {
  return {
    type: actionTypes.SET_BORROW_LIST_SHOW,
    val
  }
}

// 选中的
export const selectColumns = (selectedRowKeys, selectedRows) => {
  return {
    type: actionTypes.SELECT_COLUMNS,
    selectedRowKeys,
    selectedRows
  }
}
// 显示高级会员续费弹窗
export const visibleSeniorRenewalModal = (kiddieId) => {
  return {
    type: actionTypes.VISIBLE_SENIOR_RENEWAL_MODAL,
    kiddieId
  }
}

export const visibleSeniorModal = (data) => {
  return {
    type: actionTypes.VISIBLE_SENIOR_MODAL,
    data
  }
}

// 显示批量续费弹窗
export const visibleBatchRenew = (data) => {
  return {
    type: actionTypes.VISIBLE_BATCHRENEW,
    data
  }
}

//  查询馆剩余会员名额
export const getResidue = () => {
  return (dispatch) => {
    ajaxAxios({
      url: '/curator/memberOrder/getMemberResidue',
      method: 'get'
    }).then((res) => {
      const result = res.result
      dispatch(getResidueNum(result));
    });
  }
}

export const getResidueNum = (data) => {
  return {
    type: actionTypes.GETRESIDUE,
    data
  }
}
