import React,{Component,Fragment } from 'react';
import { Form, Icon, Input, Button, 
  // Checkbox,
  Row,Col,
  // message
 } from 'antd';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import Footer from 'components/footer.js';
import { actionCreators } from './store';

class Main extends Component {
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Fragment>
          <Row style={{paddingTop:'200px'}} type="flex" justify="center" align="middle">
            <Col span={8}>
              <Link to="/login"><Icon type="rollback" />返回登陆页面</Link>
              <div className="l-box-shadow1 l-pd3 l-bg-white l-radius1 l-mt2">
                <h2 className="l-align-center">找回密码</h2>
                <Form onSubmit={this.handleSubmit}>
                  <Form.Item>
                    {getFieldDecorator('username', {
                      rules: [{
                        required: true,
                        message: '请输入登陆账号!'
                      }],
                      initialValue:this.props.username
                    })(
                      <Input
                        prefix={<Icon type="user" className="l-pale" />}
                        placeholder="登陆账号"
                      />
                    )}
                  </Form.Item>
                  <Form.Item>
                    <Button type="primary" htmlType="submit" style={{width:'100%'}}>
                      下一步
                    </Button>
                  </Form.Item>
                </Form>
              </div>
            </Col>
        </Row>
        <div style={{position:'fixed',bottom:'0',width:'100%'}}>
          <Footer />
        </div>
      </Fragment>
    );
  }
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.props.onNext(values,this.props.history);
      }
    });
  }
}

const mapState = (state)=> ({
  username:state.forgetPassword.username
});
const mapDispatch = (dispatch)=> ({
  onNext(values,history) {
    dispatch(actionCreators.next(values));
    history.push(`/setNewPassword/${values.username}`);
  }
});

export default connect(mapState,mapDispatch)(Form.create()(Main));
