
const base1 = [
    {
        title: '宝宝姓名',
        dataIndex: 'kiddieName',
    }, {
        title: '家长手机号',
        dataIndex: 'mobile',
    },
    {
        title: '性别',
        dataIndex: 'sex',
        render: (text, record) => {
            const arr = ['暂无', '男', '女']
            return arr[text]
        }
    },
    {
        title: '阅读包',
        dataIndex: 'bagNumber',
        render: (text, record) => {
            return record.bagNumber || '暂无'
        }
    },
    {
        title: '借阅时间',
        dataIndex: 'borrowStartDate',
        render: (text, record) => {
            return text || '暂无'
        }
    }
]

const GradeAndClass = [
    {
        title: '年级',
        dataIndex: 'gradeName',
        render: (text, record) => {
            return record.gradeName || '暂无'
        }
    },
    {
        title: '班级',
        dataIndex: 'className',
        render: (text, record) => {
            return record.className || '暂无'
        }
    }
]

const address = [
    {
        title: '家庭地址',
        dataIndex: 'address',
        render: (text, record) => {
            return record.address || '暂无'
        }
    }
]
const base2 = [{
    title: '会员开始时间',
    dataIndex: 'shipCreateDate',
    render: (text, record) => {
        return text || '暂无'
    }
},
{
    title: '会员到期时间',
    dataIndex: 'shipExpireDate',
    render: (text, record) => {
        return text || '暂无'
    }
}, {
    title: '会员状态',
    dataIndex: 'memberShipStatusStr',
},
{
    title: '借阅状态',
    dataIndex: 'hasBagState',
    render: (text, record) => {
        const arr = ['', '已借阅', '可借阅', '异常', '不可借阅']// hasBagState 1 已借阅 2可借阅 3异常 4不可借阅
        return arr[text]
    }
}
]


export const listColumns1 = base1.concat(GradeAndClass).concat(base2)
export const listColumns2 = base1.concat(address).concat(base2)
