import {
  ajaxAxios
} from 'libs/io.js';
import { message } from 'antd';
import * as actionTypes from './actionTypes';
// import { loStorage } from 'libs/storage.js';
// import { actionCreators as actionCreatorsIndex } from '../../../index/index/store'

let params = {}
export const getTableList = (param = {}) => {
  params = param;
  return (dispatch) => {
    ajaxAxios({
      url: '/bookbag/managementInfo/list/page',
      data: {
        currentPage: 1,
        pageSize: 999,
        ...params
      }
    }).then((res) => {
      if (res.success) {
        dispatch(updateData(res.result.records));
      } else {
        res.logMessage && message.error(res.logMessage);
      }
    });
  }
}

const updateData = (data) => {
  return {
    type: actionTypes.UPDATE_TABLE_LIST_DATA,
    data
  }
};

export const getData = () => {
  return (dispatch) => {
    ajaxAxios({
      url: '/stats/eduFirstPage',
      method: 'get'
    }).then((res) => {
      dispatch(storeData(res.result));
    });
  }
};

const storeData = (data) => {
  return {
    type: actionTypes.STATISTICAL,
    data
  }
}

