// 列表点击单个停用 或未分配 按钮  obj = {bookagId, type}   type 4停用  0 未分配
export const stopOrUndistributedSingle = (obj) => {
  return (dispatch) => {
    // console.log('进入单个书包 停用或 未分配')
    ajaxAxios({
      url: '/bookbag/discontinue_bookbag',
      data: obj
    }).then((res) => {
      // console.log('单个书包 停用或 未分配接口返回', res.result)
      if (res.success) {
        dispatch(getTableList(params));
        setTimeout(() => {
          message.success('操作成功')
        }, 333);
      } else {
        res.logMessage && message.error(res.logMessage);
      }
    });
  }
}

// 隐藏批量停用或未分配 弹窗
export const hideStopModel = () => {
  return {
    type: actionTypes.HIDDEN_STOP_MODEL
  }
}
// 显示批量停用或未分配 弹窗
export const showStopModel = () => {
  return {
    type: actionTypes.SHOW_STOP_MODEL
  }
}

// 改变 批量停用或未分配  输入框的的值
export const chaneQrCodeNumberStop = (v) => {
  return {
    type: actionTypes.GET_QRCODE_NUMBER_STOP,
    value: v
  }
}



// 书包停用或未分配状态检测
export const changeStopQrCodeNumber = (obj) => {

  // 没有type 则 改变量停用或未分配 已扫描的 书包id列表
  if (obj.type === undefined) {
    return {
      type: actionTypes.CHANGE_STOP_QR_CODE_NUMBER_LIST,
      value: obj
    }
  }
  // 接口判断 
  return (dispatch) => {
    ajaxAxios({
      url: '/bookbag/discontinue_bookbag_check',
      method: 'get',
      params: {
        type: obj.type,
        qrCodeNumber: obj.value
      }
    }).then((res) => {
      if (res.success) {
        delete obj.type
        dispatch(changeStopQrCodeNumber(obj));
      } else {
        res.logMessage && message.error(res.logMessage);
      }
    });
  }
}

// 批量停用或未分配  obj = {bookagIds, type}   type 4停用  0 未分配
export const sureQrCodeNumber = (obj) => {
  return (dispatch) => {
    // console.log('进入批量设置书包设置为停用或 未分配')
    ajaxAxios({
      url: '/bookbag/batch_discontinue_bookbag',
      data: obj
    }).then((res) => {
      // console.log('批量设置书包设置为停用或者未非配接口返回', res.result)
      if (res.success) {
        dispatch(hideStopModel());
        dispatch(getTableList(params));
        dispatch(setSelectArr([], []));
        setTimeout(() => {
          message.success('操作成功')
        }, 333);
      } else {
        res.logMessage && message.error(res.logMessage);
      }
    });
  }
}


// 列表勾选的数据  
export const setSelectArr = (qrCodeNumberArr, objArr) => {
  return {
    type: actionTypes.CHANGE_SELECT_ARRAY,
    selectedRowKeysArr: qrCodeNumberArr,
    selectedRowsARR: objArr
  }
}

// 勾选了的qrcodeNumber处理后 数据带到 设置为停用或设置为未分配弹窗 初始值
export const setQrCodeNumber_stop_undistributed_list = (arr) => {
  return {
    type: actionTypes.SET_QRCODE_NUMBER_STOP_UNDISTRIBUTED_LIST,
    arr
  }
}

export const getTableListPre = () => {
  return (dispatch) => {
    dispatch(getTableList(params))
  }
}
