import { combineReducers } from 'redux';
import { reducer as loginReducer } from 'pages/login/login/store';
import { reducer as indexReducer } from 'pages/index/index/store';
import { reducer as forgetPasswordReducer } from 'pages/login/forgetPassword/store';
import { reducer as teacher } from 'pages/teacherManagement/teacher/store';
import { reducer as borrowBook } from 'pages/borrowManagement/borrowBook/store';
import { reducer as borrowHistory } from 'pages/borrowManagement/borrowHistory/store';
import { reducer as fillBook} from 'pages/borrowManagement/fillBook/store';
import { reducer as returnBook} from 'pages/borrowManagement/returnBook/store';
import { reducer as readBags } from 'pages/assetsManagement/readPackageManegement/store';
import { reducer as memberManagementReducer } from 'pages/memberManagement/seniorMember/store';
import { reducer as regularMember } from 'pages/memberManagement/regularMember/store';
import { reducer as bookManagement } from 'pages/assetsManagement/bookManagement/store';
import { reducer as allMember } from 'pages/memberManagement/allMember/store';
import { reducer as common } from 'components/store';

export default combineReducers({
  login:loginReducer,
  index:indexReducer,
  forgetPassword: forgetPasswordReducer,
  teacher,
  borrowBook,
  borrowHistory,
  fillBook,
  returnBook,
  seniorMember:memberManagementReducer,
  regularMember,
  readBags,
  bookManagement,
  allMember,
  common
});
