import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { Breadcrumb } from 'antd';
import { actionCreators } from './store';
import Search from './components/search.js'
import List from './components/list.js'
import ErrorBag from 'components/errorBag.js'

class Main extends Component {
    state = {
    };

    componentDidMount() {
    };

    render() {
        return (
            <Fragment>
                <div style={{ background: '#fff', padding: '16px' }}>
                    <Breadcrumb className="l-mb3">
                        <Breadcrumb.Item>借阅管理</Breadcrumb.Item>
                        <Breadcrumb.Item>补书</Breadcrumb.Item>
                    </Breadcrumb>
                    <Search />
                    <List />
                    <ErrorBag
                        searchFn={() => this.props.getTableListPre()}/>
                </div>
            </Fragment>);
    }
}

let mapStateToProps = (state) => ({
    // statistical: state.index.statistical
})
let mapDispatchToProps = (dispatch) => {
    return {
        getTableListPre() {
            dispatch(actionCreators.getTableListPre())
        }
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Main);
