import * as actionTypes from './actionTypes';

const defaultState = {
  listData: null,
  statistical: {},
  // viewBooksData: {},
  // visibileErrorModal: false,
  visibileViewBooksModal: false,
  // visibileFillModal: false,
  // isErrorModal: false,
  stopUndistributedVisible: false,
  qrCodeNumber_stop_undistributed: '',
  qrCodeNumber_stop_undistributed_list: [],
  selectedRowKeys: [],
  selectedRows: []
}

export default (state = defaultState, action) => {
  switch (action.type) {
    case actionTypes.UPDATE_TABLE_LIST_DATA: {
      return {
        ...state,
        listData: action.data
      }
    }
    case actionTypes.CHANGE_SELECT_ARRAY: {
      return {
        ...state,
        selectedRowKeys: action.selectedRowKeysArr,
        selectedRows: action.selectedRowsARR
      }
    }
    case actionTypes.STATISTICAL:
      return {
        ...state,
        statistical: action.data
      };
    // case actionTypes.VISIBILE_ERROR_MODAL:
    //   return {
    //     ...state,
    //     visibileErrorModal: true,
    //     viewBooksData: action.data,
    //     isErrorModal: action.flag
    //   }
    // case actionTypes.VISIBLE_VIEW_BOOKS_MODAL:
    //   return {
    //     ...state,
    //     visibileViewBooksModal: true,
    //     viewBooksData: action.data
    //   }
    // case actionTypes.CANCEL_ERROR_MODAL:
    //   return {
    //     ...state,
    //     visibileErrorModal: false
    //   }
    // case actionTypes.VISIBILE_FILL_MODAL:
    //   return {
    //     ...state,
    //     visibileFillModal: true
    //   }
    // case actionTypes.CANCEL_FILL_MODAL:
    //   return {
    //     ...state,
    //     visibileFillModal: false
    //   }
    // case actionTypes.UPDATE_BOOKS_DATA:
    //   return {
    //     ...state,
    //     viewBooksData: action.data
    //   }
    case actionTypes.HIDDEN_STOP_MODEL:
      return {
        ...state,
        stopUndistributedVisible: false,
        qrCodeNumber_stop_undistributed_list: []
      }
    case actionTypes.SHOW_STOP_MODEL:
      return {
        ...state,
        stopUndistributedVisible: true
      }
    case actionTypes.GET_QRCODE_NUMBER_STOP:
      return {
        ...state,
        qrCodeNumber_stop_undistributed: action.value
      }
    case actionTypes.CHANGE_STOP_QR_CODE_NUMBER_LIST:
      let { qrCodeNumber_stop_undistributed_list } = state;
      const obj = action.value;
      switch (obj.classify) {
        case 'add':
          qrCodeNumber_stop_undistributed_list.unshift(obj.value);
          qrCodeNumber_stop_undistributed_list = [...new Set(qrCodeNumber_stop_undistributed_list)];
          return {
            ...state,
            qrCodeNumber_stop_undistributed_list
          }
        default:
          const index = obj.value;
          qrCodeNumber_stop_undistributed_list.splice(index, 1)
          qrCodeNumber_stop_undistributed_list = [...new Set(qrCodeNumber_stop_undistributed_list)];
          return {
            ...state,
            qrCodeNumber_stop_undistributed_list
          }
      }
    case actionTypes.SET_QRCODE_NUMBER_STOP_UNDISTRIBUTED_LIST:
      return {
        ...state,
        qrCodeNumber_stop_undistributed_list: action.arr
      }

    default:
      return state;
  }
}
