import React,{ Component} from 'react';
import { Button,Modal } from 'antd';
import AddForm from './inForm.js';
import { connect } from 'react-redux';
import { actionCreators } from '../../../store';

class Main extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false
    }
  }
  render() {
    const { visible } = this.props;
    return(<span className="l-ml2">
      <Button  onClick={ this.showModal }>批量导入</Button>
      <Modal
          visible={visible}
          width="50%"
          title="批量导入"
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          footer={null}
          destroyOnClose
        >
          <AddForm />
        </Modal>
    </span>);
  }
  showModal = () => {
    this.props.setInform(true)
  }
  handleOk = () => {
    this.setState({ loading: true });
    setTimeout(() => {
      this.setState({ loading: false}); 
      this.props.setInform(false)
    }, 3000);
  }

  handleCancel = () => {
    this.props.setInform(false)
  }
}

let mapStateToProps = (state) => ({
  visible: state.regularMember.informVisible,
})

let mapDispatchToProps = (dispatch) => {
  return {
    setInform(flag) {
      dispatch(actionCreators.setInform(flag))
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Main)
