import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { Modal } from 'antd';
import { actionCreators } from '../store';
import ReturnSingleBagFrom from './returnSingleBagFrom.js';

class Main extends Component {

  render() {
    const {
      putInStorageVisible,
      putInStorageCancel,
    } = this.props;
    return (<Fragment>
      <Modal
        visible={putInStorageVisible}
        width="50%"
        title="归还书包"
        onCancel={putInStorageCancel}
        footer={null}
        destroyOnClose
      >
        <ReturnSingleBagFrom />
      </Modal>
    </Fragment>);
  }
}

const mapState = (state) => ({
  putInStorageVisible: state.returnBook.putInStorageVisible
})

const mapDispatch = (dispatch) => ({
  putInStorageCancel() {
    dispatch(actionCreators.putInStorageCancel());
    dispatch(actionCreators.clearInDatas());
  }
})

export default connect(mapState, mapDispatch)(Main);
